.articles {
  .news-tabs__card {
    position: relative;

    img {
      object-fit: cover;
      width: 100%;
    }

    @media (min-width: 992px) {
      &.__first { 
        height: 21.25rem;
        
        .title {
          margin: 0.625rem 0 0.5rem;
          font-size: 1.625rem;
          line-height: 1.95rem;
        }

        img {
          width: 35%;
        }
      }
    }

    .news-tabs__tags-item {
      color: $blue_2 !important;
    }
  }

  .news-tabs {
    margin-top: 4.875rem;
  
    @media(max-width: 568px) {
      margin-top: 2.5rem;
    }
  }

  .select-wrapper {
    width: 15.9375rem; 

    .choices__list {

      .choices__item {
        font-size: 1rem;
        font-family: 'Inter', sans-serif;
      }

      .choices__item[data-value="Сортировать по дате"] {
        color: $turquoise_2;
      }
    }

    @media(max-width: 991px) {
      margin-top: 0.875rem;
    }
  }

  .form-group {
    width: 15.9375rem;

    .form-control::placeholder {
      color: $turquoise_2;
    }
  }

  .articles-tabs__buttons {
    display: flex;
    margin-bottom: 0;
    justify-content: space-between;

    .news-tabs__button {
      height: 1.875rem;
      margin-bottom: 0.625rem;
      color: $turquoise;

      &.active {
        color: $white;
      }
    }

    @media(max-width: 991px) {
      flex-direction: column;
    }
  }
}

.articles-headphones {
  position: absolute;
  top: 1.25rem;
  right: 1.25rem;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  background: rgba(206, 234, 252, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    width: 0.9688rem !important;
    height: 0.9688rem !important;
  }
}

.articlesDetail-description__text {
  padding: 1.875rem;
  border-radius: 1rem;
  background: #FFFAD8;
  font-weight: 700;
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;
  color: $dark;
  margin-top: 2.5rem;

  @media(max-width: 480px) {
    padding: 1.25rem;
  }
}

.articlesDetail-top-description__img {
  max-width: 100%;
  margin-top: 2.5rem;
}

.articlesDetail-top-description__span-grey {
  font-family: "Inter", sans-serif;
  font-size: 0.813rem;
  line-height: 1rem;
  color: $turquoise;
  margin-top: 0.625rem;

  @media(max-width: 480px) {
    font-size: 0.6875rem;
    line-height: 0.8125rem;
  }
}

.articlesDetail-top-sidebar {
  margin-top: 2.5rem;
  padding-left: 1.125rem;
}

.articlesDetail-top-sidebar__link {
  font-family: "Inter", sans-serif;
  font-size: 0.813rem;
  line-height: 1rem;
  color: $blue_2;
  margin-top: 1.25rem;
  display: block;

  &:hover {
    cursor: pointer;
  }
}


.articlesDetail-top-sidebar__description-wrap {
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  position: relative;
  margin-top: 2.5rem;

  @media(min-width: 1400px) {
    max-width: 21.875rem;
  }

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: -0.625rem;
    right: -0.5625rem;
    background: url("./images/events/event-graph-background.png") center center no-repeat;
    background-size: cover;
    z-index: $lvl_-1;
    border-radius: 1.25rem;
  }

  .wrap {
    background: $card-bg;
    border-radius: 1.25rem;
    padding: 1.875rem 1.875rem 2.5rem ;
  }              
}

.articlesDetail-top-sidebar__description-item { 
  font-family: 'Inter', sans-serif;
  color: $dark;
  margin-top: 1.25rem;

  .subtitle {
    font-size: 0.6875rem;
    line-height: 0.8125rem;
    color: $turquoise;
  }

  .title {
    margin-top: 0.3125rem;
    font-weight: 700;
    font-size: 0.8125rem;
    line-height: 1rem;
    font-weight: 700;
  }

  .text {
    margin-top: 0.3125rem;
    font-size: 0.8125rem;
    line-height: 1rem;
  }
}

.articlesDetail{
  p, ul {
    font-size: 1rem;
    line-height: 1.6rem;
    font-family: "Inter", sans-serif;
    color: $dark; 
  }

  p {
    margin-top: 2.5rem;
  }

  ul {
    margin-top: 1.875rem;
  }

  .newsDetail-share {
    margin-top: 2.125rem;
  }

  @media(max-width: 1399px) {
    .video__box {
      width: 100%;
    }
  }
}

.articlesDetail-dropdown {
  margin-top: 2.5rem;

  .accordion-item {
    border-radius: 1.25rem;

    .accordion-button {
      padding: 1.125rem 1.0625rem 1.125rem 1.875rem;

      @media(max-width: 991px) {
        padding-left: 1.5625rem;
      }
    }

    .accordion-body {
      padding: 0 2.8125rem 2.5rem 1.875rem;

      @media(max-width: 991px) {
        padding-left: 1.5625rem;
      }
    }

    p, ul {
      color: $turquoise;
    }
  }

  .accordion-item:not(:first-child) {
    margin-top: 1.25rem;
  }
}

.articlesDetail-text-box {
  padding-left: 2.5rem;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    width: 0.3125rem;
    height: 100%;
    background: $blue_1;
    border-radius: 0.3125rem;
    left: 0;
  }

  @media(max-width: 991px) {
    padding-left: 1.875rem;
  }

  @media(max-width: 767px) {
    padding-left: 1.25rem;
  }
}

.articlesDetail-ul-box {
  li {
    color: $dark;

    &::marker {
      color: $turquoise;
    }

    &:not(:first-child) {
      margin-top: 1.25rem;
    }
  }
}

.articlesDetail-aside {
  margin-top: 0.625rem;
  margin-bottom: 0.875rem;
}
                
.articlesDetail-aside__item {
  margin-bottom: 1.25rem;

  span {
    font-size: 1rem;
    line-height: 1.6rem;
    font-family: "Inter", sans-serif;
    color: $dark;
    
    &:first-child {
      color: $turquoise;
      margin-right: 0.5rem;
    }
  }

  &:last-child {
    margin-left: 1.25rem;
  }
}

.articlesDetail-top-sidebar__audio {
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  position: relative;
  margin-top: 3.125rem;

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: -0.625rem;
    right: -0.5625rem;
    background: url("./images/events/event-graph-background.png") center center no-repeat;
    background-size: cover;
    z-index: $lvl_-1;
    border-radius: 1.25rem;
  }

  .wrap {
    background: $card-bg;
    border-radius: 1.25rem;
    padding: 1.875rem 1.875rem 2.5rem ;
  } 
  
  .title {
    font-family: 'Inter', sans-serif;
    color: $dark;
    font-weight: 700;
    font-size: 1.125rem;
    line-height: 1.35rem;
  }

  .newsDetail-share {
    margin-top: 1.625rem;
    flex-wrap: wrap;
    gap: 0.875rem;
  }

  .newsDetail-share__row {
    display: flex;
  }

  .newsDetail-share__hook {
    margin-right: 0;
  }

  .newsDetail-share__messanger {
    width: 2rem;
    height: 2rem;
  }

  @media(min-width: 1400px) {
    max-width: 21.875rem;
  }

  @media(max-width: 1199px){
    .wrap {
      padding: 1.875rem 1.5rem 2.5rem ;
    } 
  }

  @media(max-width: 991px) {
    .wrap {
      padding: 1.875rem 1.875rem 2.5rem ;
    } 
  }

  @media(max-width: 430px){
    .wrap {
      padding: 1.875rem 1.5rem 2.5rem ;
    } 
  }
}

.articlesDetail-list {
  h3 {
    @media(max-width: 1679px){
      margin-top: -1.875rem;
    }

    @media(max-width: 1399px){
      margin-top: -3.75rem;
    }

    @media(max-width: 1199px){
      margin-top: -13.75rem;
    }

    @media(max-width: 991px){
      margin-top: 3.125rem;
    }

    @media (max-width: 568px) {
    margin-top: 2.5rem;
    }
  }
}

