h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6, .display-1, .display-2, .display-3, .display-4, .display-5, .display-6 {
  font-weight: 800;
  font-family: $title-font;
  color: $dark;
}

p, span {
  color: $dark;
  font-family: $main-font;
}

h1, .h1, .display-1 {
  font-size: 3.3125rem;
  line-height: 3.3125rem;
  font-weight: 800;
  margin-bottom: 2.5rem;

  @media(max-width: 568px){
    margin-bottom: 1.25rem;
  }

  &.indent{
    @media(max-width: 568px){
      margin-bottom: 1.5625rem;
    }
  }

  @media(max-width: 991px) {
    font-size: 2.8125rem;
    line-height: 3.0938rem;
  }

  @media(max-width: 767px) {
    font-size: 1.875rem;
    line-height: 2.25rem;
  }
}

h2, .h2, .display-2 {
  font-size: 2.8125rem;
  line-height: 3.0938rem;
  font-weight: 800;
  margin-bottom: 2.5rem;
  margin-top: 3.75rem;


  &.indent{
    @media(max-width: 568px){
      margin-bottom: 1.5625rem;
    }
  }

  @media(max-width: 991px) {
    margin-bottom: 1.5625rem;
    margin-top: 3.125rem;
    font-size: 1.875rem;
    line-height: 2.25rem;
  }

  @media(max-width: 767px) {
    font-size: 1.625rem;
    line-height: 1.95rem;
  }

}

h2.extra {
  font-size: 4.0625rem;
  line-height: 4.0625rem;
  margin-bottom: 4.0625rem;

  @media(max-width: 991px){
    margin-bottom: 2.3125rem;
  }

  &.indent{
    @media(max-width: 991px){
      margin-bottom: 1.9375rem;
    }
  }

  @media(max-width: 991px) {
    font-weight: 400;
    font-size: 3.3125rem;
    line-height: 3.3125rem;
  }
  @media(max-width: 575px) {
    display: none;
  }
}

h3, .h3, .display-3 {
  font-size: 1.875rem;
  line-height: 2.25rem;
  font-weight: 800;
  margin-bottom: 1.5625rem;
  margin-top: 3.75rem;


  @media(max-width: 991px) {
    font-size: 1.625rem;
    line-height: 1.95rem;
    margin-bottom: 1.25rem;
    margin-top: 3.125rem;
  }

  @media(max-width: 767px) {
    font-size: 1.25rem;
    line-height: 1.5rem;
  }

  @media(max-width: 568px){
    margin-top: 2.5rem;
  }
}

h4, .h4, .display-4 {
  font-size: 1.625rem;
  line-height: 1.95rem;
  font-weight: 800;
  margin-bottom: 1.25rem;
  margin-top: 3.75rem;

  @media(max-width: 991px) {
    font-size: 1.25rem;
    line-height: 1.5rem;
    margin-top: 1.875rem;
  }

  @media(max-width: 767px) {
    font-size: 1.125rem;
    line-height: 1.3125rem;
  }

  @media(max-width: 568px){
    margin-top: 1.25rem;
  }
}

h5, .h5, .display-5 {
  font-weight: 800;
  font-size: 1.25rem;
  line-height: 1.5rem;
  margin-bottom: 1.25rem;
  margin-top: 1.875rem;
}

h6, .h6, .display-6 {
  font-size: 1.125rem;
  line-height: 1.35rem;
  font-weight: 800;
  margin-bottom: 0.625rem;
  margin-top: 1.875rem;
}

.text-bold {
  font-weight: 700;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

a {
  color: $dark;
  transition: all 0.3s ease;
  outline: none;
  text-decoration: none;
  font-size: 1rem;
  line-height: 1.6rem;
  font-family: $main-font;

  &.disabled {
    color: $turquoise;
  }

  &.fake {
    color: $blue_1;
    border-bottom: 0.0625rem solid transparent;

    &:hover {
      border-color: $blue_2;
    }
  }

  &:hover {
    color: $blue_2;
  }

  &.light {
    border-bottom: 0.0625rem solid transparent;
    padding-bottom: 0.1875rem;

    &.__hover {
      border-color: $white;
    }

    &:hover {
      border-color: $white;
      color: $white !important;
    }
  }
}

.bg-dark {
  a {
    color: #E9EFF2;

    &:hover {
      color: $blue_2;
      transition: all 0.3s ease;
    }
  }

  a.fake {
    border-bottom: 0.0625rem solid rgba(255, 255, 255, 0.5);
    transition: all 0.3s ease;

    &:hover {
      border-color: $blue_2;
    }
  }
}


p {
  font-weight: 400;
  font-size: 1rem;
  margin-bottom: 0.625rem;
}

p.lead, a.lead, span.lead {
  font-size: 1.125rem;
  line-height: 1.9775rem;
  margin-bottom: 0.625rem;
}

ul.individ-list {
  list-style: none;
  position: relative;
  padding-left: 0;

  li {
    font-size: 1.125rem;
    line-height: 2rem;
    color: $dark;
    font-weight: 400;
    position: relative;
    padding-left: 0.9375rem;
    font-family: $main-font;

    &::before {
      content: '';
      width: 0.3125rem;
      height: 0.3125rem;
      border-radius: 50%;
      background: $turquoise;
      position: absolute;
      left: 0;
      top: 0.6875rem;
    }
  }
}

ol.individ-list {
  list-style: none;
  padding: 0;
  counter-reset: list-numeric;

  li {
    color: $dark;
    font-family: $main-font;
    font-size: 1rem;
    line-height: 1.6rem;
    font-weight: 400;

    &::before {
      counter-increment: list-numeric;
      content: counter(list-numeric) '.';
      font-weight: 400;
      margin-right: .5rem;
      color: #B2B7C3;

    }
  }

  ol {
    margin-top: 1rem;
    padding-left: 1rem;
    counter-reset: list-numeric-inner;

    li {
      &::before {
        counter-increment: list-numeric-inner;
        content: counter(list-numeric) '.' counter(list-numeric-inner);
        color: #B2B7C3;
      }

      &::marker {
        color: transparent;
      }
    }
  }
}

ol.individ-list{
  li{
    ol{
      margin: 0.9375rem 0;
    }
  }
}

.back-to-top {
  display: inline-block;
  border-radius: 0.3125rem;
  background-color: #E9EFF2;
  padding: 3rem .5rem .25rem;
  font-size: 0.8125rem;
  color: $body-color;
  text-align: center;
  position: fixed;
  right: 5vw;
  bottom: 5vh;
  z-index: $lvl_-1;
  opacity: 0;
  transition: background-color $transition, opacity $transition;

  @include box-shadow;

  &::before {
    content: '';
    -webkit-mask-image: url("images/sprites/dropdown.svg");
    -webkit-mask-size: contain;
    width: 1rem;
    height: 1rem;
    background-color: $black;
    position: absolute;
    top: 1rem;
    left: 50%;
    transform: translateX(-50%) scale(-1);
    z-index: $lvl_1;
  }

  &:after {
    content: '';
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    position: absolute;
    top: .5rem;
    left: 50%;
    background-color: $white;
    transform: translateX(-50%) scale(-1);
  }

  &:hover {
    background-color: $blue_2;
    color: currentColor;
  }

  &.shown {
    z-index: $lvl_2;
    opacity: 1;
  }
}


.scrollspy-example {
  position: relative;
  height: 12.5rem;
  margin-top: .5rem;
  overflow: auto;
}

[id="modal"] .bd-example .btn,
[id="buttons"] .bd-example .btn,
[id="tooltips"] .bd-example .btn,
[id="popovers"] .bd-example .btn,
[id="dropdowns"] .bd-example .btn-group,
[id="dropdowns"] .bd-example .dropdown,
[id="dropdowns"] .bd-example .dropup,
[id="dropdowns"] .bd-example .dropend,
[id="dropdowns"] .bd-example .dropstart {
  margin: 0 1rem 1rem 0;
}

.bd-placeholder-img {
  font-size: 1.125rem;
  text-anchor: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

@media (min-width: 768px) {
  .bd-placeholder-img-lg {
    font-size: 3.5rem;
  }
}

.container, .container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
  padding-right: 0.9375rem;
  padding-left: 0.9375rem;
}

.btn{

}

.btn-primary {
  border: none;
  font-family: $main-font;
  border-radius: 2.5rem;
  color: $white;
  font-size: 1rem;
  line-height: 1.21rem;
  padding: 0.75rem 1.25rem;
  box-shadow: 0rem 0.25rem 0.9375rem rgba(0, 88, 161, 0.28);
  background-color: $blue_1 !important;
  transition: all 0.3s ease;

  &:hover {
    background-color: $blue_2 !important;
    color: $white;
  }

  &:focus {
    box-shadow: none !important;
  }

  &:active {
    color: $white !important;
    background-color: $indigo !important;
    box-shadow: none;
  }

  &.btn-sm {
    padding: 0.5rem 0.75rem;
  }

  &.btn-lg {
    padding: 0.875rem 1.875rem;
  }

  &:disabled,&.disabled{
    background-color: $light_1 !important;
    color: $turquoise_2 !important;
    border-color: $light_1 !important;
    box-shadow: none;
  }
}

.ghost-btn {
  font-family: $main-font;
  border-radius: 2.5rem;
  font-size: 1rem;
  line-height: 1.21rem;
  font-weight: 600;
  padding: 0.75rem 1.25rem;
  background-color: transparent !important;
  transition: all 0.3s ease;
  color: $blue_1 !important;
  border-color: $blue_1 !important;

  &:hover {
    border-color: $blue_2 !important;
    color: $blue_2 !important;
  }

  &:focus {
    box-shadow: none !important;
  }

  &:active {
    border-color: $indigo !important;
    box-shadow: none;
  }

  &.btn-sm {
    padding: 0.5rem 0.75rem !important;
  }

  &.btn-lg {
    padding: 0.875rem 1.875rem !important;
  }

  &:disabled,&.disabled{
    color: $light_1 !important;
    border-color: $light_1 !important;
    box-shadow: none;
  }
}

.hover-buttons {
  .btn-primary {
    background-color: $blue_2 !important;
  }
}

.ghost-hover-btn {
  .ghost-btn {
    color: $blue_2 !important;
    border-color: $blue_2 !important;
  }
}

.pressed-buttons {
  .btn-primary {
    background-color: $indigo !important;
  }

}

.ghost-pressed-btn {
