.location {
    font-family: 'Inter', sans-serif;
    color: $dark;
    display: flex;
    justify-content: flex-start;
    gap: 4.375rem;

    @media(max-width: 991px){
        flex-direction: column;
    }

    @media(max-width: 568px){
        gap: 3.125rem;
    }
}

.location__wrap {
    @media(min-width: 991px){
        width: 45%;
    }

    @media(min-width: 1400px){
        width: 41%;
    }
}

.location__tel {
    display: flex;
    justify-content: space-between;
    margin-top: 2.5rem;
    flex-wrap: wrap;

    .location__tel-item {
        display: flex;
        flex-direction: column;
    }
    
    .title { 
        font-family: 'Gilroy', sans-serif;
        font-weight: 800;
        font-size: 1.875rem;
        line-height: 2.25rem;
    }
    
    .subtitle {
        font-size: 0.8125rem;
        line-height: 1rem;
        margin-top: 0.3125rem;
    }

    @media(max-width: 1200px){
        flex-wrap: wrap;
    }

    @media(max-width: 568px){
        flex-direction: column;

        .location__tel-item {
    
            &:first-child {
                margin-bottom: 0.9375rem;
            }
        }
    }

    @media(max-width: 480px){
        margin-top: 1.25rem;

        .title {
            font-family: 'Inter', sans-serif;
            font-weight: 700;
            font-size: 1rem;
            line-height: 1.6rem;
        }

        .subtitle {
            font-size: 0.6875rem;
            line-height: 0.8125rem;
        }
    }
}

.location__email {
    margin-top: 1.25rem;

    .title {
        font-family: 'Gilroy', sans-serif;
        font-weight: 800;
        font-size: 1.875rem;
        line-height: 2.25rem;
        color: $blue_1;

        &:hover {
            color: $blue_2;
        }
    }

    @media(max-width: 480px){
        margin-top: 0.9375rem;

        .title {
            font-family: 'Inter', sans-serif;
            font-weight: 700;
            font-size: 1rem;
            line-height: 1.6rem;
        }
    }
}

.location__desc {
    margin-top: 2.5rem;

    .title {
        font-weight: 700;
        font-size: 1.125rem;
        line-height: 1.35rem;
    }
    .text {
        margin-top: 0.625rem;
        font-size: 1rem;
        line-height: 1.6rem;
        display: flex;
        align-items: center;

        img {
            margin-right: 0.625rem;
            width: 1.5rem;
            height: 1.5rem;
            fill: black;
            stroke: black;
        }
    }

    .link {
        margin-top: 0.625rem;
    }

    @media(max-width: 480px){
        margin-top: 1.875rem;

        .title {
            font-size: 1rem;
            line-height: 1.6rem;
        }

        .text {
            font-size: 0.8125rem;
            line-height: 1rem;
        }

        .link {
            p {
                font-size: 0.8125rem;
                line-height: 1rem;
            }
        }
    }
}

.location__img {
    filter: drop-shadow(0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1));
    position: relative;

    &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        bottom: -0.625rem;
        right: -0.5625rem;
        background: url("./images/events/event-graph-background.png") center center no-repeat;
        background-size: cover;
        z-index: $lvl_-1;
        border-radius: 1.25rem;
      }
    
    img {
        object-fit: cover;
        border-radius: 1.25rem;
        width: 100%;
        height: 100%;
    }

    @media(min-width: 1400px){
        margin-left: 10%;
    }
}

.location__map { 
    height: 25rem;
    margin-top: 4.375rem;
    margin-bottom: -50px;

    @media(max-width: 568px){
        margin-top: 3.125rem;
    }

    @media(min-width: 576px){
        margin-bottom: -2.5rem;
    }

    @media(min-width: 768px){
        margin-bottom: -3.75rem;
    }

    @media(min-width: 992px){
        margin-bottom: -4.375rem;
    }

    .map__textContent {
        h4 {
          margin-bottom: 0.625rem;

            @media(max-width: 568px) {
                font-size: 1rem;
                line-height: 1.6rem;
            }
        }
    }

    .map__balloon-text {
        font-size: 0.8125rem;
        line-height: 1rem;
        color: $dark;
        font-family: 'Inter', sans-serif;
        margin: 5px 0 0;
        display: block;

        &.link {
            &:hover {
                color: $blue_2;
                cursor: pointer;
            }
        }

        &:last-child {
            font-weight: 700;
            margin-top: 0.9375rem;
        }

        &:not(:last-child) {
            @media(max-width: 568px) {
                font-size: 0.6875rem;
                line-height: 0.8125rem;
                
            }
        }

        
    }

    .ymaps-2-1-79-balloon__layout {
        left: 6.25rem;
        max-width: 21.875rem;
        width: 100%;
        box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.1);

        @media(max-width: 568px) {
            padding: 1.25rem;
            left: -1rem;
            top: -3rem;
        }
    }

    .ymaps-2-1-79-balloon__tail {
        display: none;
    }
}
