.profile {
    font-family: 'Inter', sans-serif;
    color: $dark;
    display: flex;
    gap: 4.375rem;
    margin-top: 2.5rem;

    a {
        color: $blue_1;

        &:hover {
            cursor: pointer;
            color: $blue_2;
        }
    }

    .form-group {
        margin-bottom: 1.25rem;
    }

    @media(max-width: 991px){
        flex-direction: column;
        gap: 3.125rem;
    }
}

.profile__form {

    button {
        margin-top: 0.625rem;
    }

    input:-webkit-autofill,
    input:-webkit-autofill:hover,
    input:-webkit-autofill:focus,
    input:-webkit-autofill:active {
        font-size: 13px;
    }
}

.profile__subtitle {
    font-family: 'Gilroy', sans-serif;
    font-weight: 800;
    font-size: 1.875rem;
    line-height: 2.25rem;
    margin: 3.375rem 0 1.5625rem;

    @media (max-width: 768px) {
        margin: 1.5rem 0 1.25rem;
    }
}

.profile-desc-form {
    font-size: 1rem;
    line-height: 1.6rem;

    button {
        margin-top: 1.375rem;
        font-size: 0.8125rem;
        line-height: 1rem;
    }
}

.profile-desc__subtitle {
    margin: 0 0 1.25rem;
    font-family: 'Gilroy', sans-serif;
    font-weight: 800;
    font-size: 1.625rem;
    line-height: 1.95rem;
}

.profile-desc__text {
    margin-bottom: 0.875rem;
}


.profile-filter-label {
    display: block;
    margin-bottom: 0.5rem;
    position: relative;
  
    span {
      margin-left: 0.3125rem;
    }
}
