.basket-table { 
    &__row {
        display: grid;
        grid-template-columns: 75px 28% repeat(4, 1fr);
        grid-template-areas: 'img info price counter sum trash';
        gap: 25px;
        padding: 20px 0 30px;
        border-bottom: 1px solid $cyan;

        @media (max-width: 1200px) { 
            grid-template-columns: 80px 150px 25px;
            grid-template-areas: 
                'img info info'
                '. price . '
                '. counter . '
                '. sum trash ';
            gap: 0;
        }

        @media (max-width: 767px) {
            justify-content: center;
        }

        &--head {
            grid-template-areas: '. info price counter sum trash';
            color: $turquoise;
            font-weight: 700;
            font-size: 16px;

            @media (max-width: 1200px) {
                display: none;
            }
        }
    }

    &__col {
        &-text {
            font-family: 'Inter', sans-serif;
            font-size: 16px;
            font-weight: 700;
        }

        &-code {
            font-family: 'Inter', sans-serif;
            font-size: 13px;
            font-weight: 700;
            margin-top: 8px;
            display: inline-block;

            @media (max-width: 575px) {
                font-size: 16px;
            }
        }

        &--img {
            grid-area: img;
            display: flex;
            align-items: center;

            img {
                object-fit: contain;
                width: 60px;
            }
        }

        &--info {
            grid-area: info;
        }

        &--price {
            grid-area: price;

        }

        &--counter {
            grid-area: counter;

            @media (max-width: 1200px) {
                margin: 10px 0;
            }  
        }

        &--sum {
            grid-area: sum;
        }

        &--trash {
            grid-area: trash;
        }
    }

    &__info {
        > * {
            &:not(:last-child) {

            }
        }

        &-title {
            font-size: 11px;
            line-height: 13px;
            margin: 11px 0 8px;
        }

        &-descr {
            font-size: 10px;
            line-height: 12px;
            color: $turquoise;

            @media (max-width: 575px) {
                width: 110px;  
                font-size: 11px;      
            }
        }
    }

    &__labels {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: -5px;
        padding-left: 0;
        list-style: none;

        > * {
            margin-bottom: 5px;
            
            &:not(:last-child) {
                margin-right: 4px;
            }
        }

        &-item {
            padding: 2.5px 7.5px; 
            border-radius: 30px;
            color: #fff;
            font-size: 11px;
            line-height: 13px;
            font-weight: 600;

            &--red {
                background-color: $red;
            }

            &--purple {
                background-color: $purple;
            }
        }
    }

    &__status {
        font-size: 11px;
        color: $green;
        margin-top: 5px;

        @media (max-width: 575px) {
            margin-top: 10px;
        }
    }

    &__price {
        @media (max-width: 1200px) {
            display: flex;
        }

        @media (max-width: 992px) {

            > * {
                &:not(:last-child) {
                    margin-right: 10px;
                }
            }
        }

        &-current {
            font-family: 'Inter', sans-serif;
            font-weight: 700;
            font-size: 16px; 
        }

        &-title {
            font-family: 'Inter', sans-serif;
            color: #8A92A6;
            font-weight: 700;
            font-size: 16px;
            margin: 10px 0;

            @media (max-width: 575px) {
                font-size: 13px;     
            }
        }

        &-old {
            font-size: 13px;
            text-decoration: line-through;
            color: $cyan;

            @media (max-width: 1199px) {
                margin-left: 15px;    
            }
        }
    }

    &__trash-btn {
        display: flex;
        position: relative;
        align-items: center;
        color: $blue_1;
        border: none;
        background-color: transparent;
        transition: color 0.3s;

        svg {
            width: 20px;
            height: 20px;
            margin-right: 5px;
            flex-shrink: 0;
        }

        .question-popup {
            min-width: 140px;
            left: -60px;
        }

        &:hover {
            .question-popup {
              display: flex;
            }
          }
    }
}