.event-list {
  margin-bottom: 4.375rem;

  .individ-list {
    margin-top: 1.25rem;

    li {
      &:not(:last-child) {
        margin-bottom: 1.25rem;
      }
    }

    @media(max-width: 991px) {
      padding-left: 0;
    }
  }

  @media(max-width: 991px) {
    margin-bottom: 3.125rem;
  }
}

.event-list__description {
  font-size: 1.125rem;
  line-height: 1.975rem;
  font-weight: 400;
  font-family: 'Inter', sans-serif;
  color: $dark;
  display: inline-block;

}

.event-list__image-box {
  border: 0.0625rem solid $cyan;
  border-radius: 1.25rem;
  filter: drop-shadow(0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1));
  position: relative;

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: -0.625rem;
    right: -0.5625rem;
    background: url("./images/events/event-graph-background.png") center center no-repeat;
    background-size: cover;
    z-index: $lvl_-1;
  }

  @media(max-width: 991px) {
    margin-top: 2.5rem;
  }
}

.event-list__image {
  max-width: 100%;
  height: auto;
  border-radius: 1.25rem;
}

.event-list__image-description {
  color: $turquoise;
  font-size: 0.8125rem;
  line-height: 0.9812rem;
  display: inline-block;
  margin-top: 1.5625rem;
}

.event-tabs {
  margin-bottom: 6.25rem;
}

.event-tabs__buttons {
  margin-bottom: 2.5rem;
  display: flex;
}

.event-tabs__button {
  color: $dark;
  font-size: 0.8125rem;
  line-height: 0.9812rem;
  font-weight: 400;
  font-family: "Inter", sans-serif;
  padding: 0.4375rem 1.5625rem;
  border-radius: 1.25rem;
  background-color: #EFF3F6;
  width: max-content;
  transition: all 0.3s ease;
  border: none;

  &:not(:last-child) {
    margin-right: 0.625rem;
  }

  &.active {
    background-color: $blue_1;
    color: white;

    &:hover {
      background-color: $blue_2;
    }
  }

  @media(max-width: 991px) {
    margin-bottom: 0.625rem;
  }
}

.event-tabs__cards {
  &.active {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 1.875rem;
    grid-row-gap: 3.125rem;
  }

  @media(max-width: 1679px){
    grid-template-columns: repeat(3, 1fr)!important;
  }
  @media(max-width: 1199px){
    grid-template-columns: repeat(2, 1fr)!important;
  }
  @media(max-width: 991px){
    grid-template-columns: repeat(1, 1fr)!important;
  }

}

.event-tabs__card {
  padding: 1.875rem;
  border-radius: 1.25rem;
  background: $card-bg;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;

  &._closed {

    .event-tabs__progress {
      &::-webkit-progress-value {
        background-color: $red;
      }
    }

    .event-tabs__current {
      color: $red;
    }

    .event-tabs__progressbar {
      .btn {
        visibility: hidden;
      }
    }
  }

  &:not(:last-child) {
    margin-bottom: 1.25rem;
  }

  .h6 {
    display: block;
    margin-top: 0.625rem;

    @media(max-width: 768px) {
      font-size: 1rem;
      font-weight: 700;
      line-height: 1.2rem;
    }
  }

  &:hover { 
    box-shadow: 0.425rem 0.425rem 0.9rem rgba(0, 0, 0, 0.5);
    cursor: pointer;
    transition: all 0.3s ease;
    .h6 {
      color: $blue_2;
    }
  }
}

.event-tabs__tags {
  margin-bottom: 1.25rem;
}

.event-tabs__tags-item {
  padding: 0.125rem 0.625rem;
  border-radius: 0.375rem;
  font-size: 0.6875rem;
  line-height: 0.8313rem;
  font-family: 'Inter', sans-serif;
  color: $white;

  &._green {
    background-color: #41C79E;
    color: $white !important;
  }

  &.__violet {
    background-color: #8C41C7;
  }

  &._blue {
    background-color: #1B73BB;
  }
}

.event-tabs__date {
  font-size: 1rem;
  line-height: 1.6rem;
  font-family: 'Inter', sans-serif;
  margin-bottom: 0.625rem;
  display: flex;
  align-items: center;

  svg {
    width: 1.3125rem;
    height: 1.3125rem;
    fill: $turquoise;
    display: inline-block;
    margin-right: 0.375rem;
  }
}

.event-tabs__date-text {
  color: $turquoise;
}

.event-tabs__date-time{
  font-size: 1rem;
  line-height: 1.6rem;
  color: $turquoise;
  display: inline-block;
  margin-left: 0.3125rem;
}

.event-tabs__hash {
  margin-top: 0.625rem;
  margin-bottom: 1.25rem;
  overflow: hidden; 
  text-overflow: ellipsis;
  display: -moz-box;
  display: -webkit-box;
  line-clamp: 2;
  box-orient: vertical;
  -moz-box-orient: vertical;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-height: 1rem;

  .fake {
    display: inline-block;
    font-size: 0.8125rem;
    line-height: 0.9812rem;

    &:not(:last-child) {
      margin-right: 0.625rem;
    }
  }

  @media(max-width: 1680px){
    margin-bottom: 3.1875rem;
  }
  @media(max-width: 991px){
    margin-bottom: 2.75rem;
  }
}

.event-tabs__progressbar {
  .btn {
    width: max-content;
    margin-top: 1.5625rem;
    padding: 0.5rem 0.9375rem;
    border: 1px solid transparent;
  }
}

.event-tabs__progress {
  width: 100%;
  height: 0.5rem;
  border-radius: 0.3125rem;

  &::-webkit-progress-bar {
    background-color: $cyan;
    border-radius: 0.3125rem;
  }

  &::-webkit-progress-value {
    background-color: $blue_1;
    border-radius: 0.3125rem;
  }
}

.event-tabs__progressInfo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 0.3125rem;
}

.event-tabs__current {
  font-size: 1.125rem;
  line-height: 1.35rem;
  font-weight: 700;
  color: $blue_1;
}

.event-tabs__texts {
  font-size: 0.8125rem;
  line-height: 0.9812rem;
  color: #868DA1;
} 

.eventsDetail__card-box {
  margin-top: 2.5rem;

  @media (min-width: 992px) {
    position: relative;
    width: 100%;
      
    &:after {
      content: '';
      position: absolute;
      background-image: url("images/catalog/catalog-info-pattern.svg");
      border-radius: 1.25rem;
      width: 100%;
      height: 100%;
      z-index: $lvl_-1;
      bottom: -0.9375rem;
      right: -0.9375rem;

      @media (min-width: 1680px) {
        background-size: 110%;
      }
    }
  }
}

.eventsDetail__card {
  border-radius: 1.25rem;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  background: $card-bg;
  overflow: hidden;
  position: relative;

  .event-tabs__current {
    font-size: 1.625rem;
    font-weight: 800;
  }
  
  .event-tabs__texts {
    font-size: 1rem;
  } 
}

.eventsDetail__card-col {
  padding: 2.5rem 8.4375rem 3.125rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .event-tabs__progressbar {
    margin-top: 3.125rem;
  }

  @media(max-width: 1400px) {
    padding: 2.5rem 4.0625rem 3.125rem;
  }

  @media(max-width: 991px) {
    padding: 1.875rem 1.875rem 2.5rem;

    .event-tabs__progressbar {
      margin-top: 1.875rem;
    }
  }
}

.eventsDetail__card-row {
  display: flex;
  justify-content: space-between;

  .btn {
    height: fit-content;
    margin-left: 2.5rem;
    font-size: 1.125rem;
    font-weight: 500;
  }
}

.eventsDetail__card-subrow {
  display: flex;
  width: 39.375rem;
  justify-content: space-between;

  @media(max-width: 767px){
    flex-direction: column;
  }
}

.eventsDetail__card-subrow_item {
  .text {
    color: $turquoise;
    font-size: 1rem;
    line-height: 1.6rem;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0.3125rem;

    span {
      color: $dark;
      margin-left: 0.625rem;
    }

    @media(max-width: 767px){
      font-size: 0.8125rem;
      line-height: 1rem;
    }
  }
  .title {
    margin-top: 0.625rem;
    font-family: 'Gilroy', sans-serif;
    font-weight: 800;
    font-size: 1.625rem;
    line-height: 1.95rem;
    color: $dark;

    @media(max-width: 767px){
      margin-top: 0.3125rem;
      font-weight: 700;
      font-size: 1rem;
      line-height: 1.6rem;
    }
  } 

  @media(max-width: 767px){
    &:last-child {
      margin-top: 1.875rem;
    }
  }
}

.eventsDetail-top-section {
  display: flex;
  margin-top: 4.375rem;
  font-family: 'Inter', sans-serif;

  @media(max-width: 991px) {
   flex-direction: column;
  }

  p {
    font-size: 1.125rem;
    line-height: 1.9688rem;
  }
}

.eventsDetail-top-section__sidebar-box{
  display: flex;
  flex-direction: column;
  margin-left: 1.875rem;
  max-width: 21.875rem;

  @media(max-width: 1199px) {
    margin-left: 0;
  }
}

.eventsDetail-top-section__sidebar { 
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  position: relative;

  .wrap {
    background: $card-bg;
    border-radius: 1.25rem;
    padding: 1.875rem;

    @media(max-width: 380px) {
      padding: 1.5625rem;
    }
  }
  
  &:last-child {
    margin-top: 3.125rem;

    img {
      max-width: 15rem;
      max-height: 20.9375rem;
      margin: 1.25rem 0 0.625rem;

      @media(max-width: 380px) {
        max-width: 10.9375rem;
        max-height: 15.25rem;
      }
    }
  }

  .text {
    font-size: 0.8125rem;
    line-height: 1rem;
    color: $turquoise;
    margin-top: 0.3125rem;
    margin-bottom: 0.9375rem;
  }

  @media(max-width: 991px) {
    width: 100%;
    margin-top: 2.5rem;

    &:last-child {
      margin-top: 1.875rem;
    }
   }
}

.eventsDetail-top-section__sidebar-title {
  font-family: "Gilroy", sans-serif;
  font-weight: 800;
  font-size: 1.625rem;
  line-height: 1.95rem;

  @media(max-width: 380px) {
    font-weight: 700;
    font-size: 1.125rem;
    line-height: 1.35rem;
   }
}

.eventsDetail-top-section__sidebar-row {
  display: flex;
  gap: 0.9375rem;
  margin-top: 0.9375rem;

  img {
    width: 4.0625rem;
    height: 4.0625rem;
  }

  .subtitle {
    font-weight: 700;
    font-size: 1rem;
    line-height: 1.6rem;
    color: $dark;
  }

  a {
    font-size: 0.8125rem;
    line-height: 1rem;
    color: $blue_1;
    display: block;
    margin-top: 0.3125rem;

    &:hover {
      color: $blue_2;
      cursor: pointer;
    }
  }
}

.eventsDetail-ul-box {
  margin-top: 4.375rem;

  li {
    margin-top: 1.25rem;
    font-size: 1rem;
    line-height: 1.6rem;
    font-family: 'Inter', sans-serif;
    color: $dark;
    
    &::marker {
      color: $turquoise;
    }

    @media(max-width: 380px) {
      margin-top: 0.625rem;
    }
  }
}

.eventsDetail-dropdown {
  margin-top: 0.5rem;

  .accordion-item {
    border-radius: 1.25rem;

    .accordion-button { 
      padding: 1.125rem 1.0625rem 1.125rem 1.875rem;

      @media(max-width: 991px) {
        padding-left: 1.5625rem;
      }
    }

    .accordion-button:not(.collapsed)::after {
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23212529'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e") !important;
    }

    .accordion-button::after {
      background-position: 50% 50%;
      background-size: 0.75rem !important;
    }

    .accordion-body {
      margin-top: 1.375rem;
      padding: 0 2.8125rem 2.5rem 1.875rem;

      @media(max-width: 991px) {
        margin-top: 0.4375rem;
        padding-left: 1.5625rem;
      }
    }

    .accordion-body__row {
      padding-top: 0.625rem;
      padding-bottom: 0.9375rem;
      border-top: 0.0625rem solid #D8E0E4;
      display: flex;
      gap: 0.625rem;

      .accordion-body__row-item {
        font-family: 'Inter', sans-serif;
        font-weight: 700;
        font-size: 1rem;
        line-height: 1.6rem;

        &:last-child {
          font-weight: 400;
        }

        &:first-child {
          min-width: 7.5rem;
        }
      }
    }
  }

  .accordion-item:not(:first-child) {
    margin-top: 1.25rem;
  }

  @media(max-width: 991px) {
    margin-top: 2.5rem;

    .accordion-item {

      .accordion-body {
        padding: 0 1.5625rem 1.25rem;
      }

      .accordion-body__row {
        padding-bottom: 0.625rem;

        .accordion-body__row-item {
          font-size: 0.8125rem;
          line-height: 1rem;
          
          &:first-child {
            min-width: 5.3125rem;
          }
        }
      } 
    }
  }
}








.eventsDetail-form__background {
  background: url('./images/calc_page/calc-result-background.png') no-repeat;
  background-size: cover;
  margin-bottom: -4.375rem;
  padding-top: 3.4375rem;

  @media(max-width: 991px){
    margin-bottom: -3.75rem;
  }

  @media (max-width: 767px) {
    margin-bottom: -2.5rem;
  }

  @media(max-width: 575px) {
    margin-bottom: -3.125rem;
  }

  @media(max-width: 380px) {
    background: url('./images/eventsDetail/projectsDetail-form__background.png') no-repeat;
    background-position: -7.5rem -1.875rem;
    background-size: auto calc(100% + 1.875rem);
    padding-top: 4.75rem;
  }
}

.eventsDetail-form {
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  padding: 3.125rem 3.125rem 3.75rem 8.4375rem;
  background-color: $white;

  .eventsDetail-form__aside-text {
    margin-top: 1.25rem;
  }

  textarea {
    border-radius: 1.25rem !important;
    min-height: 5.375rem !important;
    padding: 0.9375rem 1.25rem !important;
  }

  @media (max-width: 1400px) {
    padding-left: 3.125rem;
  }

  @media(max-width: 767px) {
    padding: 1.875rem 1.25rem 3.375rem;
  }

  @media (min-width: 1200px) {
    textarea {
      width: 48.4375rem;
    }
  }

  h2 {
    margin: 0;
  }
}

.eventsDetail-form__row {
  display: flex;
  margin: 2.5rem 0 1.25rem;
  gap: 1.875rem;

  @media(max-width: 991px) {
    flex-direction: column;
  }

  @media(max-width: 767px) {
    margin-top: 1.875rem;
  }
}

.eventsDetail-form__row-item {

  @media(min-width: 992px) {
    width: 23.4375rem;
  }
}

.eventsDetail-form__text {
  font-family: "Inter", sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;
  margin-top: 2.5rem;

  @media(max-width: 767px) {
    margin-top: 0.9375rem;
  }
}

.eventsDetail-form__input-title {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.6rem;
  margin-bottom: 0.188rem;
}

.eventsDetail-form__aside-text {
  display: flex;
  margin-top: 1.5rem;
  font-size: 0.8125rem;

  @media(max-width: 767px) {
    margin-top: 1rem;
  }
}

.eventsDetail__button-submit {
  margin-top: 1.875rem;

  @media(max-width: 767px) {
    margin-top: 1.25rem;
  }
}
