.techSupport__wrap {
  margin-top: 2.5rem;
  display: flex;
  justify-content: space-between;

  @media(max-width: 991px) {
    flex-direction: column;
  }

  @media(max-width: 568px) {
    margin-top: 1.25rem;
  }
}

.techSupport__card {
  padding: 1.875rem 2.8125rem 2.8125rem 1.875rem;
  background: $card-bg;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  position: relative;
  height: fit-content;
  width: 40.5%;
  &:after {
      content: '';
      position: absolute;
      background-image: url("images/catalog/catalog-info-pattern.svg");
      border-radius: 1.25rem;
      width: 100%;
      height: 100%;
      z-index: $lvl_-1;
      bottom: -0.9375rem;
      right: -0.9375rem;

      @media (max-width: 500px) {
          bottom: -0.8rem;
          right: -0.4rem;
          background-size: cover;
          background-image: url("images/catalog/catalog-info-pattern-mobile.svg");
      }
  }

  a {
    font-family: 'Gilroy', sans-serif;
    font-weight: 800;
    font-size: 1.625rem;
    line-height: 1.95rem;
    color: $dark;
    margin-bottom: 1.25rem;
    display: block;

    @media(max-width: 568px) {
      font-weight: 700;
      font-size: 1rem;
      line-height: 1.6rem;
      margin-bottom: 0.9375rem;
    }
  }
 
  .text {
    font-family: "Inter", sans-serif;
    font-size: 1rem;
    line-height: 1.6rem;
    color: $dark;

    span {
      font-weight: 700;
    }

    @media(max-width: 568px) {
      font-size: 0.8125rem;
      line-height: 1rem;
    }
  }

  .techSupport__button-submit {
    margin-top: 1.875rem;

    @media(max-width: 568px) {
      margin-top: 1.25rem;
    }
  }

  @media(max-width: 991px) {
    width: 100%;
  }

  @media(max-width: 568px) {
    padding: 1.5625rem 1.75rem 2rem 1.5625rem;
  }
}

.techSupport__info {
  width: 46%;

  .link {
    font-family: 'Gilroy', sans-serif;
    font-weight: 800;
    font-size: 1.25rem;
    line-height: 1.5rem;
    color: $blue_1;

    @media(max-width: 568px) {
      font-size: 1rem;
      line-height: 1.6rem;
    }
  }

  .text {
    font-family: "Inter", sans-serif;
    font-size: 1rem;
    line-height: 1.6rem;
    color: $dark;
    margin: 1.25rem 0 2.5rem;

    @media(max-width: 568px) {
      margin: 0.625rem 0 1.25rem;
    }
  }

  @media(max-width: 991px) {
    width: 100%;
    margin-top: 3.125rem;
  }
}