.categories-common {
    &__title-link {
        font-size: 20px;
        font-weight: 800;
        color: $blue_1;

        &:hover {
            text-decoration: underline;
            text-underline-offset: 0.375rem;
            text-decoration-color:  $blue_2;
            transition: all 0.3s ease;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 10px 50px;
        list-style: none;
        padding-left: 0;

        @media (max-width: 767px) {
            grid-template-columns: repeat(2, 1fr);
        }

        @media (max-width: 500px) {
            grid-template-columns: 1fr;
        }
    }

    &__list-item {
        sup {
            margin-left: 2px;
            color: $turquoise;
            font-size: 11px;
        }
    }

    &__list-link {
        font-size: 16px;
        color: $blue_1;

        &:hover {
            text-decoration: underline;
            text-underline-offset: 0.375rem;
            text-decoration-color:  $blue_2;
            transition: all 0.3s ease;
        }
    }

    &__banner {
        padding: 31px 35px 34px 112px;
        background: url('images/categories-common/bg.jpg') no-repeat 50% / cover;
        border-radius: 15px;

        @media (min-width: 1440px) {
            margin-left: 100px;
        }

        @media (max-width: 992px) {
            padding: 25px;
        }

        @media (max-width: 500px) {
            background-position-x: 66%;
        }

        p, a {
            color: #fff;
        }

        &-title {
            color: #fff;
            font-size: 30px;
            font-weight: 800;

            @media (max-width: 500px) {
                font-size: 20px;
            }
        }

        &-wrap {
            display: flex;
            flex-wrap: wrap;
            margin-bottom: -5px;

            @media (max-width: 375px) {
                flex-direction: column;
            }

            > * {
                margin-bottom: 5px;

                @media (max-width: 375px) {
                    display: flex;
                    flex-direction: column;
                }

                &:not(:last-child) {
                    margin-right: 5px;
                }
            }
        }
    }
}