.reservation-time-calculation {
    ul.individ-list li {
        margin-bottom: 20px;
        font-size: 1rem;
    }

    &__slider {
        width: 350px; 

        @media (max-width: 375px) {
            width: 100%;
        }

        .swiper-slide {
            padding: 40px 0 10px;
            border: 2px solid #ECF6FF;
            border-radius: 16px;
        }

        .open-img {
            position: static;
            display: block;
            margin: 6px 10px 0 auto;
            border: none;
            background-color: transparent;
            width: 2.375rem;
            height: 2.375rem;
            padding: 0 !important;
        }

        .swiper-pagination-bullet {
            margin-right: 0 !important;
        }
    }

    &__thumbs {
        width: 21.875rem;
        padding-top: 2.1875rem;

        @media (max-width: 375px) {
            width: 100%;
        }
    }
}