.catalog-swiper {
  overflow: hidden;
  padding-bottom: 0.625rem;
  position: relative;
  height: fit-content;

  .swiper-pagination {
    bottom: -0.875rem;

    &-bullet {
      width: 0.625rem;
      height: 1.25rem;
      position: relative;
      background-color: transparent;
      opacity: 1;

      &:before{
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        width: 100%;
        height: 0.125rem;
        background-color: #EAE6E0;
        border-radius: 0.3125rem;
      }

      &-active{
        &:before{
          background-color: $blue_2;
        }
      }
    }
  }
}

.swiper-button-prev {
  &:after {
    display: none;
  }
}

.swiper-button-next {
  &:after {
    display: none;
  }
}


.rounded-pagination {
  height: 2.1875rem;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: $lvl_2;
  position: relative;
  margin-top: 1.4375rem;

  .swiper-pagination-bullet {
    width: 1.875rem;
    height: 1.875rem;
    background-color: transparent;
    position: relative;
    opacity: 1;
    &.swiper-pagination-bullet-active {
      &:before {
        border-color: $blue_1;
        background-color: $blue_1;
      }
    }

    &:before {
      position: absolute;
      content: "";
      border-radius: 50%;
      width: 0.5625rem;
      height: 0.5625rem;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      border: 0.125rem solid $turquoise;
    }

    &:hover {
      &:before{
        border-color: $blue_1;
      }
    }

    &:not(:last-child) {
      margin-right: 1.375rem;
    }

    .pagination-canvas{
      width: 100%;
      height: 100%;
    }
  }

  &.__white {
    .swiper-pagination-bullet {
      &:before{
        border-color: rgba(255,255,255,.5);
      }
      &:hover {
        &:before{
          border-color: rgba(255,255,255, 1);
        }
      }
    }

    .swiper-pagination-bullet-active {
      &:before {
        background-color: white;
      }
    }
  }
}

.product-swiper-top {
  overflow: hidden;

  .swiper-slide {
    position: relative;

    .sketchfab-iframe,
    .youtube-player {
      width: 100%;
      height: 32.5rem;
    }

    img {
      display: block;
      margin: 0 auto;
    }
  }
}

.product-swiper-thumbs-wrapper {
  position: relative;
  padding: 0 2.5rem;

  .product-swiper-thumbs {
    overflow: hidden;

    .swiper-slide {
      border: 0.125rem solid transparent;
      border-radius: 0.375rem;
      height: unset;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      transition: all 0.3s ease;
      cursor:pointer;

      &_blackout {
        &:before {
          content: '';
          width: 100%;
          height: 100%;
          position: absolute;
          left: 0;
          top: 0;
          background-color: rgba(0, 0, 0, .2);
          pointer-events: none;
          border-radius: 0.25rem;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }

      &_video {
        &:before {
          content: url("data:image/svg+xml,%3Csvg width='56' height='56' viewBox='0 0 56 56' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M38.5768 29.0575C39.0736 28.7772 39.3872 28.2059 39.3872 27.5812C39.3872 26.9623 39.0795 26.3959 38.5908 26.1129C38.5871 26.1108 38.5834 26.1086 38.5796 26.1065L38.5768 26.1049V26.1129L22.09 16.8484C21.6381 16.6131 21.1109 16.651 20.6893 16.9491C20.2678 17.2472 20.0093 17.7649 20.0025 18.3246V36.8457C19.9983 37.4208 20.2618 37.9566 20.6974 38.2585C21.1329 38.5604 21.6772 38.5845 22.1326 38.322L38.5768 29.0575Z' fill='white'/%3E%3Ccircle cx='28.0789' cy='27.8445' r='25.9617' stroke='white' stroke-width='3'/%3E%3C/svg%3E%0A");
        }
      }

      &_3D {
        &:before {
          content: '3D';
          color: $white;
        }
      }

      &-thumb-active {
        border-color: $turquoise;
      }
      &:hover{
        border-color: $turquoise;
      }
    }

  }

  .swiper-button-prev, .swiper-button-next {
    width: 1.625rem;
    height: 3.75rem;
    background: rgba(0, 88, 161, 0.4);
    border-radius: 0.375rem;
    margin: 0;
    bottom: 50%;
    top: auto;
    transform: translateY(50%);

    svg {
      width: 0.5rem;
      height: 1.125rem;
      fill: $white;
    }
  }

  .swiper-button-prev {
    left: 0;

    svg {
      transform: rotate(180deg);
    }
  }

  .swiper-button-next {
    right: 0;
  }
}

