.default-swiper {
  overflow: hidden;
  padding-bottom: 2.1875rem;
  position: relative;
}

.default-pagination.swiper-pagination {
  bottom: 0;

  .swiper-pagination-bullet {
    width: 1.875rem;
    height: 1.875rem;
    background: transparent;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    opacity: 1;
    border-radius: 50%;

    &:before {
      content: '';
      width: 0.625rem;
      height: 0.625rem;
      border: 0.125rem solid $turquoise;
      border-radius: 50%;
    }

    &-active {
      background-image: url("data:image/svg+xml,%3Csvg width='30' height='31' viewBox='0 0 30 31' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.9294 30.3353C11.9766 30.3353 9.09019 29.4598 6.63507 27.8193C4.17994 26.1788 2.2664 23.8472 1.13643 21.1192C0.00646216 18.3912 -0.28919 15.3894 0.286864 12.4934C0.862918 9.59736 2.2848 6.93719 4.37272 4.84928C6.46063 2.76137 9.12079 1.33948 12.0168 0.763426C14.9128 0.187373 17.9146 0.483024 20.6426 1.61299C23.3706 2.74297 25.7023 4.6565 27.3427 7.11163C28.9832 9.56675 29.8588 12.4532 29.8588 15.406L27.9635 15.406C27.9635 12.828 27.1991 10.308 25.7669 8.16458C24.3347 6.02113 22.299 4.35052 19.9173 3.36399C17.5357 2.37747 14.9149 2.11935 12.3866 2.62228C9.85819 3.1252 7.53573 4.36658 5.71288 6.18944C3.89002 8.01229 2.64864 10.3348 2.14572 12.8631C1.64279 15.3915 1.90091 18.0122 2.88743 20.3939C3.87395 22.7756 5.54457 24.8112 7.68802 26.2434C9.83147 27.6756 12.3515 28.4401 14.9294 28.4401L14.9294 30.3353Z' fill='%230058A1'/%3E%3C/svg%3E%0A");

      &:before {
        border-color: $blue_1;
