.trainingCenter-swiper,
.img-swiper {
  overflow: hidden;
  position: relative;

  .swiper-slide {
    height: unset;

    img {
      width: 100%;
      object-fit: cover;
      height: 100%;
      max-height: 27.1875rem;
    }
  }

  .swiper-button-prev, .swiper-button-next {
    width: 3.125rem;
    height: 5rem;
    background: rgba(0, 88, 161, 0.7);
    border-radius: 0.375rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    top: 50%;
    transform: translateY(-50%);

    svg {
      width: 0.5rem;
      height: 1.125rem;
      fill: $white;
    }
  }

  .swiper-button-prev {
    left: 1.875rem;

    svg {
      transform: rotate(180deg);
    }
  }

  .swiper-button-next {
    right: 1.875rem;
  }

  @media (max-width: $screen-md) {
    padding-bottom: 2.8125rem;
  }
}

.optional-equipment-swiper {
  overflow: hidden;
  padding: 0 1.25rem 3.125rem 0;
  position: relative;

  .swiper-slide {
    padding: 0.9375rem;
    background: $white;
    box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
    border-radius: 1.25rem;
    height: unset;
    transition: box-shadow 0.3s;

    .title {
      font-weight: 700;
      margin-bottom: 0.3125rem;
      font-size: 1.125rem;
      line-height: 1.35rem;
    }

    .description {
      font-size: 0.8125rem;
      line-height: 0.9375rem;
      color: $turquoise;
    }

    @media (max-width: $screen-sm) {
      .title {
        font-size: 0.8125rem;
        line-height: 0.9375rem;
      }
      .description {
        font-size: 0.625rem;
        line-height: 0.75rem;
      }

    }

    &:hover {
      box-shadow: 0.425rem 0.425rem 0.9rem rgba(0, 0, 0, 0.5);
      cursor: pointer;
      transition: all 0.3s ease;
      
      p:not(:last-child) {
        color: $blue_2;
      }
    }
  }
}

.optional-equipment {
  
  .swiper-button-prev, .swiper-button-next {
    width: 3.125rem;
    height: 5rem;
    background: rgba(0, 88, 161, 0.7);
    border-radius: 0.375rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    top: 25%;
    transform: translateY(-50%);

    svg {
      width: 0.5rem;
      height: 1.125rem;
      fill: $white;
    }
  }

  .swiper-button-prev {
    left: 1.875rem;

    svg {
      transform: rotate(180deg);
    }
  }

  .swiper-button-next {
    right: 1.875rem;
  }
}

.default-swiper-scrollbar {
  background: $light_2;
  height: 0.5rem !important;

  .swiper-scrollbar-drag {
    background: $blue_1;
  }
}

.compare-swiper-top {
  overflow: hidden;
  position: relative;
  background: $white;
  border-bottom: 0.0625rem solid $light_2;

  &.fixed {
    padding-top: 0.3rem;
  }

  .swiper-slide {
    position: relative;

    .close {
      position: absolute;
      top: 0;
      right: 0;
      width: 1rem;
      height: 1rem;
      fill: $blue_1;
      cursor: pointer;
      z-index: $lvl_1;
      line-height: 0;

      &:hover {
        fill: $blue_2;
      }
    }
  }
}

.compare-swiper-bottom {
  overflow: hidden;
  position: relative;
}
