.education {
    font-family: 'Inter', sans-serif;
    color: $dark;

    .subtitle {
        font-size: 1.125rem;
        line-height: 2rem;
        margin-top: 40px;
    }
}

.education-card {
    display: flex;
    padding: 4.375rem 5.9375rem 4.375rem 2.1875rem;
    margin-top: 4.375rem;
    gap: 3.75rem;
    background: url("./images/education/education-card_bg.png") 0 0/cover no-repeat rgba(0, 88, 161, 0.07);
    border-radius: 1.25rem;
    align-items: center;

    @media(max-width: 991px){
        flex-direction: column;

        .education-card__img {
            max-width: 60%;
        }
    }
}

.education-card__img {
    min-width: 285px;

    img {
        width: 100%;
        object-fit: cover;
    }
    
}

.education-card__desc {
    display: flex;
    flex-direction: column;

    .btn-primary {
        font-weight: 600;
        font-size: 1rem;
        line-height: 1.1875rem;

        @media (max-width: 375px) {
            font-size: 0.8125rem;
            line-height: 1rem;
        }
    }
}

.education-card__title {
    font-family: 'Gilroy', sans-serif;
    font-weight: 800;
    font-size: 1.875rem;
    line-height: 2.25rem;
}

.education-card__subtitle {
    color: $turquoise;
    margin-top: 0.625rem;

}

.education-card__text,
.education-card__link {
    margin-top: 2.5rem;
}

@media(max-width: 1200px){
    .education-card__text,
    .education-card__link {
        margin-top: 1.5625rem;
    }

    .education-card {
        padding: 3.125rem 2.1875rem;
    }
}

@media(max-width: 991px){

    .education-card__img {
        max-width: 60%;
    }
}

@media(max-width: 568px) {
    .education-card {
        padding: 1.875rem 1.25rem;
        margin-top: 3.125rem;
    }
}

@media (max-width: 375px) {
    .education-card__img {
        min-width: 13.75rem;
    }
}