.filter {
  &-items {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
  }

  &-item {
    display: inline-flex;
    align-items: center;
    background: #EFF3F6;
    border-radius: 1.25rem;
    padding: 0.4375rem 1rem;
    margin: 0 .5rem .5rem 0;
    user-select: none;


    &__name {
      margin-right: .5rem;
      font-size: 0.8125rem;
      line-height: 1rem;
      font-family: "Inter", sans-serif;
      color: $dark;
    }

    &__close {
      display: flex;

      svg {
        width: 0.5rem;
        height: 0.5rem;
        stroke: $dark;
      }

      &:hover {
        svg {
          stroke: $primary;
        }
      }
    }

    &_reset {
      background: none;
      padding-top: 0.125rem;

      a {
        font-size: 0.8125rem;
        line-height: 0.9375rem;
        color: $blue_1;
      }
    }
  }
}

.tags {
  &_absolute {
    position: absolute;
    right: 0.9375rem;
    top: 0.9375rem;
    z-index: $lvl_2;
    display: flex;
    flex-direction: column;
    gap: 0.3125rem;
    align-items: flex-end;

    .tag {
      margin: 0;
      display: block;
      width: fit-content;
    }
  }

  &_other {
    display: flex;
    flex-wrap: wrap;

    .tag {
      margin: 0;
      padding: 0.125rem 0.625rem;
      font-size: 0.6875rem;
      line-height: 0.8125rem;
      color: $dark !important;
      &:not(:last-child){
        margin-right: 0.3125rem;
      }
    }
  }

  .tag {
    &.discount {
      background: $red;
      color: $white;
    }

    &.bestseller {
      background: #7C39D1;
      color: $white;
    }
  }

  &.catalog-tag {
    .tag {
      padding: 0.3125rem 0.5rem;
    }
  }
}

.tag {
  display: inline-block;
  background: #EFF3F6;
  border-radius: 1.25rem;
  padding: 0.4375rem 1.4375rem;
  margin: 0 .5rem .5rem 0;
  font-size: 0.75rem;
  line-height: 0.875rem;
  color: $dark;

  &:hover,
  &.active {
    color: $primary;
  }

  &-badge {
    background: $blue_1;
    color: $white;
    border-radius: 50%;
    margin-left: 0.4375rem;
    padding: 0.25rem;
    display: inline-block;
    font-size: 0.625rem;
    line-height: 0.75rem;
    font-family: "Inter", sans-serif;
  }


}
