.contacts-filter-section {
  margin-bottom: 2.5rem;
}

.filter-accordion{
  .accordion-button{
    border-radius: 0!important;
  }
}

.contacts-filter-section__delimetr {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media(max-width: 991px) {
    flex-direction: column;
  }
}

.contacts-filter-section__form {
  display: flex;
  align-items: center;

  @media(max-width: 991px) {
    width: 100%;
    margin-bottom: 1.25rem;
  }
}

.contacts-filter-section__selects {
  display: flex;
  align-items: center;

  .btn {
    border: 1px solid transparent; 

    &:not(:last-child) {
      margin-right: 1.875rem;
    }

    @media(max-width: 991px) {
      display: none;
    }
  }

  .ghost-btn {
    box-shadow: none;
  }

  @media(max-width: 991px) {
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
  }
}

.select-wrapper {
  &.contacts-filter-section__citys {
    width: 13.75rem;
    margin-right: 1.875rem;

    @media(max-width: 991px) {
      width: 100%;
      margin-bottom: 1.1875rem;
    }
  }

  &.contacts-filter-section__system-type {
    width: 15.9375rem;
    margin-right: 1.875rem;
    @media(max-width: 991px) {
      width: 100%;
    }
  }
}

.contacts-filter-section__view {
  display: flex;

  .nav-link {
    width: 1.75rem;
    height: 1.75rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.375rem;
    transition: all 0.3s ease;
    border: none;
    background-color: transparent;
    padding: 0;


    svg {
      width: 0.9375rem;
      height: 1.3125rem;
      fill: $blue_1;
      stroke: $blue_1;
      transition: all 0.3s ease;

      &:hover {
        fill: $blue_2;
        stroke: $blue_2;
      }
    }

    &.active {
      background-color: $blue_1;
      border-bottom: 0 !important;
      margin-bottom: 0 !important;
      padding-bottom: 0 !important;

      &:hover {
        background-color: $blue_2;
      }

      svg {
        fill: $white;
        stroke: $white;
        transition: all 0.3s ease;
      }
    }

    &:first-child {
      margin: 0 0.4375rem;
    }
  }

  @media(max-width: 991px) {
    width: 100%;
  }
}

.contact__table-box {
  margin-bottom: 4.375rem;

  .h2 {
    margin-bottom: 0;
  }
}

.contact__table {
  margin-top: 2.5rem;
  padding-top: 1.25rem;
  padding-bottom: 1.875rem;
  border-top: 0.0625rem solid $cyan;
  border-bottom: 0.0625rem solid $cyan;

  @media(max-width: 768px){
    margin-top: 1.25rem;
  }
}

.contact__table-row {
  display: flex;

  .contact__table-col {
    &:not(:last-child) {
      margin-right: 0.9375rem;
    }
  }

  &:not(:first-child) {
    margin-top: 1.25rem;
  }

  &:not(:last-child) {
    padding-bottom: 1.875rem;
    border-bottom: 0.0625rem solid $cyan;
  }

  @media(max-width: 991px) {
    flex-direction: column;
  }
}

.contact__table-col {
  flex: 1;
 
  .h6 {
    display: block;
    margin-top: 0;
    @media(max-width: 991px) {
      font-size: 1rem;
      line-height: 1.6rem;
      font-weight: 700;
      margin-bottom: 0.3125rem;
    }
  }
}

.contact__name {
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;
  color: $dark;

  @media(max-width: 991px) {
    font-size: 0.8125rem;
    line-height: 0.9375rem;
  }
}

.contact__works {
  display: flex;
  flex-direction: column;

  @media(max-width: 991px) {
    flex-direction: row;
    margin: 0.625rem 0;
  }
}

.contact__work {
  color: $turquoise;
  font-size: 1rem;
  line-height: 1.6rem;
  font-family: "Inter", sans-serif;
  display: inline-block;

  &:not(:last-child) {
    margin-bottom: 0.3125rem;
  }

  @media(max-width: 991px) {
    &:not(:last-child) {
      border-right: 0.0625rem solid $turquoise;
    }

    &:first-child {
      padding-right: 0.375rem;
    }
    &:last-child {
      padding-left: 0.375rem;

    }
    &:nth-child(2) {
      padding: 0 0.375rem;
    }
  }
  @media(max-width: 568px) {
    font-size: 0.8125rem;
    line-height: 0.9812rem;
  }
}

.contact__address {

  svg {
    fill: $dark;
    width: 1rem;
    height: 1.375rem;
    display: inline-block;
    margin-right: 0.5625rem;

    @media(max-width: 991px) {
      display: none;
    }
  }
}

.contact__address-text {
  font-size: 1rem;
  line-height: 1.6rem;
  color: $dark;
  font-family: "Inter", sans-serif;

  @media(max-width: 568px) {
    font-size: 0.8125rem;
    line-height: 0.9812rem;
  }
}

.contact__address-link {
  display: block;
  width: max-content;
  margin-top: 0.3125rem;

  @media(max-width: 568px) {
    font-size: 0.8125rem;
    line-height: 0.9812rem;
  }
}

.contact__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 11.25rem;
  margin: 0 auto;


  .address-link {
    width: max-content;
    color: $blue_1;
    stroke: $blue_1;
    transition: all 0.3s ease;

    img, svg {
      width: 18px;
      height: 18px;
      display: inline-block;
      margin-right: 0.625rem;
    }

    &:hover {
      color: $blue_2;
      stroke: $blue_2;
      text-decoration: underline;
      text-underline-offset: 0.25rem;
    }

    @media(max-width: 568px) {
      font-size: 0.8125rem;
      line-height: 0.9812rem;

      &:not(:last-child) {
        margin-bottom: 0.625rem;
      }
    }
  }

  @media(max-width: 991px) {
    margin: 0.9375rem 0;
  }
}

.contact__button-container {
  display: flex;
  justify-content: flex-end;

  @media(max-width: 991px) {
    justify-content: flex-start;
  }
}

.contact__pagination {
  .pagination-more {
    margin-right: 2.8125rem;
  }
}

.contact__pagination-content {
  display: flex;
  align-items: center;

  @media(max-width: 568px) {
    justify-content: center;
    .pagination {
      display: none;
    }
    .pagination-more {
      margin-right: 0;
      font-weight: 600;
    }
  }
}

.contact__content-section {
  margin-bottom: 5.625rem;

  @media(max-width: 991px) {
    margin-bottom: 4.375rem;
  }
}

.map__ballon-phone-icon {

}

.map__ballon-email-icon {
  width: 1.0625rem;
  margin-right: 0.6875rem;
  display: inline-block;
}

.contact__map {
  height: 40.625rem;
  max-width: 101.875rem;
  width: 100%;
  padding: 0 0.9375rem;
  margin: 0 auto;

  @media(max-width: 568px) {
    padding: 0;
  }
}

.map__textContent {
  h4 {
    color: $blue_1;
    font-size: 1.625rem;
    line-height: 1.95rem;
    margin-bottom: 0.3125rem;
    margin-top: 0;
  }

  @media(max-width: 767px) {
    max-width: 15rem;
  }
}

.map__address {
  font-size: 0.8125rem;
  line-height: 0.9812rem;
  font-family: 'Inter', sans-serif;
  display: inline-block;
  margin-bottom: 0.625rem;
}


.map__ballon-bottom-container {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media(max-width: 767px) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.map__balloon-works-container {
  margin-bottom: 0.9375rem;
  @media(max-width: 991px) {
    display: none;
  }
}

.map__ballon-address-container {          
  margin-bottom: 0.9375rem;
  max-width: 20rem;
}

.map__address-text {
  font-size: 0.8125rem;
  line-height: 0.9812rem;
  color: $dark;
  font-family: 'Inter', sans-serif;
}

.map__balloon-works-item {
  font-size: 0.8125rem;
  line-height: 0.9812rem;
  color: $turquoise;

  &:not(:last-child) {
    border-right: 0.0625rem solid $turquoise;
  }

  &:first-child {
    padding-right: 0.375rem;
  }

  &:last-child {
    padding-left: 0.375rem;

  }

  &:nth-child(2) {
    padding: 0 0.375rem;
  }

}

.map__balloon-contacts-info {
  display: flex;
  flex-direction: column;

  a {
    color: $blue_1;
    transition: all 0.3s ease;

    &:hover {
      color: $blue_2;
      text-decoration: underline;
      text-underline-offset: 0.25rem;
    }
  }

  @media(max-width: 767px) {
    margin-bottom: 0.75rem;
  }
}

.ymaps-2-1-79-balloon__close-button {
  background: url('./images/icons/map-close-icon.svg') center center no-repeat !important;
  opacity: 1 !important;
}

.ymaps-2-1-79-balloon {               
  box-shadow: none !important;
}

.ymaps-2-1-79-balloon__layout {

  border-radius: 1.25rem;
  padding: 1.875rem;
}

.ymaps-2-1-79-balloon__content {
  padding: 0 !important;
  margin-right: 0 !important;
}

.ymaps-2-1-79-balloon__close {
  position: absolute !important;
  top: 1.0625rem !important;
  right: 1.375rem !important;

  @media(max-width: 767px) {
    top: 0.4375rem !important;
    right: 0.75rem !important;
  }
}

.ymaps-2-1-79-balloon__tail {
  box-shadow: none !important;
}
