.counter {
  display: inline-block;
  width: 5.9375rem;
  position: relative;
  user-select: none;

  &-input {
    padding: 0.4375rem 1.875rem;
    text-align: center;
    border-radius: 1.875rem;
  }

  &-minus,
  &-plus {
    display: inline-block;
    width: 1.875rem;
    height: 100%;
    cursor: pointer;
    position: absolute;
    top: 0;

    &:hover {
      ~ .counter-input {
        border-color: $primary;
      }
    }

    &::before {
      display: inline-block;
      content: '';
      width: 0.875rem;
      height: 0.125rem;
      background: $primary;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &-minus {
    left: 0;
  }

  &-plus {
    right: 0;

    &::after {
      display: inline-block;
      content: '';
      width: 0.875rem;
      height: 0.125rem;
      background: $primary;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(90deg);
    }
  }
}
//

.form-group{
  .form-control{
    min-height: 2.875rem;
    border-radius: 1.875rem;
    font-size: 1rem;
    line-height: 1.6rem;
    font-family: "Inter", sans-serif;
    box-shadow: none;
    transition: all 0.3s ease;

    &:focus{
      border-color: $blue_2;
    }

    &:hover{
      border-color: $blue_2;
    }
  }

  textarea.form-control{
    min-height: 10.9375rem;
    padding: 1.5rem 1.25rem;
  }

  &.input_text_active{
    .form-control{
      border-color: $blue_2;
    }
  }

  &.input_text_disabled{
    .form-control{
      border-color: $light_1;
      color: $light_1;
      background-color: transparent;
    }
  }
  &.input_text_alert{
    .form-control {
      border-color: $red;
    }

    .form-file-label {
        border-color: $red;
    }

    .form-check-input {
        border-color: $red;
    }
  }
}

.custom-group {
  position: relative;

  &-prepend {
    .form-control {
      padding-left: 2.5rem;
      min-height: 2.875rem;
      border-radius: 1.875rem;
      transition: all 0.3s ease;

      &:hover{
        border-color: $blue_2;
      }
    }
    .custom-group-icon {
      left: 0.0625rem;
      svg{
        width: 1.125rem;
        height: 1.125rem;
      }
    }
    &.hover{
      .form-control {
        border-color: $blue_2;
      }
    }
    &.disabled{
      .form-control {
        border-color: $cyan;
      }
    }
    &.custom-group-alert{
      .form-control {
        border-color: $red;
      }
    }
  }

  &-append {
    .form-control {
      padding-left: 2.5rem;
      min-height: 2.875rem;
      border-radius: 1.875rem;
      transition: all 0.3s ease;

      &:hover{
        border-color: $blue_2;
      }
    }
    .custom-group-icon {
      right: 0.0625rem;
      svg{
        width: 1.125rem;
        height: 1.125rem;
      }
    }

    .form-control {
      padding-right: 2.5rem;
    }

    .custom-group-icon {
      right: 0.0625rem;
      .icon-eye{
        width: 1.125rem;
        height: 1.125rem;
        display: flex;
        align-items: center;
        .closed{
          display: none;
        }
      }
      .icon-eye-closed{
        .open{
          display: none;
        }
        .closed{
          display: block;
        }
      }
      &:hover{
        fill: $primary;
      }

    }
  }

  &-icon {
    height: calc(100% - 0.125rem);
    width: 2.5rem;
    font-size: 1.125rem;
    fill: $secondary;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

  }
}

.form-control{
  box-shadow: none !important;
  border-radius: 1.875rem;
}
input[type="checkbox"]:checked,input[type="radio"]:checked{
  background-color: $blue_1;
  border-color: $blue_1;
}

@import "file-input";
@import 'vendor/choices';
@import 'vendor/datepicker';
