.invoice-request {
    counter-reset: list; 

    &--authorized,
    &--unauthorized {
        display: flex;
        justify-content: space-between;

        > * {
            &:not(:last-child) {
                margin-right: 20px;

                @media (max-width: 1400px) and (min-width: 992px) {
                    max-width: 540px;
                }
    
                @media (max-width: 1200px) {
                    margin-right: 0;
                }
            }
        }
    
        @media (max-width: 1200px) {
            flex-direction: column;
    
            > * {
                &:not(:last-child) {
                    margin-bottom: 30px;
                }
            }
        }

        textarea.form-control {
            border: 1px solid #8A92A6;
            border-radius: 1.25rem;

            &::placeholder {
                color: #A9B6C5;
            }
        }

        .js-calc_form_button {
            padding: 14px 30px;
            font-weight: 500;
            font-size: 18px;
            line-height: 22px;
        }
    }

    &__title {
        &::before {
            counter-increment: list;  
            content: counter(list) ". ";
        }
    }

    &__form-label {
        font-weight: 700;
        font-size: 16px;

        span {
            color: $red;
        }
    }

    &__group-input {
        display: flex;

        @media (max-width: 500px) {
            flex-direction: column;
        }

        > * {
            &:not(:last-child) {
                margin-right: 30px;
            }
        }

        .form-group {
            width: 50%;

            @media (max-width: 500px) {
                width: 100%;
            }
        }
    }

    &__result-block {
        @media (max-width: 1200px) and (min-width: 992px) {
            width: 50%;
        }

        @media (min-width: 480px) {
            min-width: 445px;
        }

        .btn {
            font-size: 0.8125rem;
            line-height: 1rem;
            font-weight: 600;
            padding: 0.5rem 0.9375rem;
            border-radius: 1.875rem;
        }

        .btn-primary {   
            height: 2rem;
        }

        .ghost-btn {
            height: 36px;
        }
    }
}