.exampleDetail-list {
  margin-bottom: 4.375rem;

  .row:first-child {
    flex-wrap: wrap-reverse;
  }

  .row:not(:first-child) {
    margin-top: -0.625rem;
  }

  @media(max-width: 991px) {
    margin-bottom: 3.125rem;
  }
}

.exampleDetail-list__description {
  font-size: 1.125rem;
  line-height: 1.975rem;
  font-weight: 400;
  font-family: 'Inter', sans-serif;
  color: $dark;
  display: inline-block;
}

p.exampleDetail-list__description {
  font-size: 1rem;
  line-height: 1.6rem;
  &:not(:last-child) {
    margin-bottom: 1.875rem;
  }
}

.exampleDetail__img-wrap {
  max-height: 25.9375rem;
  max-width: 45.625rem;
  margin: 2.5rem 0;
  img {
    width: 100%;
    height: 100%;
  }
}

.exampleDetail-list__doc {
  display: flex;
  gap: 0.9375rem;
  
  p:first-child {
    color: $blue_1;
    margin-bottom: 0;
  }

  svg {
    width: 2.5rem;
    height: 3rem;
    fill: $blue_1;
  }

  &:hover{
    p:first-child {
      color: $blue_2;
    }
  
    svg {
      fill: $blue_2;
    }
  }
}

.exampleDetail-tabs__tags { 
  display: flex;
  margin: 1.375rem 0 0.625rem 0;
  
  span {
    padding: 0.125rem 0.625rem;
    border-radius: 0.375rem;
    margin-right: 0.625rem;  
    font-size: 0.6875rem;
    line-height: 0.813rem;
    font-family: 'Inter', sans-serif;
    background-color: #ddeffb;
    color: $blue_2;
  }

  @media(max-width: 380px){
    flex-wrap: wrap;
    span {
      margin-bottom: 0.625rem;
    }
  }
}

.exampleDetail__gost-box {
  max-width: 382px;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  position: relative;
  

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 1.25rem;
    bottom: -0.625rem;
    right: -0.5625rem;
    background: url("./images/events/event-graph-background.png") center center no-repeat;
    background-size: cover;
    z-index: $lvl_-1;
  }

  @media(max-width: 991px) {
    margin-top: 2.5rem;
  }

  .wrap {
    background: $card-bg;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    border-radius: 1.25rem;
    padding: 1.875rem 1.875rem 2.5rem;

    @media(max-width: 380px){
      padding: 1.5625rem 1.5625rem 1.875rem;
    }

    a {
      margin-top: 1.25rem;

      .text {
        font-family: "Inter", sans-serif;
        font-size: 0.813rem;
        line-height: 1rem;
        color: $dark;
      }

      .text:last-child {
        margin-top: 0.313rem;
        font-weight: 700;
        color: $blue_1;
      }

      &:hover{
        .text:last-child {
          color: $blue_2;
        }
      }
    }
  }

  .title {
    margin: 0;
  }

  
}

.exampleDetail__image-box {

  @media(max-width: 991px) {
    margin-top: 2.5rem;
  }

  max-width: 382px;
  border: 0.0625rem solid $cyan;
  border-radius: 1.25rem;
  
  img {
    width: 100%;
  }
}
