.exampleDetail__row-wrap { 
  margin-top: -2rem !important;

  @media (max-width: 1399px) {
    margin-top: 0 !important;
  }

  @media (max-width: 991px) {
    margin-top: 5.625rem !important;
  }
}

.exampleDetail__row-wrap:not(.m-b-l) {
  @media (max-width: 991px) {
    margin-top: 3.125rem !important;
  }
}

.example-swiperCard {
  h2 {
    margin: 7.5rem 0 2.8125rem;
    @media (max-width: 991px) {
      margin: 5rem 0 1.5625rem;
    }
  }
}



.card-swiper__wrap {
  position: relative;

  .card-swiper {
    position: relative;
    overflow: hidden;
    padding: 0.3125rem 0 0.625rem 0.3125rem; 
     
    .swiper-slide {
      img {
        object-fit: cover;
        max-height: 100%;
      }

      .catalog-card {
        margin: 0 1.25rem 0.625rem 0;
        padding: 0.625rem;
        border-radius: 1.25rem;
        display: block;

        &:hover { 
          box-shadow: 0.425rem 0.425rem 0.9rem rgba(0, 0, 0, 0.5);
          cursor: pointer;
          transition: all 0.3s ease;
          .title {
            color: $blue_2;
          }
        }

        .tags_absolute {
          right: 1.5625rem;
        }
      }
    }
  
    @media (max-width: $screen-md) {
      padding-bottom: 2.8125rem;
    }

    @media(max-width: 1679px){
      margin-left: 1.875rem;
      margin-right: 1.875rem;
    }

    @media(max-width: 767px){
      max-width: 340px;
      margin: 0 auto;

      .rounded-pagination .swiper-pagination-bullet:not(:last-child) {
        margin-right: 0;
      }
    }
  }

  .swiper-button-prev, .swiper-button-next {
    width: 3.125rem;
    height: 5rem;
    background: rgba(0, 88, 161, 0.7) url('./images/exampleDetail/exampleDetail-swiper-button.png')50% 50%/8px 18px no-repeat;
    border-radius: 0.375rem;
    margin: 0;
    top: 14%;
  }

  .swiper-button-prev {
    left: -1.0625rem;
  }

  .swiper-button-next {
    right: -1.0625rem;
    transform: rotate(180deg);
  }
  .catalog-controls__counter {
    display: block;
  }

  .catalog-card .order-button {
    margin-left: 0.3125rem;
  }
}

.example-aside {
  h2 {
    margin: 7.5rem 0 2.5rem;
    @media (max-width: 991px) {
      margin: 2.5rem 0 1.25rem;
    }
  }
}

.exampleDetail-swiperAside { 
  position: relative;
  overflow: hidden;
  padding-right: 1.25rem;

  .swiper-wrapper {
    @media(min-width: 381px){
      height: 26.875rem;
    }
  }
  .swiper-slide {
    min-height: 100%;
  }

  .rounded-pagination {
    margin-top: 1.875rem;

    .swiper-pagination-bullet {
      margin-right: 0;
    }
  }

  @media (max-width: 768px) and (min-width: 576px) {
    overflow: hidden;
    padding-right: 1.25rem;
    padding-bottom: 2.375rem;
  }
}

.example-aside__card {
  padding: 0;
  border-radius: 1.25rem;
  background: $card-bg;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: hidden;
  height: 100%;

  .example-aside__img-wrap {
    height: 50%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @media(max-width: 380px){
      height: 12.5rem;
    }
  }

  .title {
    display: block;
    margin: 0.625rem 0;
    font-family: "Gilroy", sans-serif;
    font-size: 1.25rem;
    font-weight: 800;
    line-height: 1.5rem;

    @media(max-width: 380px) {
      font-size: 1rem;
      font-weight: 700;
      line-height: 1.2rem;
    }
  }

  .example-aside__textInfo {
    padding: 0.9375rem 1.875rem 2.5rem;

    span {
      font-family: "Inter", sans-serif;
      font-size: 0.813rem;
      line-height: 1rem;
      color: $turquoise;
      display: block;
    }

    @media(max-width: 380px){
      padding: 0.9375rem 1.25rem 1.25rem;
    }
  }

  
  &:hover {
    cursor: pointer;
    box-shadow: 0.425rem 0.425rem 0.9rem rgba(0, 0, 0, 0.5); 
    transition: all 0.3s ease;

    .title {
      color: $blue_2;
    }
  }

  @media(max-width: 380px){
    height: 25.625rem;
  }
}