// color:
$white: #fff;
$milk_1: #F2F2F2;
$milk_2: #ced4da;
$light_1: #E5E5E5;
$light_2: #F5F5F5;
$blue_1: #0058A1;
$blue_2: #0B6BC5;
$blue_light: #36B1ED;
$indigo: #0073D3;
$dark-blue: #535966;  
$turquoise: #8A92A6;
$turquoise_2: #A9B6C5;
$cyan: #C7D2DE;
$black: #000;
$dark: #2C2C2C;
$dark-gray: #404040;
$red: #DC3545;
$green: #28A745;
$purple: #7C39D1;
$card-bg: linear-gradient(147.55deg, rgba(227, 227, 227, 0.35) 2.3%, rgba(255, 255, 255, 0.2) 91.62%), $white;

// z-index level
$lvl_-1:-100;
$lvl_0:0;
$lvl_1:100;
$lvl_2:200;
$lvl_3:300;
$lvl_4:400;
$lvl_5:500;

// grid breakpoints
$screen-xs: 0;
$screen-sm: 576px;
$screen-md: 768px;
$screen-lg: 992px;
$screen-xl: 1200px;
$screen-xxl: 1400px;
$screen-3xl:1680px;

// container max widths
$container-sm: 540px;
$container-md: 720px;
$container-lg: 960px;
$container-xl: 1140px;
$container-xxl: 1320px;
$container-3xl:1630px;

$grid-breakpoints: (
        xs: $screen-xs,
        sm: $screen-sm,
        md: $screen-md,
        lg: $screen-lg,
        xl: $screen-xl,
        xxl: $screen-xxl,
        3xl:$screen-3xl
);

$container-max-widths: (
        sm: $container-sm,
        md: $container-md,
        lg: $container-lg,
        xl: $container-xl,
        xxl: $container-xxl,
        3xl:$container-3xl
);





