$today: #FD783E;

.datepicker-wrapper {
	position: relative;


	.form-control {
		padding-right: 2.5rem;
		min-height: 2.875rem;
		border-radius: 1.875rem;
		transition: all 0.3s ease;

		&:hover{
			border-color: $blue_2;
		}
	}

	&::before {
		display: inline-block;
		content: '';
		-webkit-mask-image:url('images/sprites/calendar.svg');
		-webkit-mask-size: contain;
		background-color: $blue_1;
		width: 1.3125rem;
		height: 1.3125rem;
		position: absolute;
		top: 50%;
		right: .75rem;
		transform: translateY(-50%);
		pointer-events: none;
	}
}

.container__main{
	.day-item{
		cursor: pointer !important;
	}
	.button-previous-month,.button-next-month{
		cursor: pointer !important;
	}
}

.form-label.date-hover{
	.form-control{
		border-color: $blue_2;
	}
}

.form-label.date-disable{
	.form-control{
		border-color: $light_1;
	}
}

.form-label.date-alert{
	.form-control{
		border-color: $red;
	}
}