  @media(max-width: 568px) {
    margin-top: 8.125rem;
  }
}

.body-fixed {
  overflow: hidden;
}

.main-shadow {
  position: relative;

  &:before {
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: $lvl_2;
  }
}

.center-button {
  display: block;
  width: max-content;
  margin: 0 auto;
}

.top-section {

  @media(max-width: 1399px) {
    margin-bottom: 6.4375rem;
  }

  @media(max-width: 991px) {
    margin-bottom: 3.125rem;
  }
}

.btn-center {
  display: block;
  width: max-content;
  margin: 0 auto;
}

.thumbs-background {
  position: relative;
  padding-bottom: 4.375rem;

  &:before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 80%;
    background-color: #E6EFF6;
    z-index: $lvl_-1;
  }
}

.equipment-desk {

  .thumbs-background {

    &:before {
      top: 3.938rem;
  
      @media(max-width: 480px) {
        top: 1.875rem;
      }
    }
  }
  
}

.page_section {                         
  min-width: 0;
}

.title-box {
  display: flex;
  align-items: center;

  svg {
    margin: 0 1.125rem;
    stroke: #BECCE0;
    width: 1.25rem;
    height: 2.0625rem;

    @media(max-width: 568px) {
      margin: 0 0.6875rem;
    }
  }
}

.event-section {

  .swiper_pagination {
    width: 100%;
    margin-top: 0.9375rem;
  }

  .btn {
    padding: 0.9375rem 2.8438rem;

  }

  .rounded-pagination {

    @media(max-width: 568px) {
      display: flex;
    }
  }

  .title-box {
    .h2 {
      display: block;
      margin-top: 0;
      margin-bottom: 0;

      @media(max-width: 568px) {
        font-size: 1.625rem;
        line-height: 1.9375rem;
      }
    }
  }


}

.events-items {
  display: flex;

}

.events-item__textContent {
  position: relative;
  z-index: $lvl_1;

  .h5 {
    display: block;
    margin-top: 0;

    @media(max-width: 991px) {
      font-size: 1rem;
      line-height: 1.2rem;
    }
  }
}

.events-item {
  position: relative;
  min-height: 16.25rem;
  cursor:pointer;

  .h5{
    display: block;
    transition: all 0.3s ease;
  }

  &._imaged {
    border-radius: 0.75rem;
    overflow: hidden;
    padding: 0 1.5625rem 1.5625rem 1.5625rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    .h5 {
      color: $white;
    }

    .events-item__date {
      color: $white;
      font-size: 0.8125rem;
      line-height: 0.9375rem;
      font-family: 'Inter', sans-serif;
    }

    .events-item__background{
      transition: all 0.3s ease;
    }

    &:after{
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: transparent;
      transition: all 0.3s ease;
    }

    &:hover{
      .events-item__background{
        transform:scale(1.2);
        transition: all 0.3s ease;
      }
      &:after{
        background-color: rgba(0,0,0,0.3);
        transition: all 0.3s ease;
      }
    }
  }

  &._gradient {

    &:before {
      content: "";
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      position: absolute;
      background: linear-gradient(0deg, rgba(48, 120, 178, 0.41), rgba(48, 120, 178, 0.41));
      pointer-events: none;
    }
  }

  .h5 {
    margin-bottom: 0.625rem;
  }

  &:not(._imaged){
    &:hover{
      .h5{
        color: $blue_2;
        transition: all 0.3s ease;
      }
    }
  }
}

.events-item__date {
  margin-bottom: 0.625rem;
}

.events-item__description {
  color: $turquoise;
  font-size: 1rem;
  line-height: 1.6rem;
  font-family: 'Inter', sans-serif;
}

.events-item__tags {
  margin-bottom: 0.5625rem;
}

.events-item__tag {
  padding: 0.0625rem 0.625rem;
  color: $white;
  font-size: 0.6875rem;
  line-height: 0.875rem;
  font-family: 'Inter', sans-serif;
  border-radius: 0.375rem;

  &._blue {
    background-color: #1B73BB;
  }

  &._green {
    background-color: #41C79E;
  }

  &._light-blue {
    background-color: #55A4E4;
  }

  &._max-light-blue {
    background-color: #d8eefd;
