button:disabled {
  opacity: .5;
}

input[type="radio"], input[type="checkbox"] {
  box-shadow: none !important;
}

.color-check-label {
  position: relative;

  input {
    width: 0;
    height: 0;
    position: absolute;
    opacity: 0;
  }

  span {
    display: flex;
    align-items: center;
    gap: 0.625rem;
    margin-bottom: 0.625rem;
    position: relative;

    &:before {
      content: '';
      width: 1.25rem;
      height: 1.25rem;
      border-radius: 50%;
      border: 0.125rem solid transparent;
    }
  }

  &_white {
    span {
      &:before {
        background-color: rgba(0, 0, 0, 0.035);
        border-color: rgba(0, 0, 0, 0.01);
      }
    }
  }

  &_black {
    span {
      &:before {
        background-color: $black;
      }
    }
  }

  &_gray {
    span {
      &:before {
        background-color: #C4C4C4;
      }
    }
  }

  &_beige {
    span {
      &:before {
        background-color: #F5F5DC;
        border-color: rgba(0, 0, 0, 0.01);
      }
    }
  }

  input:checked + span:after {
    position: absolute;
    content: '';
    width: 28px;
    height: 28px;
    border-radius: 50%;
    border: 2px solid $blue_1;
    left: -4px;
    top: -4px;
  }
}

.banner {                              
  display: flex;
  position: relative;
  column-gap: 1.875rem;
  row-gap: 0.9375rem;
  justify-content: space-between;
  align-items: center;
  border-radius: 0.9375rem;
  background-image: url("images/catalog/catalog-banner-background.png");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 0 2.8125rem;
  @media (max-width: $screen-md) {
    background-image: url("images/catalog/catalog-banner-background_small.png");
  }

  &__text {
    font-size: 1.625rem;
    line-height: 1.9375rem;
    font-weight: 700;
    font-family: $title-font;
    color: $white;
  }

  &__img {
    margin-bottom: -0.9375rem;
  }

  &__close {
    width: 0.875rem;
    height: 0.875rem;
    cursor: pointer;
    position: absolute;
    top: 0.625rem;
    right: 0.625rem;

    &:before, &:after {
      content: '';
      width: 100%;
      height: 0.125rem;
      position: absolute;
      left: 0;
      top: 50%;
      background: #828282;
    }

    &:before {
      transform: translateY(-50%) rotate(45deg);
    }

    &:after {
      transform: translateY(-50%) rotate(-45deg);
    }
  }

  @media (max-width: $screen-md) {
    padding: 1.875rem 1.25rem 0;
    flex-direction: column;
    &__text {
      font-size: 1.125rem;
      line-height: 1.3125rem;
    }
  }

}

