      }

      tbody {
        display: block;

        tr {
          display: block;
          padding: 0.9375rem;
          border-radius: 1rem;
          margin-bottom: 0.625rem;

          &:last-child {
            margin-bottom: 0;
          }

          td {
            display: block;
            padding: 0;
            margin-bottom: 0.9375rem;

            &:last-child {
              margin-bottom: 0;
