.categories .example-tabs__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px 50px;
}

.question-info {
    display: flex;
    align-items: flex-start;

    svg {
        width: 24px;
        height: 24px;
        flex-shrink: 0;
        margin-right: 12px;
        fill: $blue_light;
    }

    &__wrap {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: -10px;

        > * {
            margin-bottom: 10px;

            &:not(:last-child) {
                margin-right: 53px;
            }
        }
    }
}

.help-info {
    display: flex;
    align-items: center;
    padding: 14px 22px;
    background: rgba(239, 249, 255, 0.8) url('./images/invoice-request/bg.svg') no-repeat 50% / cover;
    border-radius: 20px;

    > * {
        &:not(:last-child) {
            margin-right: 15px;
        }
    }

    &__icon-wrap {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        width: 36px;
        height: 36px;
        border-radius: 50%;
