.choices {
  font-size: 1rem;
  line-height: 1.6rem;

  &:after {
    border-color: $blue_1 transparent transparent !important;
  }

  .choices__inner {
    min-height: 2.875rem;
    font-family: "inter", sans-serif;
    background-color: transparent;
    transition: border-color 0.3s ease;

    &:hover {
      border-color: $blue_2;
      transition: border-color 0.3s ease;
    }
  }

  .is-highlighted {
    background-color: #F2FAFF !important;
  }

  &.is-open {
    &:after {
      border-color: transparent transparent $blue_1  !important;
    }

    .choices__inner {
      border-color: $blue_2;
      border-bottom-color: transparent;
      color: $dark;
      border-radius: 1.25rem 1.25rem 0 0;
    }
  }

  &.is-disabled {
    .choices__inner {
      opacity: 1;
      border-color: $light_1;
      background-color: $white;
      color: $light_1;
    }

    &:after {
      border-color: $milk_2 transparent transparent;
    }
  }

  .choices__inner {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 0.625rem 0 0.625rem 1.3125rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    background-clip: padding-box;
    border: 0.0625rem solid $cyan;
    appearance: none;
    border-radius: 1.875rem;

    input {
      display: none;
      pointer-events: none;
    }
  }

  &[data-type="select-multiple"] {
    .choices__inner {
      padding: 0.8125rem;

      .choices__item {
        padding: 0.3125rem 0.4375rem;
        background: $dark-gray;
        border-radius: 0.3125rem;
        color: $white;
        font-size: 0.75rem;
        line-height: 0.875rem;
        border: none;
      }

      .choices__item--selectable {
        font-size: 0.6875rem;
        line-height: 0.8319rem;
        background-color: #EAEEF1;
        color: $dark;
        border-radius: 18.75rem;
      }
    }

    .choices__button {
      margin: 0 0 0 0.3125rem;
      padding: 0;
      border: none;
      background: url("./images/icons/multiple-select-close.svg") center center no-repeat;
      position: relative;

    }
  }

  .choices__list {
    padding: 0 !important;
  }

  .choices__list--dropdown {
    border-top: none;
    border-color: $milk_2;
    border-radius: 0 0 $border-radius $border-radius;
    padding: .5rem 0;

    .choices__list {
      @include custom-scroll;
    }

    .choices__item {
      padding-left: 1.3125rem !important;
    }

    [role="option"] {
      padding: .5rem .5rem;
    }

    &.is-active {
      border-radius: 0 0 1.25rem 1.25rem;
      border-color: $blue_2;
    }
  }

  &:after {
    right: 1.1875rem !important;
  }
}

.select-wrapper {
  &.active {
    .choices__inner {
      border-color: $blue_2;
      color: $dark;
    }
  }

  &.disabled {
    .choices__inner {
      border-color: $light_1;
      color: $light_1;
    }
  }

  &.alert-select {
    .choices__inner {
      border-color: $red;
      color: $dark;
    }

    .choices__list--dropdown {
      &.is-active {
        border-radius: 0 0 1.875rem 1.875rem;
        border-color: $red;
      }
    }
  }
}


