.catalog-action-icon {
  width: 0.9375rem;
  height: 2.1875rem;
  line-height: 0;
  stroke: $cyan;
  cursor: pointer;
  transition: .3s;

  &:hover, &.active {
    stroke: $red;
  }
}

.compare-side {
  padding: 1.5625rem;
  background: #F3F9FF;
  border-radius: 1.25rem;

  &__item {
    cursor: pointer;
    font-size: 0.8125rem;
    line-height: 1rem;
    color: $blue_1;
    display: flex;
    align-items: center;

    &-icon-wrapper {
      display: inline-flex;
      width: 1.1875rem;
      height: 1.1875rem;
      justify-content: center;
      margin-right: 0.625rem;
    }

    &:hover {
      color: $blue_2;

      svg {
        fill: $blue_2;
      }
    }

  }

  svg {
    fill: $blue_1;

    &.trash {
      width: 1.25rem;
      height: 1.25rem;
    }

    &.close {
      width: 0.9375rem;
      height: 0.9375rem;
    }
  }

  @media(max-width: 991px){
    margin-bottom: 1.5625rem;
  }
}


.compare-table {
  &__item {
    padding: 0.625rem 0.9375rem 0.625rem 0.625rem;
    margin-bottom: 0;

    &.hover {
      background: rgba(239, 249, 255, 0.8);
    }

    @media (max-width: $screen-lg) {
      padding: 0.625rem 0.9375rem 0.625rem 0;
    }

    @media (max-width: $screen-sm) {
      font-size: 0.8125rem;
      line-height: 0.9375rem;
    }

    &-title {
      @media (max-width: $screen-sm) {
        font-size: 0.6875rem;
        line-height: 0.75rem;
      }
    }

  }

  &__mobile-title {
    font-size: 0.8125rem;
    line-height: 0.9375rem;
    font-weight: 700;
    @media (min-width: $screen-lg) {
      display: none;
    }
    @media (min-width: $screen-sm) {
      font-size: 1rem;
      line-height: 1.3125rem;
    }
  }
}

.fixed-block {
  padding-bottom: 1.875rem;
    
  @media (min-width: 768px) {
    padding-bottom: 2.5rem;
  }
    
  @media (min-width: 1400px) {
    padding-bottom: 3.125rem;
  }

  .pb-3-fixed-block {
    padding-bottom: 1rem;
  }

  &.fixed { 
    padding-bottom: 0;
    position: fixed;
    width: var(--width);
    top: 3.688rem;
    z-index: $lvl_1;
    animation: departure .5s;
    @keyframes departure {
      0% {
        transform: translateY(-100%);
      }
      100% {
        transform: translateY(0);
      }
    }

    &.fixed_shadow {
      box-shadow: 1rem 1.625rem 1.25rem -1.625rem rgba(0, 0, 0, 0.15);
    }

    .pb-3-fixed-block {
      padding-bottom: 0;
    }

    .swiper-slide {
      .price,
      form {
        display: none;
      }
      .catalog-swiper {

        margin-bottom: 0.1rem !important;
        
        .img-fluid {
          max-height: 4rem;
        }
      }
    }

    .js-product {
      display: flex;
        
      a {
        font-size: 0.8125rem;
        line-height: 0.9831rem;
        margin-top: 0.5rem;
        display: inline-block;
      }

      .catalog-swiper {
        width: 55%;

        .swiper-pagination {
          bottom: -0.6rem;
        }
      }
    }

    @media (max-width: 1200px) { 
      top: 103px;
    }
  }

  .swiper-slide {
     a {
        &:hover {
          cursor: pointer;
        }
     }      
  }
}

.compare-radio {
  display: flex;
  align-items: flex-start;
  gap: 0.625rem;
  cursor: pointer;

  input {
    display: none;
  }

  span {
    color: $turquoise;
  }

  input:checked + .title {
    color: $dark;
    border-bottom: 0.3125rem solid $blue_2;
  }
}

.comparison_top{
  margin-bottom: 1.5625rem;
} 

.compare__favor-btn {

  @media (min-width: 1680px) {
    margin-left: 0.5rem;
  }

  @media (max-width: 1679px) {
    margin-left: 1.5rem;
  }

  @media (max-width: 480px) {
    margin-left: 1.3rem;
  }

}

.item-closed {
  display: none;
}