.characteristic-link{
  display: flex;
  align-items: center;

  a {
    font-size: 0.938rem;
    line-height: 1.125rem;
  }

  svg{
    display: inline-block;
    margin-left: 0.725rem;
    stroke: $blue_1;
    margin-bottom: 0.25rem;
  }
}

.documents-wrapper {
  padding: 2.5rem 2.8125rem;
  background-color: #F3F9FF;
  border-radius: 1.25rem;
  background-image: url("/images/productDetail/productDetail-documents-background.svg");
  background-size: cover;
  @media (max-width: $screen-md) {
    padding: 2.1875rem 0.9375rem;
  }
  .h3 {
    display: block;
  }

  a {

    &:hover {
      p:first-child {
        text-decoration: underline;
        text-underline-offset: 0.25rem;
        color: $blue_2;
      }

      svg {
        fill: $blue_2;
      }
    }

    p {
      transition: all 0.3s ease;
      margin: 0;
    }
    
    p:first-child {
      color: $blue_1;
    }
  }
}

.file {
  display: flex;
  gap: 0.9375rem;
  color: $blue_1;

  svg {
    width: 2.1875rem;
    height: 2.5rem;
    min-width: 2.1875rem;
    min-height: 2.5rem;
    fill: $blue_1;
  }
}

.product-price {
  padding: 1.875rem 1.5625rem;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  position: relative;
  background: $card-bg;
  @media (max-width: $screen-sm) {
    padding: 1.5625rem 1.25rem;
  }

  &:after {
    content: '';
    background-image: url("images/productDetail/productDetail-product_price-background.svg");
    background-size: cover;
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: -0.9375rem;
    right: -0.9375rem;
    border-radius: 1.25rem;
    z-index: $lvl_-1;
  }

  .detail__action {
    &:not(:last-child) {
      margin-right: 2.5rem;
    }
  }

  .h3 {
    @media(max-width: 991px){
      margin-bottom: 0.4rem;
    }
  }
}

.detail__production-column{ 

  .swiper-pagination-bullet {
    @media(max-width: 568px){
      margin-right: 0rem !important;
    }
  }

  @media(max-width: 991px){
    margin-bottom: 3.75rem;
  }
}

.detail__production-thumbs {
  justify-content: center;
}

.detail__buttons-delimetr{
  .d-block{
    &:not(:last-child){
      margin-right: 3.125rem;

      @media(max-width: 1199px){
        margin-right: 0.9375rem;
      }
    }
  }
}

.detail__buttons-delimetr-colors {
  margin-left: 0.9rem;
}

.detail__product-price-box{
  .counter{
    margin-right: 0.9375rem;
  }
}

.detail__radio-buttons{
  display: flex;
  align-items: center;
  .detail__radio{
    margin-bottom: 0.9375rem;

    &:not(:last-child){
      margin-right: 0.625rem;
    }
  }

  .form-check-label{
    &:not(:last-child){
      margin-right: 0.9375rem;
    }
  }
}

.detail__action {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  svg {
    width: 0.75rem;
    height: 1.0625rem;
    stroke: $turquoise;
    display: inline-block;
    margin-right: 0.9375rem;
  }

  &:hover {
    color: $red;

    svg {
      stroke: $red;
    }
  }
}

.detail__radio {
  input {
    display: none;
  }

  span {
    padding: 0.4375rem 0.9375rem;
    font-size: 0.8125rem;
    line-height: 1rem;
    background: #EFF3F6;
    border-radius: 1.25rem;
  }

  input:checked + span {
    color: $white;
    background: $blue_1;
    transition: all 0.3s ease;

    &:hover {
      background: $blue_2;
    }
  }
}

.sketchfab-iframe,
.youtube-player {
  width: 100%;
  border-radius: 1.25rem;
  height: 13.4375rem;
  padding: 0 2px;

  &.sidebar-video {
    width: 100%;
    @media (min-width: $screen-3xl) {
      height: 19.6875rem;
    }
    @media (min-width: $screen-lg) {
      height: 13.4375rem;
    }
    @media (min-width: $screen-sm) {
      height: 19.6875rem;
    }
  }
}

.product-benefits {
  background: #303030;
  padding: 3.75rem 0;
  @media (max-width: $screen-md) {
    padding: 1.875rem 0 3.75rem;
  }
}

.product-benefits_margin {
  margin-bottom: 8.125rem;
  
  @media (max-width: 991px) {
    margin-bottom: 5.625rem;
  }

  @media (max-width: 767px) {
    margin-bottom: 1.25rem;
  }
} 

.swiper-button {
  background-color: rgba(0, 88, 161, 0.29) !important;
  border-radius: 0.375rem;
  cursor: pointer;
  border: 0.0625rem solid transparent;
  transition: all 0.3s ease;

  svg {
    fill: $white;
    transition: all 0.3s ease;
  }

  &:hover {
    background-color: transparent !important;
    border-color: rgba(0, 88, 161, 0.29);

    svg {
      fill: rgba(0, 88, 161, 0.29);
    }
  }
}

.specification {
  border-bottom: 0.0625rem dashed $turquoise;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  & > * {
    position: relative;
    bottom: -0.375rem;
    background: $white;
    margin: 0;
  }

  &__key {
    color: $turquoise;
    white-space: nowrap;
  }

  &__value {
    text-align: end;

    &:not(.d-flex) {
      padding-right: 1.25rem;
    }
  }

  @media (max-width: $screen-sm) {
    border-bottom: none;
    & > * {
      bottom: 0;
    }
    &__key {
      display: none;
    }
    &__value {
      text-align: start;
      padding-right: 0;
    }
  }
}

.two-string {
  border-bottom: none;
  gap: 0rem;

    &::before {
      content: ''; 
      border-bottom: 0.0625rem dashed $turquoise;
      flex-grow: 1;
      order: 2;  
    }

    .specification__value {
      order: 3;

      @media (min-width: 1200px) and (max-width: 1399px) {
        width: min-content;
      }
    }

    .specification__key {
      @media (min-width: 992px) and (max-width: 1199px) {
        white-space: normal;
      }
    }

}

.show-hidden-block {
  padding: 0.75rem 3.75rem;
}

.text-sm-nowrap {                           
  @media (min-width: $screen-sm) {
    white-space: nowrap;
  }
}

@each $key, $breacpoint in $grid-breakpoints {
  @media(min-width: $breacpoint) {
    .text-#{$key}-nowrap {
      white-space: nowrap;
    }
  }
}

.fake-icon {
  width: 1.0625rem;
  height: 1.0625rem;
  fill: $blue_2;
}

.where-bye {
  background: transparent;
  border-radius: 3.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 1.25rem;
  padding: 0;

  &__select {
    width: 100%;
    max-width: 37.5rem;
    margin-bottom: 0;

    .choices__inner {
      background: $white;
    }
  }

  .btn{
    padding: 0.625rem 2.125rem;

    @media(max-width: 767px){
      display: none;
    }
  }

  @media (min-width: $screen-md) {
    padding: 0.9375rem;
    background-color: #E6EEF6;
    background-image: url("images/productDetail/productDetail-search-background.svg");
    background-size: cover;
  }
}

.img-download {
  position: fixed;
  width: 100%;
  height: 45px;
  top: 0px;
  left: 0;
  z-index: 10001;
  background-color: rgba(0, 0, 0, 0.5);
}

.img-download_btn {
  display: inline-block;
  white-space: nowrap;
  line-height: 45px;
  vertical-align: middle;
  padding: 0 20px;
  cursor: pointer;
  -webkit-transition: all .2s;
  transition: all .2s;
  cursor: pointer;
  color: #E1E1E1;
  font-weight: bold;
  font-size: 14px;
  text-decoration: none;
  float: left;

  &:hover {
    background-color: #555555;
    color: #E1E1E1;
    text-decoration: none;
  }

  &::before {
    content: url(./images/icons/rz-link.png);
    display: inline-block;
    margin-right: 5px;
  }
}

.img-download_zoom {
  min-width: 60px;
  display: inline-block;
  white-space: nowrap;
  line-height: 45px;
  vertical-align: middle;
  padding: 0 20px;
  cursor: pointer;
  -webkit-transition: all .2s;
  transition: all .2s;
  cursor: pointer;
  color: #E1E1E1;
  font-weight: bold;
  font-size: 14px;
  float: left;

  &:hover {
    background-color: #555555;
    color: #E1E1E1;
    text-decoration: none;
  }

  &::before {
    content: url(./images/icons/rz-zoom.png);
    display: inline-block;
    margin-right: 5px;
  }
}