.p-l-0 {
  padding-left: 0;
}

.p-r-0 {
  padding-right: 0;
}

@each $indent in $indentation {
  .gap-#{$indent} {
    gap: #{$indent}px !important;
  }
  .col-gap-#{$indent} {
    column-gap: #{$indent}px !important;
  }
  .row-gap-#{$indent} {
    row-gap: #{$indent}px !important;
  }
  .m-b-#{$indent} {
    margin-bottom: #{$indent}px !important;
  }
  .m-t-#{$indent} {
    margin-top: #{$indent}px !important;
  }
  .m-r-#{$indent} {
