  }
  .p-r-#{$indent} {
    padding-right: #{$indent}px !important;
  }
  .p-l-#{$indent} {
    padding-left: #{$indent}px !important;
  }

}

@each $key, $breacpoint in $grid-breakpoints {
  @media(min-width: $breacpoint) {
    @each $indent in $indentation {
      .gap-#{$key}-#{$indent} {
        gap: #{$indent}px !important;
      }
      .col-gap-#{$key}-#{$indent} {
        column-gap: #{$indent}px !important;
      }
      .row-gap-#{$key}-#{$indent} {
        row-gap: #{$indent}px !important;
      }
      .m-b-#{$key}-#{$indent} {
        margin-bottom: #{$indent}px !important;
      }
      .m-t-#{$key}-#{$indent} {
        margin-top: #{$indent}px !important;
      }
      .m-r-#{$key}-#{$indent} {
        margin-right: #{$indent}px !important;
      }
      .m-l-#{$key}-#{$indent} {
        margin-left: #{$indent}px !important;
      }
      .p-b-#{$key}-#{$indent} {
        padding-bottom: #{$indent}px !important;
      }
      .p-t-#{$key}-#{$indent} {
        padding-top: #{$indent}px !important;
      }
      .p-r-#{$key}-#{$indent} {
        padding-right: #{$indent}px !important;
      }
      .p-l-#{$key}-#{$indent} {
        padding-left: #{$indent}px !important;
      }
    }
  }
}
