.projectsDetail {

  .top-slider__imageBox {
    height: 41.6875rem;

    @media(max-width: 991px) {
      height: 35.4375rem; 
    }

    @media(max-width: 568px) {
      height: 32.5rem; 
    }
    
  }  
  
  .top-slider__imageBg { 
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    background: linear-gradient(0deg, rgba(0, 28, 52, 0.4), rgba(0, 28, 52, 0.4));
    @media(max-width: 991px) {
      height: 35.625rem;
    }
  }

  .top-slider__content {
    margin-top: 16.5625rem;

    @media(max-width: 991px) {
      margin-top: 9.4375rem;
    }
  }

  .top_slider__textContent {
    .extraH {
      font-size: 3.313rem;
      line-height: 3.313rem;

      @media(max-width: 1199px) {
        font-size: 1.875rem;
        line-height: 2.25rem;
      }
    }

    .top_slider__description {
      font-size: 1.0625rem;
      line-height: 1.375rem;
    }
  }

  .top-slider__pagination-container {
    top: 34.375rem;

    @media(max-width: 991px) {
      top: 27.4375rem;
    }

    .swiper-pagination-bullet {
      margin-right: 0;
    }
  }
}

.projectsDetail-list {
  margin-bottom: 4.375rem;

  display: flex;
  flex-direction: column;

  .projects-list__description {
    font-size: 1.125rem;
    line-height: 2rem;
  }

  @media (min-width: 992px) {
    &._one-child {
      flex-direction: row-reverse;
    }
  }

  @media(max-width: 991px) {
    margin-bottom: 3.125rem;
  }
  
  ol, ul {
    padding-left: 1.3rem;
}
}

.projectsDetail-info {
  padding: 1.875rem 0.9375rem;
  background: $card-bg;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  position: relative;
  margin-bottom: 4.375rem;

  &:after {
        content: '';
        position: absolute;
        background-image: url("images/catalog/catalog-info-pattern.svg");
        border-radius: 1.25rem;
        width: 100%;
        height: 100%;
        z-index: $lvl_-1;
        bottom: -0.9375rem;
        right: -0.9375rem;

        @media (min-width: 1680px) {
          background-size: 110%;
        }

        @media (max-width: 500px) {
            bottom: -0.8rem;
            right: -0.4rem;
            background-size: cover;
            background-image: url("images/catalog/catalog-info-pattern-mobile.svg");
        }
    }

    @media(max-width: 991px) {
        width: 100%;
        margin-bottom: 3.75rem;
    }
}

.projectsDetail-info__row {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;

  @media(max-width: 991px) {
    flex-direction: column;
    justify-content: flex-start;
    gap: 1.875rem;
  }
  
  .projects-info__item { 
    color: $dark;

    .projects-title {
      font-family: 'Gilroy', sans-serif;
      font-weight: 800;
      font-size: 1.625rem;
      line-height: 2.0125rem;
    }

    .projects-text {
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 1rem;
      line-height: 1.25rem;
      margin-top: 0.625rem;
    }

    .projects-icons {
      display: flex;
      gap: 0.625rem;
      margin-top: 0.9375rem;
      flex-wrap: wrap;
    
      .projects-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 6.375rem;
        height: 2.75rem;
      }
    
      img {
        max-width: 100%;
      }
    }
  }
}

.projectsDetail-list__description {
  font-size: 1.125rem;
  line-height: 1.975rem;
  font-weight: 400;
  font-family: 'Inter', sans-serif;
  color: $dark;
  display: inline-block;
}

.projectsDetail-list__ul { 
  color: $turquoise;
  font-size: 1rem;
  line-height: 1.6rem;

  li {
    margin-top: 1.25rem;
  }

  span {
    color: $dark;
   }

   .projectsDetail-list__li {
    margin-top: 0.625rem;
   }
}

.projectsDetail-list__tz-item {
  margin-top: 1.25rem;
  font-size: 1rem;
  line-height: 1.6rem;
  
  & > span:first-child {
    margin-right: 0.5rem;
    color: $turquoise;
  }

  .projectsDetail-list__ul {
    margin-left: 2.8125rem;
    @media(max-width: 991px) {
      margin-left: 1.875rem;
    }
  }
}

.projectsDetail__image-box {
  width: auto;
}

.projectsDetail__image {
  overflow: hidden;
  border-radius: 1.25rem;
  width: 350px;
  height: 240px;
  
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  & ~ span {
    font-size: 0.8125rem;
    line-height: 1rem;
    color: $turquoise;
    display: inline-block;
    max-width: 21.875rem;
    margin-top: 0.625rem;
  }

  @media(max-width: 1199px) {
    margin-top: 2.5rem;
  }

  @media(max-width: 767px) {
    width: auto;
    height: auto;
  }

  @media (max-width: 375px) {
    height: 240px;
  }
}

.projectsDetail-list__aside {
  font-family: "Inter", sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;
  margin-top: 2.5rem;

  &:first-child {
    padding-left: 2.5rem;
    position: relative;

    @media(max-width: 991px) {
      padding-left: 1.875rem;
    }

    @media(max-width: 767px) {
      padding-left: 1.25rem;
    }

    &::before {
      content: '';
      position: absolute;
      width: 0.3125rem;
      height: 100%;
      background: $blue_1;
      border-radius: 0.3125rem;
      left: 0;
    }
  }

  span {
    display: inline-block;
    &:not(:first-child) {
      margin-top: 1.875rem;
    }
  }
}

.projectsDetail-aside {
  h2 {
    margin: 4.375rem 0 2.5rem;
    @media (max-width: 991px) {
      margin: 2.5rem 0 1.25rem;
    }
  }
}

.projectsDetail-tabs__card {
  padding: 0;
  border-radius: 1.25rem;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  background: $card-bg;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: hidden;
  height: 25rem;
  transition: box-shadow 0.3s;

  .projectsDetail-tabs-img {
    height: 50%;
    img {
      width: 100%;
      object-fit: cover;
      height: 100%;
    }
  }

  &:not(:last-child) {
    margin-bottom: 1.25rem;
  }

  .title {
    display: block;
    margin: 0.625rem 0;
    font-family: "Gilroy", sans-serif;
    font-size: 1.25rem;
    font-weight: 800;
    line-height: 1.5rem;

    @media(max-width: 768px) {
      font-size: 1rem;
      font-weight: 700;
      line-height: 1.2rem;
    }
  }

  .projectsDetail-tabs__textInfo {
    padding: 1rem 1.875rem 2.3125rem;
    min-height: 12.5rem;

    span, li {
      font-family: "Inter", sans-serif;
      font-size: 0.813rem;
      line-height: 1rem;
      color: $turquoise;
    }

    ul {
      padding-left: 1.5rem;
    }

    .projectsDetail-tabs__tags {
      display: flex;
      
      span {
        padding: 0.125rem 0.625rem;
        border-radius: 0.375rem;
        margin-right: 0.625rem;  
        font-size: 0.6875rem;
        line-height: 0.813rem;
        font-family: 'Inter', sans-serif;
        background-color: #ddeffb;
        color: $blue_2;
      }

      @media(max-width: 380px){
        flex-wrap: wrap;
        span {
          margin-bottom: 0.625rem;
        }
      }
    }
    @media(max-width: 380px){
      min-height: 14.5rem;
      padding: 1rem 1.25rem 2.3125rem;
    }
  }

  &:hover {
    cursor: pointer;
    box-shadow: 0.425rem 0.425rem 0.9rem rgba(0, 0, 0, 0.2); 
  }
}

.projectsDetail-swiperCard {
  h2 {
    margin: 6.875rem 0 2.8125rem;
    @media (max-width: 991px) {
      margin: 3.5625rem 0 1.5625rem;
    }
  }
}

.projectsDetail-form__background {
  background: url('./images/calc_page/calc-result-background.png') no-repeat;
  background-size: cover;
  margin-bottom: -4.375rem;
  padding-top: 3.4375rem;

  @media(max-width: 991px){
    margin-bottom: -3.75rem;
  }

  @media (max-width: 767px) {
    margin-bottom: -2.5rem;
  }

  @media(max-width: 575px) {
    margin-bottom: -3.125rem;
  }

  @media(max-width: 380px) {
    background: url('./images/eventsDetail/projectsDetail-form__background.png') no-repeat;
    margin-bottom: -4.925rem;
    background-position: -7.5rem -1.875rem;
    padding-top: 4.75rem;
  }
}

.projectsDetail-form {
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  padding: 3.125rem 0 3.75rem 8.4375rem;
  background-color: $white;

  .projectsDetail-form__aside-text {
    margin-top: 1.25rem;
  }

  textarea {
    border-radius: 1.25rem !important;
    min-height: 5.375rem !important;
    padding: 0.9375rem 1.25rem !important;
  }

  @media (max-width: 1400px) {
    padding-left: 3.125rem;
  }

  @media(max-width: 767px) {
    padding: 1.875rem 1.25rem 3.375rem;

    textarea {
      min-height: 8.75rem !important;
    }
  }

  @media (min-width: 1200px) {
    textarea {
      width: 48.4375rem;
    }

    input {
      width: 24.6875rem;
    }
  }

  h2 {
    margin: 0;
  }
  
}

.projectsDetail-form__text {
  font-family: "Inter", sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;
  margin-top: 2.5rem;

  @media(max-width: 767px) {
    margin-top: 0.9375rem;
  }
}

.projectsDetail-form__input-title {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.6rem;
  margin: 1.25rem 0 0.1875rem;
}

.projectsDetail-form__aside-text {
  display: flex;
  margin-top: 1.5rem;
  font-size: 0.8125rem;


  @media(max-width: 767px) {
    margin-top: 1rem;
  }
}

.projectsDetail__button-submit {
  margin-top: 1.875rem;

  @media(max-width: 767px) {
    margin-top: 1.25rem;
  }
}
