.myEvents { 

  .event-tabs__hash {
    margin-bottom: 0;
  }

  .event-tabs__card:not(:last-child) {
    margin-bottom: 0rem;
  }

  .block-modal__content {
    max-width: 47.625rem;
    width: 100%;
    margin: 0.625rem;
  }

  .event-tabs__progressbar {
    .btn {
      font-family: 'Inter', sans-serif;
      font-weight: 600;
      font-size: 0.8125rem;
      line-height: 1rem;
      margin-top: 2.5rem;
    }
  }

  .block-modal__myEvents {
    .btn {
      font-weight: 600;
    }
  }
}

.block-modal__myEvents {
  display: flex;
  flex-direction: column;
  padding: 2.5rem 2.5rem 3.4375rem;
  width: 100%;
  height: fit-content;
  background: $card-bg;
  border-radius: 1.25rem;

  .title {
      font-family: 'Gilroy', sans-serif;
      font-weight: 800;
      font-size: 1.875rem;
      line-height: 2.25rem;
  }

  .subtitle {
    font-family: "Inter", sans-serif;
    font-weight: 700;
    font-size: 1rem;
    line-height: 1.6rem;
    margin: 1.4375rem 0 0.1875rem;
  }

  button {
    margin-top: 1.875rem;
  }

  @media(max-width: 568px){
    padding: 1.25rem;

    button {
      margin-top: 1.25rem;
    }
  }
}

