.file-icons {
  display: flex;
  gap: .75rem;
  flex-wrap: wrap;
}

.file-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1.9375rem;
  height: 2.5rem;
  fill: $primary;

  &:hover{
    fill: $link-hover-color;
  }

  svg{
    width: 100%;
    height: 100%;
  }
}

.socs {
  display: flex;
  flex-wrap: wrap;
}

a.soc {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  border-radius: 0.3125rem;
  background: $secondary;
  fill: $white;
  padding: 0.625rem;
  transition: background-color $transition;

  svg {
    width: 100%;
    height: 100%;
  }

  &:hover {
    background: $link-hover-color;
  }

  &:not(:last-child){
    margin-right: 0.625rem;
    margin-bottom: 0.625rem;
  }
}
