.documents__card-box {
  margin-top: 2.5rem;

  @media (min-width: 992px) {
    position: relative;
    width: 100%;
      
    &:after {
      content: '';
      position: absolute;
      background-image: url("images/catalog/catalog-info-pattern.svg");
      border-radius: 1.25rem;
      width: 100%;
      height: 100%;
      z-index: $lvl_-1;
      bottom: -0.9375rem;
      right: -0.9375rem;

      @media (min-width: 1680px) {
        background-size: 110%;
      }
    }
  }
}

.documents__card {
  border-radius: 1.25rem;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  background: $card-bg;
  overflow: hidden;
  position: relative;
  padding: 1.875rem 2.625rem 2.5rem 1.875rem;

  .text {
    font-family: "Inter", sans-serif;
    font-size: 1rem;
    line-height: 1.6rem;
    color: $dark;

    a {
      color: $blue_1;

      &:hover {
        color: $blue_2;
        cursor: pointer;
      }
    }
  }
}

.documents-tabs {
  margin: 2.125rem 0 4.375rem;
}

.documents-tabs__button {
  color: $turquoise;
  margin-right: 2.8125rem;
  padding-bottom: 0.3125rem;
  transition: all 0.3s ease;
  border-bottom: 5px solid transparent;
  margin-top: 1rem;

  &.active {
    color: $dark;
    border-color: $blue_2;
  }
}

.documents__selects {
  display: flex;
  align-items: flex-end;
  margin-top: 2.5rem;

  .documents__search {
    width: 30.625rem;
    margin-right: 1.875rem;
  }

  .documents__btn-wrap {
    display: flex;
    align-items: baseline;
  }

  .btn {
    &:not(:last-child) {
      margin-right: 1.875rem;
      border: 1px solid transparent;
    }
  }

  .ghost-btn {
    box-shadow: none;
    border: none;
    padding: 0;
    margin-bottom: 10px;
  }

  .select-wrapper {
    
    &.documents__system-type {
      width: 15.9375rem;
      margin-right: 1.875rem;
      
      .choices.is-open {

        .choices__inner {
          border-radius: 1.45rem 1.45rem 0 0;
        }

        .choices__list--dropdown.is-active {
          border-radius: 0 0 1.45rem 1.45rem;
        }
      }

      @media(max-width: 991px) {
        width: 100%;
      }
    }
  }

  .choices__list {

    .choices__item {
      font-size: 1rem;
      font-family: 'Inter', sans-serif; 
    }

    .choices__item[data-value="Выбрать"] {
      color: $turquoise_2;
    }

    .choices__item.choices__item--choice:first-child {
      display: none;
    }
  }

  .title {
    font-family: "Inter", sans-serif;
    font-weight: 700;
    font-size: 1rem;
    line-height: 1.6rem;
    margin-bottom: 0.1875rem;
  }

  @media(max-width: 991px) {
    width: 100%;
    flex-direction: column;
    align-items: flex-start;

    .documents__search {
      width: 100%;
      margin-bottom: 0.8125rem;
    }

    .documents__btn-wrap {

    }

    .documents__select-wrap {
      width: 100%;
      margin-bottom: 0.8125rem;
    }
  }
}

.documents-tabs__card {
  margin-top: 3.125rem;
  width: 100%;
  border-radius: 1.25rem;
  background: $card-bg;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  padding: 1.25rem 1.875rem 2.5rem;
  font-family: "Inter", sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;

  @media(max-width: 568px) {
    padding: 0;
  }
}

.documents-tabs__row {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  padding: 1.25rem 1.5rem 0.625rem 1.5rem;
  gap: 1.875rem;
  min-height: 6.25rem;
  border-radius: 1rem;

  &:first-child {
    color: $turquoise;
    min-height: 4.375rem;

    .documents-tabs__equipment {
      font-weight: 700;
    }
  }

  &:hover {
    background: rgba(239, 249, 255, 0.8);
    cursor: pointer;
  }

  @media(max-width: 991px) {
    flex-direction: column;
    gap: 1.25rem;
  }

  @media(max-width: 568px) {
    gap: 0.625rem;
  }
}

.documents-tabs__item {
  display: flex;
  gap: 1.875rem;
  justify-content: flex-start;
  width: 75%;

  @media(max-width: 991px) {
    width: 100%;
  }

  @media(max-width: 568px) {
    gap: 1rem;
  }
}

.documents-tabs__name {
  font-weight: 700;

  .name {
    color: $blue_1;
  }

  .size {
    font-weight: 400;
    margin-top: 0.3125rem;
  }
}


.documents-tabs__equipment {

  .item:not(:first-child) {
    margin-top: 5px;
  }

  .item span {
    span {
      background: $blue_2;
      margin-left: 0.3125rem;
    }
  }

  @media(max-width: 991px) {
    margin-left: 2.875rem;
  }

  @media(max-width: 568px) {
    margin-left: 2rem;
  }

  .question-description {
    background-color: $blue_2;

    .question-popup {
      min-width: 15.1rem;
    }
  }
}

.js-question-description {
  position: relative;

  &:hover {
    cursor: pointer;

    .question-popup {
      display: flex;
    }
  }
}

.question-description {
  font-family: "Inter", sans-serif;
  width: 1rem;
  height: 1rem;
  margin-left: 0.3125rem;
  color: $white;
  font-weight: 600;
  font-size: 0.6875rem;
  line-height: 0.8125rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding-top: 0.0625rem;
  border-radius: 50%;
  background: $turquoise;
}

.question-popup {
  position: absolute;
  border-radius: 1rem;
  background: $dark-gray;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.4);
  justify-content: center;
  align-items: center;
  padding: 0.6875rem 1.0625rem;
  font-size: 0.625rem;
  line-height: 0.75rem;
  color: $white;
  font-weight: 400;
  bottom: 40px;
  display: none;
  user-select: text;

  &:before {
    content: "";
    position: absolute;
    width: 0.75rem;
    height: 0.75rem;
    bottom: -5px;
    transform: rotate(45deg);
    background: $dark-gray;
  }
}

.documents-tabs__download {
  padding: 1.875rem 1.5rem;
  background: $dark-gray;
  border-radius: 0.5rem;
  min-height: 5rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  color: $white;
  

  .close-button {
    width: 1.25rem;
    height: 1.25rem;
    cursor: pointer;
    position: relative;
    margin-right: 0.4375rem;

    &:before, &:after {
      content: '';
      width: 100%;
      height: 0.0625rem;
      position: absolute;
      left: 0;
      top: 50%;
      background: $white;
    }

    &:before {
      transform: translateY(-50%) rotate(45deg);
    }

    &:after {
      transform: translateY(-50%) rotate(-45deg);
    }
  }

  .checkbox-counter {
    margin-right: 2.25rem;

    span {
      color: $white;
      font-weight: 700;
    }
  }

  .download-button {
    cursor: pointer;

    svg {
      margin-right: 0.4375rem;
      width: 1.1875rem;
      height: 1.375rem;
    }
  }

  .documents-tabs__download-wrap {
    display: flex;
    align-items: center;
  }

  @media(max-width: 991px) {
    flex-direction: column;
    gap: 0.625rem;
    align-items: flex-start;
    font-size: 13px;
    line-height: 16px;
  }
}