.recoveryPassword,
.changePassword {
    font-family: 'Inter', sans-serif;
    color: $dark;

    a {
        color: $blue_1;

        &:hover {
            color: $blue_1;
            cursor: pointer;
        }
    }

    .form-group {
        margin-bottom: 1.25rem;
    }

    .block-modal__content {
        max-width: 32rem;
        width: 100%;
        margin: 0.625rem;
    }
}

.recoveryPassword__subtitle,
.changePassword__subtitle {
    font-size: 1rem;
    line-height: 1.6rem;
    margin-top: 2.5rem;

    .text:not(:first-child) {
        margin-top: 0.3125rem;
    }
}

.recoveryPassword__form,
.changePassword__form {
    margin-top: 1.25rem;

    button {
        margin-top: 0.6rem;
    }
}

.profile,
.recoveryPassword {
    .custom-group-icon {
        right: 0.9375rem;
        cursor: pointer;

        svg {
            width: 1.375rem;
        }

        .icon-eye {
            .eye {
                display: block;
            }

            .eye-closed {
                display: none;
            }

            &.icon-eye-closed {
                .eye {
                    display: none;
                }
    
                .eye-closed {
                    display: block;
                }
            }
        }
    }
}

.success-notification {
    margin-bottom: 1.25rem;
    background-color: #E7F7EB;
    padding: 0.9375rem;
    border-radius: 1rem;
    display: flex;
    justify-content: space-between;
  
    svg {
      stroke: #0B8627;
      width: 1.5rem;
      height: 1.5rem;
      display: inline-block;
      margin-right: 0.625rem;
    }
  
    span {
      font-size: 1rem;
      line-height: 1.6rem;
      font-weight: 400;
      color: #0B8627;
      font-family: 'Inter', sans-serif;
    }
  
    &__message {
      margin-right: 0.9375rem;
    }
  }