.example-list {
  margin-bottom: 4.375rem;

  @media(max-width: 991px) {
    margin-bottom: 3.125rem;
  }
}

.example-list__description {
  font-size: 1.125rem;
  line-height: 1.975rem;
  font-weight: 400;
  font-family: 'Inter', sans-serif;
  color: $dark;
  display: inline-block;
  span {
    font-size: 1.125rem;
    font-weight: 700;
    line-height: 2rem;
    padding-left: 0.5rem;
  }
}

.example__image-box {
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  position: relative;
  

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 1.25rem;
    bottom: -0.625rem;
    right: -0.5625rem;
    background: url("./images/events/event-graph-background.png") center center no-repeat;
    background-size: cover;
    z-index: $lvl_-1;
  }

  @media(max-width: 991px) {
    margin-top: 2.5rem;
  }

  .wrap {
    background: $card-bg;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    border-radius: 1.25rem;
    padding: 1.875rem 1.875rem 2.5rem;

    .btn {
      padding: 0.625rem 0.9375rem;
      font-size: 0.8125rem;
    }
  }

  .title {
    margin: 0;
  }

  .text {
    font-family: "Inter", sans-serif;
    font-size: 1rem;
    color: $dark;
    margin: 1.25rem 0 1.875rem;
  }
}

.example-list__image {
  max-width: 100%;
  height: auto;
  border-radius: 1.25rem;
}

.example-list__image-description {
  color: $turquoise;
  font-size: 0.8125rem;
  line-height: 0.9812rem;
  display: inline-block;
  margin-top: 1.5625rem;
}

.example-tabs {
  margin-bottom: 6.25rem;
}

.example-tabs__buttons {
  margin-bottom: 2.5rem;
  display: flex;
}

.example-tabs__button {
  color: $dark;
  font-size: 0.8125rem;
  line-height: 0.9812rem;
  font-weight: 400;
  font-family: "Inter", sans-serif;
  padding: 0.4375rem 1.5625rem;
  border-radius: 1.25rem;
  background-color: #EFF3F6;
  width: max-content;
  transition: all 0.3s ease;
  border: none;

  &:not(:last-child) {
    margin-right: 0.625rem;
  }

  &.active {
    background-color: $blue_1;
    color: white;

    &:hover {
      background-color: $blue_2;
    }
  }

  @media(max-width: 991px) {
    margin-bottom: 0.625rem;
  }
}

.example-tabs__cards {
  &.active {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 1.875rem;
    grid-row-gap: 3.125rem;
  }

  @media(max-width: 1679px){
    grid-template-columns: repeat(3, 1fr)!important;
  }
  @media(max-width: 1199px){
    grid-template-columns: repeat(2, 1fr)!important;
  }
  @media(max-width: 991px){
    grid-template-columns: repeat(1, 1fr)!important;
  }

}

.example-tabs__card {
  padding: 0;
  border-radius: 1.25rem;
  background: $card-bg;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: hidden;
  height: 100%;
  transition: box-shadow 0.3s;

  img {
    object-fit: cover;
    width: 100%;
  }

  &:not(:last-child) {
    margin-bottom: 1.25rem;
  }

  .title {
    display: block;
    margin: 1rem 0;
    font-family: "Gilroy", sans-serif;
    font-size: 1.25rem;
    font-weight: 800;
    line-height: 1.5rem;

    @media(max-width: 768px) {
      font-size: 1rem;
      font-weight: 700;
      line-height: 1.2rem;
    }

    @media(max-width: 380px){
      margin: 0.5rem 0;
    }
  }

  .example-tabs__textInfo {
    padding: 1rem 1.875rem 0;
    min-height: 12.5rem;

    span, li {
      font-family: "Inter", sans-serif;
      font-size: 0.813rem;
      line-height: 1rem;
      color: $turquoise;
    }

    ul {
      padding-left: 1.5rem;
    }

    .example-tabs__tags {
      display: flex;
      
      span {
        padding: 0.125rem 0.625rem;
        border-radius: 0.375rem;
        margin-right: 0.625rem;  
        font-size: 0.6875rem;
        line-height: 0.813rem;
        font-family: 'Inter', sans-serif;
        background-color: #ddeffb;
        color: $blue_2;
      }

      @media(max-width: 380px){
        flex-wrap: wrap;
        span {
          margin-bottom: 0.625rem;
        }
      }
    }
  }

  &:hover { 
    box-shadow: 0.425rem 0.425rem 0.9rem rgba(0, 0, 0, 0.5);
    cursor: pointer;
    transition: all 0.3s ease;
    
    .title {
      color: $blue_2;
    }
  }
}