.audio__box{
  margin-top: 20px;
}

.audio__controls {

}

.audio__col {
  display: flex;
  flex-direction: column;
}

.audio__row {
  display: flex;
  align-items: center;
}

.audio__navigation-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: $milk_1;
  border-radius: 2.25rem;
  padding: 0.7688rem 1.375rem;
  gap: 1.5419rem;

  @media(max-width: 1199px){
    gap: 1rem;
  }

  @media(max-width: 991px){
    gap: 1.5419rem;
  }

  @media(max-width: 430px){
    gap: 1rem;
    padding: 0.7688rem;
  }

  @media(max-width: 360px){
    gap: 0.65rem;
  }
}

#audioBack,
#audioNext {
  display: flex;
  width: 0.79rem;
  height: 0.79rem;

  &:hover {
    cursor: pointer;
  }
}

#audioBack {
  svg {
    transform: rotate(180deg);
  }
}

#audioPlay {
  display: flex;
  width: 0.8313rem;
  height: 0.9375rem;
  
  &:hover {
    svg {
      fill: $blue_2;
      cursor: pointer;
    }
  }

  .play-svg {
    display: block;
  }

  .pause-svg {
    display: none;
  }

  &.__play {
    .play-svg {
      display: none;
    }
  
    .pause-svg {
      display: block;
    }
  }
}

.audio__speed {
  margin-left: 1.0625rem;
  min-width: 1.5rem;
  height: 1.5rem;
  border-radius: 0.25rem;
  background: $blue_1;
  display: flex;
  align-items: center;
  justify-content: center;
  
  span {
    font-family: 'Inter', sans-serif;
    font-size: 13px;
    line-height: 16px;
    color: $white;
  }

  &:hover {
    background: $blue_2;
    cursor: pointer;
  }

  @media(max-width: 360px){
    margin-left: 0.65rem;
  }
}

.audio__volume {
  margin-left: 2.7738rem;
  display: flex;
  align-items: center;
  min-width: 4rem;
  width: 4.6875rem;
  justify-content: space-between;

  @media(max-width: 1199px){
    margin-left: 1.0625rem;
  }

  @media(max-width: 991px){
    margin-left: 2.7738rem;
  }

  @media(max-width: 430px){
    margin-left: 1.0625rem;
  }

  @media(max-width: 360px){
    margin-left: 0.65rem;
  }
}

#audio__volume-img {
  display: flex;
  width: 1.3362rem;
  height: 1.1944rem;

  &:hover {
    cursor: pointer;
  }

  svg {
    display: none;
    width: 1.3362rem;
    height: 1.1944rem;
  }

  &.__high:not(.__mute) {
    .high-svg {
      display: block;
    }
  }

  &.__low:not(.__mute) {
    .low-svg {
      display: block;
    }
  }

  &.__mute {
    .mute-svg {
      display: block;
    }
  }
}



#audio__volume-bar-controls {
  width: 2.5rem;
  height: 1rem;
  display: flex;
  align-items: center;

  &:hover {
    cursor: pointer;
  }
}

#audio__volume-bar {
  background: rgba(206, 234, 252, 0.8);
  border-radius: 4px;
  width: 2.5rem;
  height: 0.15rem;
}

#audio__volume-level {
  height: 0.15rem;
  background: $blue_1;
  text-align: center;
  width: 32px;
  border-radius: 4px;
}

.audio__duration-wrap {
  margin-top: 1.25rem;
}

#audio__duration-bar-controls {
  width: 100%;
  height: 1rem;
  display: flex;
  align-items: center;

  &:hover {
    cursor: pointer;
  }
}

#audio__duration-bar {
  background: rgba(206, 234, 252, 0.8);
  border-radius: 4px;
  width: 100%;
}

#audio__position-bar {
  height: 0.15rem;
  background: $blue_1;
  text-align: center;
  width: 0;
  border-radius: 4px;
  position: relative;

  span {
    width: 1.03rem;
    height: 1rem;
    border: 0.125rem solid $white;
    border-radius: 50%;
    position: absolute;
    background: $blue_1;
    right: -10px;
    top: -7px;
  }
}

.audio__display {
  display: flex;
  justify-content: space-between;
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;
  color: $turquoise;
  margin-top: 0.4375rem;
}
