.registration {
    font-family: 'Inter', sans-serif;
    color: $dark;
    display: flex;
    gap: 4.375rem;

    a {
        color: $blue_1;

        &:hover {
            cursor: pointer;
            color: $blue_2;
        }
    }

    .form-group {
        margin-bottom: 1.25rem;
    }

    .block-modal__content {
        max-width: 32rem;
        width: 100%;
        margin: 0.625rem;
    }

    @media(max-width: 991px){
        flex-direction: column;
    }
}

.registration__subtitle {
    margin-top: 2.5rem;

    .text:not(:first-child) {
        margin-top: 0.3125rem;
    }
}



.registration__form {
    margin-top: 2.5rem;
}

.registration__info {
    display: flex;
    align-items: flex-start;
    margin-bottom: 1.875rem;
    
    a,
    .text {
        font-size: 0.8125rem;
        line-height: 1rem;
    }

    .text {
        align-self: center;
    }

    svg {
        width: 1.2775rem;
        height: 1.3331rem;
        flex-shrink: 0;
        margin-right: 0.75rem;
        fill: $blue_light;
    }
}

.profile-desc__wrap,
.registration-desc__wrap {
    box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
    border-radius: 1.25rem;
    position: relative;
  
    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      bottom: -0.625rem;
      right: -0.5625rem;
      background: url("./images/events/event-graph-background.png") center center no-repeat;
      background-size: cover;
      z-index: $lvl_-1;
      border-radius: 1.25rem;
    }
  
    .wrap {
      background: $card-bg;
      border-radius: 1.25rem;
      padding: 1.875rem 1.875rem 2.5rem;
    }              
}

.registration-desc__img {
    width: 100%;

    img {
        width: 100%;
    }
}

.registration-desc__title {
    font-family: 'Gilroy', sans-serif;
    font-size: 1.625rem;
    line-height: 1.95rem;
    font-weight: 800;
    margin-top: 2.9375rem;
}

.registration-desc__ul {
    margin-top: 0.625rem;

    li {
        font-size: 1rem;
        line-height: 1.6rem;
    }
}

.registration-desc__text {
    font-size: 0.8125rem;
    line-height: 1rem;
    margin-top: 1.875rem;
}

.block-modal { 
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.3);
    opacity: 0;
    visibility: hidden;
    display: flex;
    justify-content: center;
    align-items: center;

    &.active {
        opacity: 1;
        visibility: visible;
    }

    &__content {
        position: relative;
        display: flex;
        max-height: 100%;
    }

    &__close {
        position: absolute;
        top: 2.625rem;
        right: 2.625rem;
        width: 1.25rem;
        height: 1.25rem;
        border: none;
        background-color: transparent;
        padding: 0;

        svg {
            fill: $blue_1;
            width: 100%;
            height: 100%;
            pointer-events: none;
        }

        @media(max-width: 568px) {
            top: 1.8rem;
            right: 1.8rem;
        }
    }
}

.block-modal__wrap {
    width: 100%;
    height: fit-content;
    padding: 1rem;
}

.block-modal__authorization {
    display: flex;
    flex-direction: column;
    padding: 2.5rem 2.5rem 3.4375rem;
    width: 100%;
    height: fit-content;
    background: $card-bg;
    border-radius: 1.25rem;

    .title {
        font-family: 'Gilroy', sans-serif;
        font-weight: 800;
        font-size: 1.875rem;
        line-height: 2.25rem;
    }

    a,
    .authorization__info,
    .subtitle {
        font-size: 0.8125rem;
        line-height: 1rem;
    }

    .authorization__info,
    .subtitle {
        color: $turquoise;

        span {
            color: $turquoise;
        }
    }

    .subtitle {
        margin: 1.5625rem 0 1.25rem;
    }

    .authorization__info {
        margin-bottom: 1.875rem;
    }

    @media(max-width: 568px) {
        padding: 1.25rem;
    }
}