.top-slider__pagination-content {
  display: flex;
  justify-content: flex-end;
  align-items: center;

  .rounded-pagination {
    bottom: 0 !important;
    width: max-content !important;
  }

  @media(max-width: 991px) {
    justify-content: center;
  }
}

.top-slider__buttons {
  display: flex;
  margin-left: 6.75rem;

  @media(max-width: 991px) {
    display: none;
  }
}

.top-slider__button {
  background-color: rgba(196, 196, 196, 0.4);
  width: 2.375rem;
  height: 2.375rem;
  border-radius: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  svg {
    fill: $white;
    width: 0.3125rem;
    height: 0.625rem;
  }

  &._prev {
    transform: rotate(180deg);
    margin-right: 1rem;
  }

  &.swiper-button-disabled {
    background-color: rgba(196, 196, 196, 0.2);

    svg {
      fill: rgba(255, 255, 255, 0.5);
    }
  }
}

.index-top-slider {
  position: relative;
  overflow: visible;
  min-height: 41.875rem;

  @media(max-width: 991px){
    min-height: auto;
  }
}

.top-slider__imageBox {
  width: 100%;
  height: 41.875rem;
  position: absolute;
  top: 0;
  -webkit-mask: url('./images/index/index-top_slider-mask_02.svg') no-repeat 100% 100%;
  mask: url('./images/index/index-top_slider-mask_02.svg') no-repeat 100% 100%;
  -webkit-mask-size: cover;
  mask-size: cover;

  @media (max-width: 500px) {
    -webkit-mask: url('./images/index/index-top_slider-mask_03.svg') no-repeat 100% 100%;
    mask: url('./images/index/index-top_slider-mask_03.svg') no-repeat 100% 100%;
    -webkit-mask-size: cover;
    mask-size: cover;
  }
}

.top-slider__imageBg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background: linear-gradient(180deg, rgba(0, 88, 161, 0.2) 27.62%, rgba(0, 88, 161, 0) 57.15%), linear-gradient(0deg, rgba(0, 88, 161, 0.5), rgba(0, 88, 161, 0.5));

  @media(max-width: 991px) {
    height: 35.625rem;
  }
}

.top-slider__image {
  width: 100%;
  height: 100%;
  object-fit: cover;

  @media(max-width: 991px) {
    mask: none;
    height: 35.625rem;
  }
}

.slider-item {
opacity: 0;
  &:not(.swiper-slide-visible) {
    opacity: 1;
    visibility: hidden;
    transition: all 0.5s ease;
  }
}

.top-slider__content {
  max-width: 48.125rem;
  position: relative;
  z-index: $lvl_0;
  margin-bottom: 5.625rem;
  height: 16.875rem;
  overflow: hidden;

  .solution__link{
    margin-bottom: 1.875rem;
  }

  @media(max-width: 991px) {
    margin-bottom: 4.375rem;
    height: 15.625rem;
  }
}

.solution__link {
  display: flex;
  max-width: 100%;
  background-color: $blue_1;
  border-radius: 1.875rem;
  width: max-content;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(20, 20, 20, 0.18);
  font-size: 0.8125rem;
  line-height: 0.9831rem;

  @media(max-width: 568px){
    box-shadow: none;
  }


  .slider_item__big-string {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-start;

    .slider_item__first-row {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-right: 0.3rem;
    }

    .slider_item__first-img {
      display: none;
    }

    @media(max-width: 465px) {
      align-items: flex-start;
      width: min-content;
      
      .slider_item__first-row {
        width: 100%;
        margin-right: 0rem;
      }

      .slider_item__first-img {
        display: block;
      }

      .slider_item__secong-img {
        display: none;
      }
    }

      
  }
    

  svg {
    display: inline-block;
    margin-left: 0.625rem;
    width: 1.1875rem;
    height: 0.8125rem;
    fill: $white;
  }
  
  span {
    color: white;
  }
}

.slider_item__solution {
  border-radius: 1.875rem;
  background-color: $black;
  font-size: 0.8125rem;
  line-height: 0.9831rem;
  font-family: "Inter", sans-serif;
  color: white;
  padding: 0.4063rem 1.0625rem 0.4688rem 0.875rem;
  display: flex;
  align-items: center;
}

.slider_item__all-solution {
  color: white;
  font-family: 'Inter', sans-serif;
  font-size: 0.8125rem;
  line-height: 1rem;
  margin-left: 0.6563rem;
  padding: 0.4063rem 0.9063rem 0.4688rem 0.9375rem;

  svg {
    fill: rgba(255, 255, 255, 0.7);
    width: 0.625rem;
    height: 0.3937rem;
  }

  &:hover{
    color: $white;
  }

  @media(max-width: 568px){
    margin-left: 0;
    background-color: $blue_1;
    border-radius: 3.125rem;
  }
}

.top_slider {

  @media(max-width: 991px) {
    padding-bottom: 2.5rem;
  }
}

.top_slider__textContent {
  .extraH {
    color: white;
    font-size: 4.0625rem;
    line-height: 4.0625rem;
    margin-top: 0;
    margin-bottom: 0;


    @media(max-width: 1199px) {
      font-size: 1.875rem;
      line-height: 2.25rem;
    }

    .h2 {
      display: block;
    }
  }
}

.top_slider__description {
  font-size: 1.0625rem;
  line-height: 1.3813rem;
  font-family: "Inter", sans-serif;
  color: white;
  margin-top: 1.9375rem;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  @media(max-width: 991px) {
    font-size: 0.875rem;
    line-height: 1.1375rem;
  }
  @media(max-width: 568px) {
    margin-top: 1.25rem;
  }
}

.solution_thumbs__container{
  max-width: 100%;
  display: flex;
  align-items: center;

  @media(max-width: 991px){
    flex-direction: column-reverse;
    align-items: unset;
  }
}

.solution_thumbs {
  position: relative;

  .solution__link {
    svg {
      transform: rotate(180deg);
    }
  }

  @media(max-width: 991px) {
    .solution__link {
      display: none;
    }
  }
}

.solution_thumbs__title {
  top: 1.25rem;
  font-size: 1rem;
  line-height: 1.6rem;
  font-weight: 700;
  font-family: "Inter", sans-serif;
  color: $white;

  &.__all{
    display: none;
  }

  @media(max-width: 991px) {
    position: static;
    margin-bottom: 0.625rem;
    &.__thumbs{
      display: none;
    }
    &.__all{
      display: inline-block;
    }
  }
}

.solution_thumbs__items {
  margin-top: 1.25rem;
  min-width: 0;
  //max-width: 1190px;
  padding-bottom: 1.25rem;

  @media(max-width: 992px) {
    margin-top: 0.6875rem;
    padding-top: 0;
  }

  .swiper-slide {
    pointer-events: auto !important;
  }
}

.solution_thumbs__objects {
  display: flex;
  align-items: center;
  min-width: 0;
  position: relative;

  @media(max-width: 991px) {
    flex-direction: column;
    align-items: inherit;
  }

  .swiper{
    padding: 0 0.33rem;
    padding-right: 1.4rem;
  }
}

.solution_pagination_container {
  position: absolute;
  bottom: -3.125rem;
  justify-content: center;
  height: 2.1875rem;
  width: 100%;
  display: none;

  .swiper-pagination {
    display: flex;
    justify-content: center;
  }

  @media(max-width: 991px) {
    display: flex;
  }
}

.solution_thumbs__item {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 1.25rem;
  min-height: 16.25rem;
  background: $card-bg;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  min-width: 0;
  cursor: pointer;

  &.__margin {
    margin-right: 1.6875rem;
  }

  .h6 {
    display: block;
    margin-bottom: 0.3125rem;

    a{
      font-family: "Gilroy", sans-serif;
      color: $black;
      transition: all 0.3s ease;
    }
  }

  &:hover{
    box-shadow: 0.425rem 0.425rem 0.9rem rgba(0, 0, 0, 0.5);
    a{
      color: $blue_2;
      transition: all 0.3s ease;
    }
  }

  @media(max-width: 991px) {
    height: auto;
    h6 {
      font-size: 0.8125rem;
      line-height: 0.9812rem;
    }

  }
  @media(max-width: 568px) {
    padding-bottom: 1.25rem;
  }
}

.solution_thumbs__imgBox {
  max-width: 10.5rem;
  max-height: 7.5rem;
  margin: 0 auto 1.625rem auto;
}

.solution_thumbs__image {
  max-width: 100%;
  height: auto;
}

.solution_thumbs__description {
  font-size: 0.8125rem;
  line-height: 0.9831rem;
  color: $turquoise;
  font-family: 'Inter', sans-serif;

  @media(max-width: 991px) {
    font-size: 0.6875rem;
    line-height: 0.8319rem;
  }
}

.thumb_sum {
  position: absolute;
  right: -1.3125rem;
  top: 50%;

  @media(max-width: 1679px) {
    display: none;
  }

  svg {
    stroke: #9FBED3;
    display: inline-block;
    width: 1rem;
    height: 1rem;
  }
}

.solution_thumbs__item.swiper-slide-active,
.solution_thumbs__item.swiper-slide-next {
  .thumb_sum {
    display: block;
  }
}

.solution_thumbs__finally {
  position: relative;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  max-width: 22rem;
  margin-left: 0.4rem;

  .thumb_equal {
    position: absolute;
    left: -1.5rem;
    top: 53%;

    svg {
      stroke: #9FBED3;
      display: inline-block;
      width: 0.9375rem;
      height: 0.625rem;
    }

    @media(max-width: 991px) {
      top: auto;
      left: 48%;
      bottom: -2.25rem;
    }
  }

  @media(max-width: 991px) {
    display: flex;
    align-items: center;
    background-color: white;
    border-radius: 0.625rem;
    padding-left: 0.625rem;
    .solution_thumbs__finnalyBox {
      margin-right: 1.125rem;

      .solution_thumbs__finnalyImage {
        width: 3.75rem;
        height: 3.75rem;
        border-radius: 0.625rem;
      }
    }
  }

  @media(max-width: 991px) {
    max-width: 100%;
    margin-left: 0;
    margin-bottom: 1.875rem;
  }
}

.solution_thumbs__finally {
  background: $card-bg;

  &:hover{
    box-shadow: 0.425rem 0.425rem 0.9rem rgba(0, 0, 0, 0.5);
    cursor: pointer;
    transition: all 0.3s ease;
      a{
        color: $blue_2;
      }
  }
}

.solution_thumbs__finnalyImage {
  max-width: 100%;
  height: auto;
  border-top-left-radius: 1.25rem;
  border-top-right-radius: 1.25rem;
}

.solution_thumbs__finnalyText {
  padding: 1.5rem 1.1875rem 1.5rem 1.1875rem;
  border-radius: 0 0 1.25rem 1.25rem;

  .h6 {
    display: block;
    margin-top: 0;
    font-weight: 700;
  }

  @media(max-width: 991px) {
    padding: 0.875rem 1rem 0.8125rem 0;
    border-top-right-radius: 0.625rem;
    border-bottom-right-radius: 0.625rem;

    .solution_thumbs__tag {
      font-size: 0.8125rem;
      line-height: 0.9812rem;
    }
    h6 {
      font-size: 0.8125rem;
      line-height: 0.9812rem;
    }
  }
  @media(max-width: 568px) {
    max-width: 11.6875rem;
  }
}

.solution_thumbs__tag {
  margin-bottom: 0.3125rem;

  a{
    color: $turquoise;
    font-size: 0.8125rem;
    line-height: 0.9375rem;
    display: inline-block;
    transition: all 0.3s ease;

    &:first-child{
      padding-right: 0.3125rem;
    }

    &:not(:first-child){
      padding: 0 0.3125rem;
    }

    &:not(:last-child){
      border-right: 0.0625rem solid $turquoise;
    }

    &:hover{
      color: $blue_2;
    }
  }
}

.equipment-desk {

  .h1 {
    display: block;
    max-width: 57.5rem;
    margin-bottom: 1.875rem;
    font-weight: 800;

    @media(max-width: 375px){
      font-size: 1.5625rem;
      line-height: 1.875rem;
    }
  }

  @media(max-width: 1399px) {
    margin-bottom: 8.125rem;
  }

  @media(max-width: 680px) {
    margin-bottom: 3.125rem;
  }
}
