.gallery {
	display: block;
	border-radius: $border-radius;
	overflow: hidden;
	position: relative;

	&::before {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		opacity: 0;
		background-color: $black;
		transition: opacity $transition;
	}

	&::after {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		opacity: 0;
		background-image: url(./images/tech-components-hover_response_loupe.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: 100%;
		width: 3.5625rem;
		height: 3.5rem;
	}

	&:hover::before {
		opacity: .2;
	}

	&:hover::after {
		opacity: 1;
	}
}

.video-gallery {
	display: block;
	position: relative;

	&::before {
		content: '';
		width: 15rem;
		height: 15rem;
		border-radius: 50%;
		background-color: $body-color;
		opacity: .5;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		@media (max-width: $screen-md - 1) {
			width: 7.5rem;
			height: 7.5rem;
		}
	}

	&::after {
		content: '';
		font-size: 12rem;
		color: $white;
		position: absolute;
		background-image: url(./images/tech-components-hover_response_loupe.svg);
		background-repeat: no-repeat;
		background-position: center;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		transition: color $transition;

		@media (max-width: $screen-md - 1) {
			font-size: 6rem;
		}
	}

	&:hover {
		&::after {
			color: #FF9B06;
		}
	}
}
