.form-file {
  --bs-form-file-height: 2.375rem;
  position: relative;
  margin-bottom: 1.25rem;
}

.form-file-input {
  position: relative;
  z-index: $lvl_1;
  width: 100%;
  margin: 0;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  min-height: 2.875rem;


  &[disabled] ~ .form-file-label,
  &:disabled ~ .form-file-label{
    cursor: default;
  }

  &[disabled] ~ .form-file-label .form-file-text,
  &:disabled ~ .form-file-label .form-file-button{
    background-color: #e9ecef;
    border-color: $milk_2;
  }
}

.form-file-label {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: $lvl_1;
  display: flex;
  cursor: pointer;
  height: 100%;
  border-color: $milk_2;
  border-style: solid;
  border-width: 0.0625rem;
  border-radius: 1.875rem;
  transition: all 0.3s ease;

  &:hover{
    border-color: $blue_2;
    transition: all 0.3s ease;
  }

  &.hover{
    border-color: $blue_2;
  }

  &.form-alert{
    border-color: $red;
  }

  &.disabled{
    .form-file-button{
      background-color: $light_1;
      color: $turquoise_2;
    }
  }
}

.form-file-text {
  display: flex;
  align-items: center;
  flex-grow: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  background-color: $white;
  border-left: 0.0625rem solid $milk_2;
  border-radius: 1.875rem 0 0 1.875rem;
  padding: 0.5313rem 0.4375rem 0.5313rem 1.3125rem;
  color: $turquoise_2;
  font-size: 1rem;
  line-height: 1.6rem;
  font-family: "Inter", sans-serif;
}

.form-file-button {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  padding: 0.375rem 0.75rem;
  margin-left: -0.0625rem;
  border-style: solid;
  border-width: 0.0625rem;
  border-radius: 1.875rem;
  font-size: 1rem;
  line-height: 1.21rem;
  color: $white;
  background-color: #00569E;
  border-color: transparent;
}

