.requisites{

  a {
    margin-top: 2.5rem;
  }
}

.requisites__items {
  margin-top: 2.5rem;
}

.requisites__item {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  border-top: 0.0625rem solid $cyan;

  @media(max-width: 991px) {
    flex-direction: column;
  }
}

.requisites__item:last-child {
  border-bottom: 0.0625rem solid $cyan;
}

.requisites__text {
  font-family: "Inter", sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;
  padding: 0.9375rem 0 1.25rem 0;
}

.requisites__text:first-child {
  min-width: 23.75rem;

  @media(max-width: 991px) {
    padding-bottom: 0;
  }
}

.requisites__text:last-child {
  text-transform: uppercase;
}

.requisites__title {
  font-family: 'Gilroy', sans-serif;
  font-weight: 800;
  font-size: 2.8125rem;
  line-height: 3.0938rem;
  margin-top: 4.375rem;

  @media(max-width: 991px) {
    margin-top: 2.5rem;
  }
}