.audio_player{
  position: absolute;
  right: 0;
  top: 7.875rem;
  background-color: $blue_1;
  border-radius: 1.25rem 0 0 1.25rem;
  padding-left: 0.1875rem;
  padding-bottom: 0.1875rem;
  z-index: $lvl_2;
  width: 27.1875rem;

  &._closed{
    width: 5.3125rem;
    height: 4.9375em;
    background-color:#1074C7;
    object-fit: cover;
    .audio_player__header{
      height: 100%;
      flex-direction: column;
      padding: 0;
      justify-content: center;
      align-items: center;
      margin-bottom: 0;
    }

    .audio_player__visual{
      height: 1.6875rem;
      margin-right: 0;
      margin-bottom: 0.25rem;
      margin-top: 0.625rem;
    }

    .audio_player__title{
      font-size: 0.75rem;
      line-height: 0.9rem;
      text-align: center;
    }
  }

  @media(max-width: 768px){
    display: none;
  }
}

.audio_player__waves{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: $lvl_0;
  object-fit: none;
  border-radius: 1.25rem 0 0 1.25rem;
}

.audio_player__header{
  display: flex;
  align-items: center;
  padding-top: 0.75rem;
  padding-left: 0.9375rem;
  margin-bottom: 0.375rem;
  height: 2.875rem;
  cursor: pointer;
  width: 100%;
}

.audio_player__body{
  background-color: $white;
  border-radius: 1.25rem 0 0 1.25rem;
  padding: 1.125rem 1.75rem 1.3125rem 0.9375rem;
  position: relative;
  overflow: hidden;

  &._hidden{
    padding: 0;
    visibility: hidden;
    height: 0;
  }
}

.audio_player__item{
  &:not(:last-child){
    margin-bottom: 1.125rem;
  }
}

.audio_player__visual{
  display: flex;
  margin-right: 0.75rem;
  align-items: center;
  position: relative;
}

.audio_player__visual-item{
  width: 0.125rem;
  border-radius: 1.25rem;
  background-color: $white;
  transition: all 0.3s ease;

  &:not(:last-child){
    margin-right: 0.1875rem;
  }

  &:nth-child(1){
    height: 0.9375rem;
  }

  &:nth-child(2){
    height: 1.4375rem;
  }
  &:nth-child(3){
    height: 0.75rem;
  }

  &:nth-child(4){
    height: 1.1875rem;
  }

  &:nth-child(5){
    height: 1.6875rem;
  }

  &:nth-child(6){
    height: 0.625rem;
  }

  &:nth-child(7){
    height: 0.875rem;
  }

  &:nth-child(8){
    height: 0.5625rem;
  }

  &:nth-child(9){
    height: 0.1875rem;
  }

  &.__1{
    height: 0.9375rem;
  }
  &.__2{
    height: 1.4375rem;
  }
  &.__3{
    height: 0.75rem;
  }
  &.__4{
    height: 1.1875rem;
  }
  &.__5{
    height: 1.6875rem;
  }
  &.__6{
    height: 0.625rem;
  }
  &.__7{
    height: 0.875rem;
  }
  &.__8{
    height: 0.5625rem;
  }
  &.__9{
    height: 0.1875rem;
  }
}

.audio_player__title{
  color: $white;
  font-family: "Inter", sans-serif;
  font-size: 1rem;
  line-height: 1.2rem;
  font-weight: 700;
  position: relative;
}

.top-slider__pagination-container {
  position: absolute;
  height: 2.1875rem;
  width: 100%;
  top: 28.75rem;
  z-index: $lvl_1;


  @media(max-width: 568px) {
    top: 25rem;
  }
}

.audio-player {
  display: flex;
  max-width: 25rem;

  .play-btn {
    min-width: 2rem;
    min-height: 2rem;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    border: 0.125rem solid rgba(19, 76, 223, 0.5);
    justify-content: center;
    align-items: center;
    stroke: rgba(19, 76, 223, 0.5);
    cursor: pointer;
    display: flex;
    margin-right: 0.625rem;

    svg {
      width: 0.625rem;
      height: 0.625rem;
    }

    .stop-icon {
      display: none;
    }

    &.stop {
      border-color: $blue_1;
      background-color: $blue_1;
      stroke: $white;

      .stop-icon {
        display: block;
      }

      .play-icon {
        display: none;
      }
    }
  }

  &__time {
    color: $turquoise;
    margin-right: 0.625rem;
  }

  &__name {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
  }

  .track-wrapper {
    height: 0.3125rem;
    background: #E1E1E1;
    border-radius: 0.375rem;
    margin-top: 0.625rem;
    cursor: pointer;
    --width: 0;

    .track {
      width: var(--width);
      height: 100%;
      border-radius: 0.375rem;
      background: $blue_1;
    }
  }
}

