.equipment-items {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
  overflow: hidden;

  @media(max-width: 1300px) {
    align-items: flex-start;
  }

  @media(max-width: 991px){
    justify-content: space-around;
  }

  @media(max-width: 767px) {
    flex-direction: column;
   align-items: flex-start;
    flex-wrap: nowrap;
  }
}

.equipment-item__imgBox {
  position: relative;
  width: 100%;
}

.equipment-item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 40%;

  .equipment-item__image {
    max-width: 100%;
    max-height: 100%;
  }

  @media(max-width: 767px){
    width: 100%;
  }

  &._first {
    margin-bottom: 12.5rem;
    height: 31.25rem;

    .equipment-item__imgBox {
      height: 18.375rem;
      @media(max-width: 767px) {
        height: 12rem;
      }
    }

    .equipment-item__image {
      position: absolute;
      top: 24%;
      max-width: 115%;
      
      @media(min-width: 768px) {
        right: -21%;  
      }
    }

    .equipment-item__bgBox {
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      svg {
        width: 100%;
        height: 100%;
      }

      @media(max-width: 1250px){
        top: -20%;
      }

      @media(max-width: 767px) {
        left: -8%;  
      }
    }

    .equipment-item__text {
      max-width: 25.5rem;
      padding-bottom: 2.5rem;
      
      h4 {
        font-weight: 800;
      }

      @media(max-width: 568px) {
        padding: 1.5625rem 0.9375rem 2.5rem 0;
      }
    }

    @media(max-width: 1199px) {
      margin-bottom: 0rem;
    }
  }

  &._second {
    margin-bottom: 7.5rem;

    .equipment-item__imgBox {
      width: 32.0625rem;
      height: 24.5rem;

      @media(max-width: 1199px){
        width: 28.5rem;
      }

      @media(max-width: 991px){
        width: 19.375rem;
      }
      @media(max-width: 767px){
        width: 100%;
        height: 16.25rem;
      }
    }

    .equipment-item__image {
      position: absolute;
      top: -1.25rem;
      left: -1.875rem;
      @media(max-width: 680px) {
        left: -0.625rem;
      }

    }

    .equipment-item__bgBox {
      width: 25.0625rem;
      height: 23.375rem;
      margin-top: 2.2rem;

      svg {
        width: 100%;
        height: 100%;
      }

      @media(max-width: 991px){
        width: 17.8125rem;
      }

      @media(max-width: 767px){
        width: 100%;
      }

      @media(max-width: 680px) {
        width: 16.6875rem;
        height: 18.75rem;
        left: 23%;
      }
    }

    .equipment-item__text {
      max-width: 24.3125rem;
      margin-top: 1rem;

      @media(max-width: 991px) {
        padding-left: 0;
      }
    }

    @media(max-width: 1199px) {
      margin-bottom: 11.25rem;
    }

    @media(max-width: 1300px) {
      .equipment-item__image {
        left: 0;
      }
    }
  }

  &._third {
    .equipment-item__imgBox {
      height: 14.625rem;
      z-index: $lvl_1;

      .equipment-item__image {
        max-width: 90%
      }

      @media(max-width: 1300px){
        height: 10.875rem;
      }
      @media(max-width: 991px){
        height: 7.5rem;
      }
      @media(max-width: 768px){
        height: 5.625rem;
      }
    }
    .equipment-item__bgBox {
      width: 33.3125rem;
      height: 24.8125rem;
      top: -43%;
      right: -24%;
      z-index: $lvl_0;

      svg {
        width: 100%;
        height: 100%;
      }

      @media(max-width: 1300px){
        height: 20.6875rem;
        width: 27.8125rem;
        right: -15%;
        top: -46%;
      }

      @media(max-width: 991px){
        height: 12.5rem!important;
        width: 18.9375rem!important;
        right: -17%!important;
        top: -30%!important;
      }

      @media(max-width: 679px){
        width: 17.5rem!important;
      }

      @media(max-width: 568px){
        height: 17.375rem;
        width: 18.4375rem;
        right: -15%;
        top: -33%;
      }
    }

    .equipment-item__text{
      align-self: flex-start;
      
      @media(min-width: 1320px){
        margin-top: 1rem;
        padding: 1rem 0 0 0;
      }

      @media(max-width: 679px){
        padding-left: 0;
      }
    }

    @media(max-width: 1199px) {
      margin-bottom: 6.25rem;
    }
  }

  &._fourth {
    margin-top: -2rem;

    .equipment-item__imgBox {
      height: 18rem;

      @media(max-width: 768px){
        height: 13.5625rem;
      }

      @media(max-width: 500px){
        margin-bottom: 1rem;
      }
    }

    .equipment-item__bgBox {
      margin-left: 2.5rem;
      align-self: flex-start;
      height: 20rem;

      @media(max-width: 680px){
        height: 13.3125rem;
      }
    }

    .equipment-item__image {
      position: absolute;
      left: -20%;
      margin-top: 0.5rem;
    }

    .equipment-item__text {
      margin-top: 2rem;
      padding-top: 1.25rem;
      width: 100%;

      @media(max-width: 991px) {
        padding-top: 0;
        padding-left: 0;
      }

      h4 {
        margin-left: -0.625rem;
        @media(max-width: 991px) {
          margin-left: 0;
        }
      }
    }

    @media(max-width: 680px) {
      .equipment-item__image {
        left: -15%;
      }
    }

    @media(max-width: 679px){

      .equipment-item__bgBox{
        right: -20%;
      }
    }
  }

  .equipment-item__bgBox {
    width: 20.6875rem;
    height: 17.875rem;
    position: absolute;
    z-index: $lvl_-1;

    svg {
      stroke: #CCDEEC;
      width: 100%;
      height: 100%;
      transition: all 0.3s ease;
    }

    @media(max-width: 991px){
      width: 16.8125rem;
    }
    @media(max-width: 768px){
      width: 100%;
      height: 12.5rem;
    }
    

  }

  &:hover {
    svg {
      stroke: #0b6bc58f;
      transition: all 0.3s ease;
    }
  }
}

.equipment-item__text {
  padding: 1.75rem 0 0 1.8125rem;
  background-color: white;

  @media(max-width: 1199px) {
    width: 100%;
  }

  .h4 {
    display: block;
    margin-top: 0;
    margin-bottom: 1.5625rem;
  }
}

.equipment-item__links {
  display: flex;
  background-color: white;

  a {
    color: $turquoise;
    font-size: 1rem;
    line-height: 1.2rem;
    font-family: 'Inter', sans-serif;
    display: inline-block;
    transition: all 0.3s ease;
    &:hover {
      text-decoration: underline;
      text-underline-offset: 0.25rem;
      color: $blue_2;
    }
  }

  @media(max-width: 991px){
    flex-direction: column;
  }
}

.equipment-item__column{
  display: flex;
  flex-direction: column;

  a{
    display: inline-block;
    margin-bottom: 0.9375rem;
  }

  &:first-child{
    margin-right: 2rem;
  }

  @media(max-width: 1199px){
    &:not(:last-child){
      margin-bottom: 0.9375rem;
    }
  }
}

.equipment-desk__thumbs {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-column-gap: 1.875rem;
  margin-bottom: 3.125rem;
  margin-top: 6.25rem;

  @media(max-width: 1680px) {
    grid-template-columns: repeat(4, 15.625rem);
    grid-gap: 1.875rem;
    justify-content: center;
  }
  @media(max-width: 1199px) {
    grid-template-columns: repeat(2, 15.625rem);
  }

  @media(max-width: 991px) {
    margin-top: 3.75rem;
  }

  @media(max-width: 568px) {
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 2.625rem;
  }
}

.equipment-desk__thumb {
  display: flex;
  flex-direction: column;
  max-width: 15rem;
  max-height: 17.5rem;
  background: $card-bg;
  border-radius: 1.25rem;
  position: relative;
  overflow: hidden;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);

  &:hover { 
    box-shadow: 0.425rem 0.425rem 0.9rem rgba(0, 0, 0, 0.5);
    cursor: pointer;
    transition: all 0.3s ease;
    h5 {
      color: $blue_2;
    }
  }

  h5 {
    margin-bottom: 0;
    @media(max-width: 568px) {
      font-size: 0.6875rem;
      line-height: 0.8125rem;
    }
  }

  @media(max-width: 1680px) {
    display: none;
    &:nth-child(-n+4) {
      display: flex;
    }
  }

  @media(max-width: 1300px) {
    height: 100%;
  }

  @media(max-width: 991px) {
    height: auto;
  }

  @media(max-width: 380px) {
    min-height: 9.375rem;
    max-width: 8.125rem;
    border-radius: 0.643rem;
  }
}

.equipment-desk__titleBox {
  padding-left: 1.25rem;
  padding-right: 0.563rem;
  padding-bottom: 1.25rem;
  margin-top: auto;

  @media(max-width: 480px) {
    padding-left: 0.688rem;
    padding-right: 0.563rem;
    padding-bottom: 0.875rem;
  }

  .h5 {
    display: block;
    margin-top: 0;
    font-weight: 800;
    font-family: "Inter", sans-serif;
  }
}

.equipment-desk__imageBox {
  max-height: 9.375rem;
  margin-bottom: 1rem;

  @media(max-width: 480px) {
    margin-bottom: 0;
  }

  &.px-3 {
    padding-top: 0.8rem;
  }

}

.thumb-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  margin-top: 1.5rem;

  @media(max-width: 1200px) {
    margin-top: 1.25rem;
  }

  @media(max-width: 568px) {
    margin-top: 0.9rem;
  }
}

.equipment-desk__more {
  display: block;
  width: max-content;
  padding: 0.9688rem 3.75rem !important;
  margin: 0 auto;

  span {
    color: $white;
    margin-left: 0.3rem;
  }
}
