@media (min-width: 992px) {
  .news-head-box {
    height: 350px;
    position: relative;
    width: 100%;

    &:after {
      content: '';
      position: absolute;
      background-image: url("images/catalog/catalog-info-pattern.svg");
      width: 100%;
      height: 100%;
      z-index: $lvl_-1;
      bottom: -0.9375rem;
      right: -0.9375rem;
      border-radius: 1.25rem;

      @media (min-width: 1680px) {
        background-size: 110%;
      }
    }
  }
}
.news-tabs {
  margin-top: 3.75rem;

  .nav-tabs {
    position: relative;

    .news-tabs__buttons {
      display: flex;
      margin-bottom: 0;
    }

    svg {
      margin: 0 1.25rem 0 0.625rem;
      stroke: #BECCE0;
      width: 1.25rem;
      height: 2.0625rem;
  
      @media(max-width: 568px) {
        margin-top: 0.1875rem;
        width: 0.9375rem;
        height: 1.5625rem;
      }
    }
  }

  @media(max-width: 568px) {
    margin-top: 2.5rem;
  }
}

.news-tabs__button {
  color: $dark;
  font-size: 0.8125rem;
  line-height: 0.9812rem;
  font-weight: 400;
  font-family: "Inter", sans-serif;
  padding: 0.4375rem 1.5625rem;
  border-radius: 1.25rem;
  background-color: #EFF3F6;
  width: max-content;
  transition: all 0.3s ease;
  border: none;

  &:not(:last-child) {
    margin-right: 0.625rem;
  }

  &.active {
    background-color: $blue_1;
    color: white;

    &:hover {
      background-color: $blue_2;
    }
  }

  @media(max-width: 991px) {
    margin-bottom: 0.625rem;
  }
}

.news-tabs__cards { 
  &.active {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 1.875rem;
    grid-row-gap: 3.125rem;
  }

  @media(max-width: 1679px){
    grid-template-columns: repeat(3, 1fr)!important;
  }
  @media(max-width: 1199px){
    grid-template-columns: repeat(2, 1fr)!important;
  }
  @media(max-width: 991px){
    grid-template-columns: repeat(1, 1fr)!important;
  }

}

.news-tabs__card {
  padding: 0;
  border-radius: 1.25rem;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  background: $card-bg;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: hidden;
  height: 100%;
  padding-bottom: 2.9375rem;
  transition: box-shadow 0.3s;

  img {
    object-fit: cover;
    width: 100%;
  }

  &.__first {

    .title {
      margin: 0.75rem 0;
    }

    .news-tabs__text {
      margin-top: 1.25rem;
      font-size: 1rem;
      line-height: 1.6rem;

      @media(max-width: 380px) {
        margin-top: 0.9375rem;
        font-size: 0.8125rem;
        line-height: 0.9375rem;
      }
    }

    .hashtag {
      font-size: 0.8125rem;
      line-height: 1rem;

      @media(max-width: 380px) {
        font-size: 0.6875rem;
        line-height: 0.8125rem;
      }
    }

    .news-tabs__textInfo {
      padding: 1rem 1.25rem 0;
    }

    img {
      min-height: 10.625rem;
    }
  }

  @media (min-width: 992px) {
    &.__first {
      height: 21.875rem;
      flex-direction: row;
      align-items: center;
      padding-bottom: 0;
      position: relative;

      .news-tabs__textInfo {
        padding: 0 1.25rem;
        margin-left: 5rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }

      .title {
        font-size: 1.875rem;
        line-height: 2.25rem;
      }

      img {
        width: 45%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  .news-tabs__date{
    margin-top: 0.625rem;

    .news-tabs__date-item {
      display: flex;
      align-items: center;
    }
  }

  .title {
    display: block;
    margin-top: 0.75rem;
    font-family: "Gilroy", sans-serif;
    font-size: 1.25rem;
    font-weight: 800;
    line-height: 1.5rem;

    @media(max-width: 380px){
      margin: 0.5rem 0;
    }
  }

  .news-tabs__title-tags {
    overflow: hidden; 
    text-overflow: ellipsis;
    display: -moz-box;
    display: -webkit-box;
    line-clamp: 2;
    box-orient: vertical;
    -moz-box-orient: vertical;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    line-height: 1rem;
    margin: 0.25rem 0 0.625rem 0;
    
    a {
      display: inline-block;
      font-family: "Inter", sans-serif;
      font-size: 0.8125rem;
      line-height: 1rem;
      margin-right: 0.625rem;
      color: $blue_1;

      &:hover {
        color: $blue_2;
      }
    }
  }

  .news-tabs__textInfo {
    padding: 1rem 1.875rem 0;
    min-height: 12.5rem;

    .news-tabs__date {
      display: flex;
      justify-content: space-between;

      svg { 
        width: 1.3125rem;
        height: 1.3125rem;
        fill: $turquoise;
        display: inline-block;
        margin-right: 0.375rem;
      }
    }

    span, li {
      font-family: "Inter", sans-serif;
      font-size: 0.813rem;
      line-height: 1rem;
      color: $turquoise;
      display: inline-block;
      overflow: hidden; 
      text-overflow: ellipsis;
      display: -moz-box;
      display: -webkit-box;
      line-clamp: 3;
      box-orient: vertical;
      -moz-box-orient: vertical;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
    }

    ul {
      padding-left: 1.5rem;
    }
  }

  &:hover {
    box-shadow: 0.425rem 0.425rem 0.9rem rgba(0, 0, 0, 0.5);
    cursor: pointer;
    transition: all 0.3s ease; 

    .news-tabs__textInfo {
      a {
        color: $blue_2;
      }
    }
  }
}

.news-tabs__tags-item {
  font-size: 0.6875rem;
  line-height: 0.8125rem;
  color: $blue_2;
  padding: 0.125rem 0.625rem;
  border-radius: 0.375rem;
  margin-right: 0.625rem;  
  font-size: 0.6875rem;
  line-height: 0.813rem;
  font-family: 'Inter', sans-serif;
  background-color: #ddeffb;
  color: $blue_2 !important;

  &.__blue {
    background-color: #1B73BB;
    color: $white !important;
  }

  &.__violet {
    background-color: #8C41C7;
    color: $white !important;
  }

  &.__green {
    background-color: #41C79E;
    color: $white !important;
  }
}

.news-tabs__tags {
  display: flex;

  @media(max-width: 380px){
    flex-wrap: wrap;
  }
}

.newsDetail__span-grey {
    font-family: "Inter", sans-serif;
    font-size: 0.813rem;
    line-height: 1rem;
    color: $turquoise;
    margin-right: 0.9375rem;
}

.newsDetail__span-grey-desc {
  font-family: "Inter", sans-serif;
  font-size: 0.813rem;
  line-height: 1rem;
  color: $turquoise;

  @media(max-width: 480px){
    font-size: 0.6875rem;
    line-height: 0.8125rem;
  }
}

.newsDetail__img-wrap {
  max-width: 780px;
  margin: 2.5rem 0 0.625rem;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.newsDetail__description {
  font-size: 1.125rem; 
  line-height: 1.975rem;
  font-weight: 400;
  font-family: 'Inter', sans-serif;
  color: $dark;
  margin-top: 2.5rem;
  margin-bottom: 0;
}



.newsDetail-set {
  h3 {
    margin-top: 4.375rem;

    @media(max-width: 1199px) {
      margin-top: 2.938rem;
    }

    @media(max-width: 991px) {
      margin-top: 3.75rem;
    }
  }

  ul {
    color: $turquoise;
    margin-bottom: 1.5625rem;
    span {
      font-size: 1rem;
      line-height: 1.6rem;
      font-family: 'Inter', sans-serif;
    }
  }
  li {
    &:not(:first-child){
      margin-top: 1.25rem;
    }
  }

  p {
    font-size: 1rem;
      line-height: 1.6rem;
      font-family: 'Inter', sans-serif;
  }
}

.newsDetail-sidebar {
 
 h5 {
  margin: 2.5rem 0;

    @media(max-width: 991px) {
      margin: 1.875rem 0 1.25rem;
    }
  }

  span {
    margin-right: 0.9375rem;
  }
}

.newsDetail__gost-box {
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  position: relative;

  &:last-child {
    margin-top: 1.875rem;
  }

  @media(min-width: 1400px) {
    max-width: 15rem;
  }

  @media(max-width: 991px) {
    margin-right: 2.8125rem;
  }
  

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: -0.625rem;
    right: -0.5625rem;
    background: url("./images/events/event-graph-background.png") center center no-repeat;
    border-radius: 1.25rem;
    background-size: cover;
    z-index: $lvl_-1;
  }

  .wrap { 
    background-color: $white;
    border-radius: 1.25rem;
    padding: 0.938rem 1.25rem 2.25rem ;

    
    a {
      font-family: "Inter", sans-serif;
      font-size: 1rem;
      line-height: 1.6rem;
      color: $dark;
      margin: 0.625rem 0 0.3125rem 0;
      display: inline-block;
    }

    .text-small {
      font-size: 0.6875rem;
      line-height: 0.8125rem;
    }

    &:hover { 
      box-shadow: 0.425rem 0.425rem 0.9rem rgba(0, 0, 0, 0.5);
      cursor: pointer;
      transition: all 0.3s ease;
      a {
        color: $blue_2;
      }
    }
  }

  .title {
    margin: 0;
  }

  .swiper-slide {
    img {
      height: 7.9375rem;
    }
  }                           
}

.row.newsDetail {
  @media(max-width: 1400px) {
    justify-content: space-between;
  }
}

.newsDetail-share {
  display: flex;
  margin-top: 2.5rem;

  .newsDetail-share__messanger {
    margin-right: 0.9375rem;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background: #F2FAFF;
    display: flex;
    justify-content: center;
    align-items: center;

    svg {
      max-width: 50%;
      max-height: 50%;
      fill: $blue_1;
    }

    @media(max-width: 991px) {
      width: 2.25rem;
      height: 2.25rem;
    }
  }

  a {
    display: flex;
    align-items: center;

    &:hover {
      svg {
        fill: $blue_2;
      }
    }
  }

  .newsDetail-share__hook {
    margin-right: 1.25rem;

    span {
      font-family: 'Inter';
      font-size: 1rem;
      line-height: 1.6rem;
      color: $blue_1;
    }

    svg {
      width: 0.975rem;
      height:  0.975rem;
      margin-right: 0.25rem;
      fill: $blue_1;
      margin-bottom: 11px
    }

    &:hover {

      span {
        color: $blue_2;
      }

      svg {
        fill: $blue_2;
      }
    }
  }
}

.newsDetail-pagination {
  display: flex;
  margin-top: 2.5rem;
  justify-content: space-between;
  
  .newsDetail-pagination__item {
    font-family: 'Inter';
    font-size: 1rem;
    line-height: 1.6rem;
    color: $blue_1;
    font-weight: 700;
  
    svg {
      width: 0.8125rem;
      height: 1rem;
      fill: $blue_1;
      margin-right: 0.3125rem;
    }

    &.__right {
      svg {
        margin-left: 0.3125rem;
        transform: rotate(180deg);
      }
    }

    &:hover {
      color: $blue_2;
      svg {
        fill: $blue_2;
      }
    }
  }

  @media(max-width: 991px) {
    display: none;
  }
}

.newsDetail-event {
  margin-bottom: 6.25rem;
}