.video__box {
  position: relative;
  margin-top: 2.5rem;
  max-width: 100%;
  width: 780px;


  .video__controls {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: $lvl_1;
    
    .video__controls-background {
      opacity: 0;
      width: 100%;
      height: 100%;
    }

    .video__controls-button {
      width: 5rem;
      height: 5rem;
      position: absolute;

      svg {
        width: 100%;
        height: 100%;
        fill: rgba(255, 255, 255, 0.9);
        stroke: rgba(0, 0, 0, 0.5);
      }

      .video__controls-pause-img {
        display: none;
      }

      &:hover {
        cursor: pointer;
      }
    }

    &:hover {
      .video__controls-background {
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1));
        opacity: 1;
        transition: all 0.3s ease;
      }

      #durationBar,
      #displayStatus {
        opacity: 1;
        transition: 0s;
      }
    }

    &.__play{
      .video__controls-play-img {
        display: none;
      }

      &:hover {
        .video__controls-pause-img {
          display: block;
        }
      }
    }
  }

  .video__wrap {
    position: relative; 
    padding-bottom: 57%;
  }

  .video__wrap video,
  .video__wrap iframe {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
  }

  @media(max-width: 1399px) {
    width: 67%;
  }

  @media(max-width: 991px) {
    width: 100%;
  }
}

.barContainer {
  z-index: $lvl_1;
  position: absolute;
  width: 94%;
  bottom: 20px;
  left: 3%;
}

#durationBar {
  background: rgba(206, 234, 252, 0.4);
  width: 100%;
  position: relative;
  border-radius: 5px;
  opacity: 0;
  display: none;
  transition: 2s 3.5s;

  &:hover {
    background:rgba(206, 234, 252, 0.6);
    cursor: pointer;

    #positionBar {
      background: rgba(0, 88, 161, 0.6);
    }
  }
}

#positionBar {
  height: 5px;
  background: rgba(0, 88, 161, 0.4);
  text-align: center;
  width: 0;
  border-radius: 5px;
}

#displayStatus {
  position: absolute;
  display: inline-block;
  left: 3%;
  bottom: 30px;
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;
  color: rgba(0, 0, 0, 0.9);
  opacity: 0;
  transition: 2s 3.5s;
}