.trainingCenter { 
  font-family: 'Inter', sans-serif;

  p {
    font-size: 1rem;
    line-height: 1.6rem;
  }

  .eventsDetail-top-section__sidebar {
    
    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      bottom: -0.625rem;
      right: -0.5625rem;
      background: url("./images/events/event-graph-background.png") center center no-repeat;
      background-size: cover;
      z-index: $lvl_-1;
    }

    &:last-child {
      margin-top: 0;

      @media(max-width: 991px){
        margin-bottom: 2.1875rem;
      }
    }
  }

  .btn-primary {
    font-weight: 600;
    font-size: 13px;
    line-height: 16px;
  }

  .guarantee__info-box {
    margin-top: 1.25rem;
    display: flex;
  
    img {
        width: 2.1875rem;
        height: 1.75rem;
        margin-right: 15px;
    }
  
    .title {
        font-family: 'Gilroy', sans-serif;
        font-weight: 800;
        font-size: 1.625rem;
        line-height: 1.95rem;
        color: $dark;
  
        @media(max-width: 991px) {
            font-size: 1.25rem;
            line-height: 1.5rem;
        }
    }
  
    .text {
        margin-top: 1.25rem;
    }
  }
}

.trainingCenter-top-section {
  display: flex;
  margin-top: 2.5rem;

  @media(max-width: 991px) {
   flex-direction: column;
  }
}

.trainingCenter-ul-box {
  margin: 2.5rem 0 1.5625rem;

  ul {
    margin-bottom: 0;
  }

  li {
    margin-top: 1.25rem;
    font-size: 1rem;
    line-height: 1.6rem;
    font-family: 'Inter', sans-serif;
    color: $dark;
    &::marker {
      color: $turquoise;
    }
  }
}

.trainingCenter-swiper {
  margin-top: 4rem;

  .swiper-slide {

    img {
      max-height: 29.5rem;
    }
  }
}

.trainingCenter-bottom-section {
  margin-top: 4.375rem;

  @media(max-width: 767px) {
    margin-top: 0; 
  }
}

.trainingCenter-bottom-section__wrap {
  display: flex;
  margin-top: 2.5rem;

  @media(max-width: 991px) {
   flex-direction: column;
  }
}

.trainingCenter-bottom-section__certificate-wrap {
  display: flex;
  justify-content: center;
}

.trainingCenter-bottom-section__certificate {
  max-width: 16.875rem;
  display: flex;
  flex-direction: column;

  .desk {
    margin-top: 0.9375rem;
    font-size: 0.8125rem;
    line-height: 1rem;
    text-align: center;
  }

  @media(max-width: 991px){
    margin-top: 2.1875rem;
    max-width: 80%;
  }

  img {
    object-fit: cover;
    width: 100%;
  }
}



