.noUi-target{
  background: transparent;
  border: none;
  box-shadow: none;
  .noUi-connects{
    width: 100%;
    height: 0.5rem;
    background: #F1F1F1;
    border-radius: 0.25rem;
  }
  .noUi-connect{
    background: $blue_1;
  }
  .noUi-handle{
    width: 1.25rem;
    height: 1.25rem;
    border-radius: 50%;
    background: $blue_1;
    border: 0.125rem solid $white;
    box-shadow: none;
    &:before,&:after{
      display: none;
    }
    &.noUi-handle-lower{
      &:before{
        display: block;
        position: absolute;
        width: 0.4375rem;
        height: 0.4375rem;
        top: 50%;
        left: 50%;
        background: $white;
        border-radius: 50%;
        transform: translate(-50%,-50%);
      }
    }
  }
}