.personalData {
    font-family: 'Inter', sans-serif;
    color: $dark;
    font-size: 1rem;
    line-height: 1.6rem;
    margin-top: 2.5rem;

    .text {

        span:first-child {
            color: $turquoise;
        }
    }

    .text:not(:first-child) {
        margin-top: 1.25rem;
    }
}

.error {
    margin-top: 2.5rem;
    display: flex;

    @media(max-width: 991px){
        flex-direction: column;
        gap: 1.875rem;
    }

    a,
    .text {
        font-family: 'Inter', sans-serif;
        color: $dark;
        font-size: 1.125rem;
        line-height: 2rem;
    }

    .text:not(:first-child) {
        margin-top: 0.3125rem;
    }

    a {
        color: $blue_1;

        &:hover {
            color: $blue_2;
        }
    }

    button {
        margin-top: 1.875rem;
    }
}

.error__img {

    img {
        object-fit: cover;
        width: 100%;
        height: 100%;
    }
}

.websiteMap {
    margin-top: 2.5rem;
    display: flex;

    @media(max-width: 991px){
        flex-direction: column;
        gap: 1.25rem;
    }

    a {
        font-family: 'Inter', sans-serif;
        color: $blue_1;
        font-size: 1rem;
        line-height: 1.6rem;
        display: block;

        &:hover {
            color: $blue_2;
            text-decoration: underline;
            text-underline-offset: 0.375rem;
        }

        &.title {
            font-weight: 700;
            font-size: 1.125rem;
            line-height: 1.35rem;

            &:hover {
                text-decoration-thickness: 0.125rem;
            }
        }
    }

    a:not(:first-child) {
        margin-top: 0.625rem;

        &.title {
            margin-top: 1.25rem;
        }
    }
}