.bottom-swiper__wrapper {
  max-width: 100%;
  margin-bottom: 2.0625rem;
}

.bottom-slider__swiper {
  position: relative;
  padding: 0 0.1875rem;

  .swiper_pagination {
    margin-top: 1.375rem;
  }
}

.bottom-slider__button-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;


  .bottom-swiper-button {
    width: 3.125rem;
    height: 5rem;
    background-color: rgba(0, 88, 161, 0.29);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.375rem;
    cursor: pointer;
    border: 0.0625rem solid transparent;
    transition: all 0.3s ease;
    position: static;

    &:hover {
      background-color: transparent;
      border-color: rgba(0, 88, 161, 0.29);

      svg {
        fill: rgba(0, 88, 161, 0.29);
      }
    }

    svg {
      width: 0.5rem;
      height: 1.125rem;
      fill: $white;
      transition: all 0.3s ease;
    }

    z-index: $lvl_1;

    &.swiper-button-disabled {
      background-color: transparent;
      border-color: rgba(0, 88, 161, 0.29);

      svg {
        fill: rgba(0, 88, 161, 0.29);
      }
    }
  }

  .swiper-button-prev {
    transform: rotate(180deg);
  }

  .buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 86%;
  }


  @media(max-width: 767px) {
    display: none;
  }
}

.bottom-slider__section {
  min-width: 0;
  max-width: 100%;
  padding-top: 6.875rem;
  background-color: $milk_1;
  position: relative;

  &:before{
    content: "";
    position: absolute;
    width: 100%;
    bottom: -4.375rem;
    left: 0;
    height: 4.375rem;
    background-color: $milk_1;
  }

  @media(max-width: 1380px) {
    padding-bottom: 8.75rem;
  }

  @media(max-width: 991px) {
    padding: 4.6875rem 0 0 0;
  }

}

.bottom-slider__top {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 0;

  .h2{
    display: block;
    margin-top: 0;
  }
}

.bottom-slider__description {
  color: $turquoise;
  font-size: 1.25rem;
  line-height: 2rem;
  font-family: 'Inter', sans-serif;
  text-align: center;
  max-width: 57.25rem;

  @media(max-width: 768px) {
    font-size: 1rem;
    line-height: 1.6rem;
  }
}

.bottom-slider__points {
  display: flex;
  justify-content: space-between;
  max-width: 56.9375rem;
  width: 100%;

  @media(max-width: 767px) {
    flex-direction: column;
    align-items: center;
  }
}

.bottom-slider__point {
  display: flex;
  align-items: center;

  @media(max-width: 991px){
    max-width: 18.4375rem;
  }
  @media(max-width: 767px){
    &:not(:last-child){
      margin-bottom: 2rem;
    }
  }
}

.bottom-slider__image {
  max-width: 4.4375rem;
  margin-right: 1.3125rem;
}

.bottom-slider__img {
  max-width: 100%;
  height: auto;
}

.bottom-slider__text {
  color: $turquoise;
  font-size: 1.875rem;
  line-height: 2.25rem;
  font-family: 'Gilroy', sans-serif;

  @media(max-width: 991px) {
    font-size: 1rem;
    line-height: 1.2rem;

  }
}

.bottom-slider__body {
  .bottom-slider__item {
    width: 100%;
    height: 30rem;
    display: flex;
    align-items: center;
    overflow: hidden;
    background: $card-bg;
    box-shadow: 0rem 0.4375rem 0.375rem rgba(0, 0, 0, 0.13);
    border-radius: 1.25rem;

    @media(max-width: 767px) {
      flex-direction: column;
    }

    .h3 {
      display: block;
      margin-top: 0;
    }

    @media(max-width: 1199px) {
      height: 100%;
    }
  }

  .slider-content {
    min-width: 0;
    display: flex;
    flex-direction: column;

    @media(max-width: 1199px) {
      width: 100%;
      padding-left: 0;
    }
  }

  .slider-content__date {
    color: $turquoise;
    font-size: 0.8125rem;
    line-height: 0.9812rem;
    font-family: "Inter", sans-serif;
    font-weight: 400;
    display: none;
    margin-bottom: 0.625rem;

    @media(max-width: 1199px) {
      display: inline-block;
    }
  }

  .slider-content__text {
    h3 {
      margin-bottom: 1.875rem;
      @media(max-width: 1300px) {
        font-size: 1.25rem;
        line-height: 1.5rem;
        font-weight: 800;
      }

      @media(max-width: 1199px) {
        margin-bottom: 0.625rem;
      }
    }

    @media(max-width: 1199px) {
      max-width: 100%;
    }

    @media(max-width: 1199px) {
      padding: 0.9375rem 1.25rem 1.5625rem 1.25rem;
    }
  }

  .slider-image {
    width: 100%;
    height: 100%;
    position: relative;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;

      @media(max-width: 568px) {
        max-width: 100%;
        height: auto;
      }
    }

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.2);
    }

    @media(max-width: 767px) {
      max-height: 20rem;
      width: 100%;
      overflow: hidden;
    }

    @media(max-width: 568px) {
      height: 10.375rem;
    }
  }

  .slider-content__description {
    font-size: 1rem;
    line-height: 1.6rem;
    color: $turquoise;
    font-family: 'Inter', sans-serif;
    display: inline-block;
    margin-bottom: 3.125rem;

    @media(max-width: 1199px) {
      font-size: 0.8125rem;
      line-height: 0.9375rem;
      margin-bottom: 0;
    }
  }

  .slider-content__link {
    padding: 0.5rem 0.8125rem 0.4375rem 1.25rem;
    border: 0.0625rem solid $blue_1;
    border-radius: 2.5rem;
    width: max-content;
    display: block;
    transition: all 0.3s ease;

    span{
      color: $blue_1;
      font-size: 0.9375rem;
      line-height: 1.125rem;
      font-family: "Roboto", sans-serif;
      display: inline-block;
      margin-right: 0.875rem;
    }

    svg {
      stroke: $blue_1;
      transform: rotate(180deg);
      width: 0.75rem;
      height: 0.875rem;
      display: inline-block;
    }

    &:hover{
      border-color: $blue_2 !important;
      color: $blue_2;

      svg{
        stroke:$blue_2;
      }

      span{
        color: $blue_2;
      }
    }

    @media(max-width: 991px) {
      display: none;
    }
  }

  .rounded-pagination{
    margin-top: 0;
  }

  @media(max-width: 1199px) {
    padding: 0 0.9375rem;
  }
}