.slider-gallery-modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s, background-color 0.3s;
    display: flex;

    @media (min-width: 992px) and (min-height: 600px) {
        align-items: center;
    }

    &.active {
        opacity: 1;
        visibility: visible;
    }

    &__content {
        position: relative;
        display: flex;
        height: 600px;
        width: 100%;
        padding: 0px 40px;

        @media (max-width: 992px) {
            height: auto;
        }

        @media (max-width: 576px) {
            padding: 0px 20px;
        }
    }

    &__gallery-main {
        position: relative;
        width: 800px;
        margin: 0 auto;
        align-self: center;

        @media(min-width: 1400px) {
            margin-left: calc(50% - 578px);
        }

        @media (max-width: 992px) {
            width: 600px;
        }

        @media (max-width: 767px) {
            width: 100%;
        }
    }

    &__gallery-thumbs {
        margin-left: 0;
        margin-right: 20px;
        margin-bottom: 200px;

        .swiper-slide {
            opacity: 0.5;
            border: 0.125rem solid transparent;
            border-radius: 0.375rem;

            &:hover{
                border-color: $turquoise;
                opacity: 0.8;
                cursor: pointer;
            }
        }

        .swiper-slide-thumb-active {
            opacity: 1;
            border-color: $turquoise;
        }

        @media (max-width: 1200px) {
            display: none;
        }
    }

    &__close {
        position: absolute;
        top: 20px;
        right: 20px;
        width: 40px;
        height: 40px;
        border: none;
        background-color: transparent;
        z-index: $lvl_1;

        svg {
            fill: $blue_1;
            pointer-events: none;
        }

        @media (max-width: 991px) {
            top: 10px;
            right: 10px;
        }
    }

    &__btn {
        padding: 8px;
        background-color: rgba(0, 88, 161, 0.7);
        border-radius: 6px;
        width: 50px;
        height: 80px;

        svg {
            fill: #fff;
            width: 20px;
            height: 20px;
        }

        &--prev {
            left: -10%;
        }

        &--next {
            right: -10%;
        }

        @media (max-width: 1024px) {
            display: none;
        }
    }

    .swiper-pagination-bullets.swiper-pagination-horizontal {
        bottom: -30px;

        @media (min-width: 1025px) {
            display: none;
        }
    }

    .swiper-pagination-bullet-active {
        background: rgba(0, 88, 161, 0.7);
    }

    .swiper-button-prev {
        transform: rotate(180deg);
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}