.catalog-change-buttons-wrapper { 
  display: flex;

  p{
    margin-right: 0.4375rem;
  }

  .catalog-button{
    &:not(:last-child){
      margin-right: 0.3125rem;
    }
  }
}

.catalog-button {
  width: 1.75rem;
  height: 1.75rem;
  cursor: pointer;

  svg {
    max-width: 100%;
    max-height: 100%;
  }
}

.catalog-change-button {
  padding: 0.375rem;
  line-height: 0;
  border-radius: 0.375rem;
  transition: .3s;

  &:hover {
    box-shadow: -0.125rem 0.25rem 0.8125rem -0.125rem rgba(34, 60, 80, 0.2);

    svg {
      stroke: $blue_2;
    }
  }

  svg {
    stroke: $blue_1;
  }

  &.active {
    background-color: $blue_1;

    &:hover {
      background-color: $blue_2;
    }

    svg {
      stroke: $white;
    }
  }
}

.sorting-select {
  min-width: 15.9375rem;
  z-index: $lvl_3;

  .choices__inner {
    padding: 0.3125rem 1.25rem;
    min-height: 2.375rem;
  }
}

.catalog-items {
  @media (max-width: $screen-sm) {
    margin: -4px -15px 0 -15px;
  }

  .banner {
    margin: 0 15px;
  }
}

.catalog-card-wrapper { 
  .catalog-card {
    display: block;
    position: relative;
    padding: 12px;
    border-radius: 1.25rem;
    border: 0.0625rem solid transparent;
    height: 100%;
    width: 100%;

    &:hover {
      box-shadow: 0rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
      border-color: #F9F9F9;
    }

    .order-button{
      margin-left: 0.3125rem;
    }
  }


  // @media (max-width: $screen-sm) {
  //   &:nth-child(odd) {
  //     .catalog-card {
  //       margin-left: -0.9375rem;
  //     }
  //   }

  //   &:nth-child(even) {
  //     .catalog-card {
  //       margin-right: -0.9375rem;
  //     }
  //   }
  // }
}


.catalog-list {
  padding: 1.875rem 0;
  border-bottom: 0.0625rem solid $milk_1;

  &__main {
    display: flex;
    column-gap: 1.25rem;

    .catalog-swiper {
      flex-basis: 22%;
      max-width: 22%;

      .swiper-pagination-bullet {
        width: 0.25rem;
      }

      @media (min-width: $screen-sm) {
        flex-basis: 40%;
        max-width: 40%;
        .swiper-pagination-bullet {
          width: 0.625rem;
        }
      }

    }

    &__info {
      flex-basis: 60%;
      max-width: 60%;
    }

    @media (min-width: $screen-3xl - 1px) {
      column-gap: 2.8125rem;
      .catalog-swiper {
        min-width: 0;
        flex-basis: 30%;
        max-width: 30%;
      }

      &__info {
        flex-basis: 70%;
        max-width: 70%;
      }
    }
  }

  .order-button{
    margin-left: 0.3125rem;
  }
}

.catalog-controls {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  column-gap: 0.3125rem;
  row-gap: 1.5625rem;

  .total-price {
    display: none;
  }

  &_card {
    @media (min-width: $screen-3xl) {
      justify-content: start;
      column-gap: 1.5625rem;
    }
  }

  &_list {
    justify-content: start;
    column-gap: 1.5625rem;
  }

  &_table {
    .total-price {
      display: block;
    }
  }
}

.presence {
    font-size: 0.6875rem;
    line-height: 0.8125rem;
  
    &.in-stock {
      color: $green;
    }
  
    &.not-in-stock {
      color: $red;
    }
  
    &.check-availability {
      color: $turquoise;
    }
  }


.presence-in-card {

  > * {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &__price {
    > * {
      &:not(:last-child) {
        margin-right: 10px;
      }
    }
  }

  .old-price{
    margin-bottom: 0;
  }
}

.catalog-table {
  position: relative;

  &__row {
    padding: 0.9375rem 0.3125rem 1.5625rem;
    border-bottom: 0.0625rem solid #EAE6E0;

    &:first-child {
      border-top: 0.0625rem solid #EAE6E0;
    }

    &.selected {
      background: rgba(239, 249, 255, 0.8);
    }
  }

  &__img {
    max-width: 5rem;
    width: 100%;
    @media (max-width: $screen-md) {
      max-width: 2.5rem;
    }
  }

  &__footer {
    background: $dark-gray;
    border-radius: 0.5rem;
    padding: 0.9375rem 1.25rem;
    position: sticky;
    bottom: 0;
    display: flex;
    align-items: center;
    color: $white;
    column-gap: 1.5625rem;
    row-gap: 0.9375rem;
    flex-wrap: wrap;

    .close-footer {
      cursor: pointer;
      width: 1rem;
      height: 1rem;
      position: relative;

      &:before, &:after {
        content: '';
        width: 100%;
        height: 0.125rem;
        border-radius: 0.3125rem;
        background-color: $white;
        position: absolute;
        left: 0;
        top: 50%;
        line-height: 0;
      }

      &:before {
        transform: translateY(-50%) rotate(45deg);
      }

      &:after {
        transform: translateY(-50%) rotate(-45deg);
      }
    }

    .selected-footer-count {
      color: $white;
      font-weight: 700;
    }

    .other-actions {
      display: flex;
      column-gap: 0.9375rem;
      align-items: center;
      cursor: pointer;
      position: relative;

      svg {
        width: 1.25rem;
        height: 0.3125rem;
        fill: $white;
      }

      .dropdown {
        position: absolute;
        padding: 0.625rem;
        background: #F2FAFF;
        box-shadow: 0rem 0.375rem 1.25rem rgba(0, 0, 0, 0.4);
        border-radius: 0.3125rem;
        left: 0;
        top: 0;
        transform: translateY(-100%);

        &__item {
          font-size: 0.8125rem;
          line-height: 1rem;
          color: $blue_1;
          margin-left: 0.625rem;
          margin-bottom: 0.625rem;

          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }

    @media (max-width: $screen-sm) {
      padding: 0.9375rem 0.625rem;
      column-gap: 0.625rem;
      .selected-footer-count {
        font-size: 0.8125rem;
        line-height: 0.9375rem;
      }
    }
  }

  .order-button{
    margin-left: 0.3125rem;

    @media (min-width: 768px) {
      margin-bottom: 1.4rem;
    }
  }
}

.catalog_v2 {
  .catalog-button:not(.catalog-change-button) {
    display: none !important;
  }
}