.catalog-info {
  padding: 1.875rem;
  background: $card-bg;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    background-image: url("images/catalog/catalog-info-pattern.svg");
    width: 100%;
    height: 100%;
    z-index: $lvl_-1;
    bottom: -0.9375rem;
    right: -0.9375rem;
    border-radius: 1.25rem;

    @media (min-width: 1680px) {
      background-size: 110%;
    }
  }
}

.filter-wrapper {

  .filter {
    padding: 0;

    &__accordion {
      .accordion-button { 
        padding: 0;
        font-size: 1rem !important;
        line-height: 1.2rem !important;
        font-weight: 700 !important;
        color: $black !important;
        background: transparent !important;
        font-family: "Inter", sans-serif !important;
        display: flex;
        align-items: flex-start; 

        &:after {
          width: 1rem;
          height: 1rem;
          background-size: contain;
        }

        &.other-parameters {
          color: $turquoise !important;
          display: flex;
          gap: 0.3125rem;

          span {
            font-size: 0.6875rem;
            line-height: 0.8125rem;
            color: $turquoise !important;
          }
        }

        &:not(.collapsed)::after {
          background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23212529'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
          transform: rotate(-180deg);
        }
      }

      .accordion-body {
        padding: 0.625rem 0.25rem 0 0;
        position: relative;

      }
      &_inner{
        margin-right: -0.3125rem;
      }
    }

    &__inputs-wrapper {
      height: var(--height);
      overflow: hidden;
      overflow: auto;
    }

    .show-more-filters {
      margin-top: 0.625rem;
      cursor: pointer;
      display: none;

      &.active {
        display: block;
      }
    }

    &__buttons-wrapper {
      margin: 0 auto;
      width: fit-content;
      display: flex;
      flex-direction: column;

      .btn-primary {
        margin-bottom: 1.5625rem;
      }
    }

    .close-filter {
      width: 1.5rem;
      height: 1.5rem;
      position: relative;
      border-radius: 50%;
      cursor: pointer;
      border: 0.0625rem solid $dark;

      &:before, &:after {
        content: '';
        background: $dark;
        width: 0.625rem;
        height: 0.125rem;
        border-radius: 0.1875rem;
        position: absolute;
        left: 50%;
        top: 50%;
      }

      &:before {
        transform: translate(-50%, -50%) rotate(45deg);
      }

      &:after {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }

    .popup-result {
      padding: 0.9375rem 0.625rem 0.9375rem 1.25rem;
      background: $dark-gray;
      box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
      border-radius: 0.9375rem;
      position: absolute;
      left: 50%;
      right: auto;
      transform: translate(-50%, calc(-100% + 0.625rem));
      display: none;
      gap: 1.25rem;
      z-index: $lvl_1;

      &.active {
        display: flex;
      }

      a {
        border: 0.0625rem solid $white !important;
        color: $white !important;
        font-weight: 400 !important;
      }

      .close-popup {
        width: 0.6875rem;
        height: 0.6875rem;
        cursor: pointer;
        position: relative;

        &:before, &:after {
          content: '';
          width: 100%;
          height: 0.0625rem;
          position: absolute;
          left: 0;
          top: 50%;
          background: $white;
        }

        &:before {
          transform: translateY(-50%) rotate(45deg);
        }

        &:after {
          transform: translateY(-50%) rotate(-45deg);
        }
      }

      &:before {
        content: '';
        border: 0.625rem solid transparent;
        border-top: 0.625rem solid $dark-gray;
        position: absolute;
        bottom: 0.0625rem;
        left: 50%;
        transform: translate(-50%, 100%);
      }

      @media (min-width: $screen-lg) {
        right: 0;
        left: auto;
        transform: translateX(100%);
        &:before {
          border: 0.625rem solid transparent;
          border-right: 0.625rem solid $dark-gray;
          bottom: 50%;
          left: 0.0625rem;
          transform: translate(-100%, 50%);
        }
      }
    }

    .filter-slider {
      padding-right: 0.9375rem;
      position: relative;

      &:after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 1.25rem;
        height: 0.5rem;
        background: #F1F1F1;
        border-radius: 0.25rem;
      }
    }
  }

  @media (max-width: $screen-lg) {
    position: fixed;
    left: 0;
    top: 109px;
    width: 100%;
    height: calc(100% - 109px);
    background-color: rgba(0, 0, 0, .5);
    z-index: $lvl_4;
    .filter {
      padding: 0.625rem 0.9375rem 2.5rem;
      max-width: 30%;
      height: 100%;
      max-height: 100%;
      overflow: auto;
      background: $white;
    }
  }
  @media (max-width: $screen-md) { 
    .filter {
      max-width: 45%;
    }
  }
  @media (max-width: $screen-sm) {
    top: 103px;
    height: calc(100% - 103px);

    .filter {
      max-width: 100%;
    }
  }

}

.filter-label {
  display: block;
  margin-bottom: 0.3125rem;
  position: relative;

  span {
    margin-left: 0.3125rem;
    font-size: 0.8125rem;
    line-height: 1rem;
  }

  input:checked + span {
    font-weight: 700;
  }
}

.noUi-target .noUi-handle {
  cursor: pointer;
}

.filter-item_reset:hover {
  a {
    color: $blue_2;
    transition: all 0.3s ease;
  }
}

.catalog-controls__counter {
  display: none;
  
  @media (min-width: 768px) {
    display: block !important;
  }
}