.basket {

    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 32px;

        > * {
            margin-bottom: 10px;

            &:not(:last-child) {
                margin-right: 10px;
            }
        }

        &-text {
            font-weight: 800;
            font-size: 20px;
        }

        .ghost-btn {
            padding: 7px 15px;
            font-size: 13px;
            line-height: 16px;
        }
    }

    &__table {
        margin-bottom: 70px;

        @media (max-width: 500px) {
            margin-bottom: 30px;
        }
    }

    &__result {
        display: flex;
        flex-direction: column;
        max-width: 340px;
        min-width: 180px;
        width: 340px;    
        z-index: $lvl_2;
        position: absolute;
        opacity: 0;

        &-wrap {
            width: 100%;
            box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
            border-radius: 1.25rem;
            position: relative;
            background: $card-bg;
            padding: 15px 15px 30px;

            > * {
                &:not(:last-child) {
                    margin-bottom: 30px;
                }
            }

            @media (max-width: 500px) {
                width: 100%;
            }

            &:before {
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                bottom: -0.625rem;
                right: -0.5625rem;
                background: url("./images/events/event-graph-background.png") center center no-repeat;
                border-radius: 1.25rem;
                background-size: cover;
                z-index: $lvl_-1;
            }
        }

        &-title {
            font-size: 30px;
            font-weight: 800;
        }

        &-item {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;

            > * {
                margin-bottom: 10px;

                &:not(:last-child) {
                    margin-right: 10px;
                }
            }
        }

        &-term {
            font-size: 13px;
            font-weight: 400;

            &--bold {
                font-weight: 700;
            }
        }

        &-descr {
            font-size: 13px;
            font-weight: 700;

            &--green {
                color: $green;
            }
        }

        &-btn {
            display: flex;
            padding: 8px 17.5px;
            font-weight: 600;
            font-size: 13px;
            line-height: 16px;
        }

        @media (max-width: 991px) {
            width: 250px;
        }

        @media (max-width: 767px) {
            width: 220px;
        }

        @media (max-width: 575px) {
            margin: 0 auto;
            width: 70%;
        }

        @media (max-width: 480px) {
            align-items: center;
        }
    }
}