.projects-list {
  margin-bottom: 4.375rem;

  @media(max-width: 991px) {
    margin-bottom: 3.75rem;
  }
}

.projects-list__description {
  font-size: 1.125rem;
  line-height: 1.975rem;
  font-weight: 400;
  font-family: 'Inter', sans-serif;
  color: $dark;
  display: inline-block;
}

.projects-info {
  padding: 1.875rem 0.9375rem;
  background: $card-bg;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    background-image: url("images/catalog/catalog-info-pattern.svg");
    border-radius: 1.25rem;
    width: 100%;
    height: 100%;
    z-index: $lvl_-1;
    bottom: -0.9375rem;
    right: -0.9375rem;
    @media (min-width: 1680px) {
      background-size: 110%;
    }
  }

  @media(max-width: 991px) {
    width: fit-content;
  }
}

.projects-info__row {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;

  @media(max-width: 991px) {
    flex-direction: column;
    justify-content: flex-start;
    gap: 1.875rem;
  }
  
  .projects-info__item { 
    color: $dark;

    .projects-title {
      font-family: 'Gilroy', sans-serif;
      font-weight: 800;
      font-size: 1.875rem;
      line-height: 2.25rem;
    }

    .projects-text {
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 1rem;
      line-height: 1.25rem;
      margin-top: 0.625rem;
    }

    .projects-icons {
      display: flex;
      gap: 0.625rem;
      margin-top: 0.9375rem;
      flex-wrap: wrap;
    
      .projects-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 3.75rem;
        height: 2.1875rem;
        border: 1px solid #E9EFF2;
        border-radius: 4px;
      }
    
      img {
        max-width: 85%;
      }
    }

    .projects-link {
      margin-top: 0.625rem;

      a {
        font-size: 0.8125rem;
        line-height: 1rem;
        color: $blue_1;
        &:hover {
          color: $blue_2;
        }
      }
    }
  }
}

.projects-table__cards {
    margin-top: 2.75rem;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 2rem;
    grid-row-gap: 3.125rem;

  @media(max-width: 1679px){
    grid-template-columns: repeat(3, 1fr)!important;
  }
  @media(max-width: 1199px){
    grid-template-columns: repeat(2, 1fr)!important;
  }
  @media(max-width: 768px){
    grid-template-columns: repeat(1, 1fr)!important;
  }

}

.projects-table__card {
  padding: 0;
  border-radius: 1.25rem;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  background: $card-bg;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: hidden;
  height: 100%;
  transition: box-shadow 0.3s;

  .projects-table__img-wrap {
    max-height: 12.5rem;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .projects-table__textInfo {
    padding: 0.938rem 1.875rem 1.875rem;

    .projects-table__date {
      display: flex;
      justify-content: flex-end;
    }

    .title {
      display: block;
      margin: 0.625rem 0;
      font-family: "Gilroy", sans-serif;
      font-size: 1.125rem;
      font-weight: 800;
      line-height: 1.5rem;
  
      @media(max-width: 768px) {
        font-size: 1rem;
        font-weight: 700;
        line-height: 1.2rem;
      }
  
      @media(max-width: 380px){
        margin: 0.5rem 0;
      }
    }

    span {
      font-family: "Inter", sans-serif;
      font-size: 0.813rem;
      line-height: 1rem;
      color: $turquoise;
      display: inline-block;
      overflow: hidden; 
      text-overflow: ellipsis;
      display: -moz-box;
      display: -webkit-box;
      line-clamp: 3;
      box-orient: vertical;
      -moz-box-orient: vertical;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
    }
  }
 
  &:hover { 
    box-shadow: 0.425rem 0.425rem 0.9rem rgba(0, 0, 0, 0.5);
    cursor: pointer;
    transition: all 0.3s ease;
    
    .title {
      color: $blue_2;
    }
  }
}

.projects-filter-section__selects {
  display: flex;
  align-items: center;

  .btn {
    &:not(:last-child) {
      margin-right: 1.875rem;
      border: 1px solid transparent; 
    }

    @media(max-width: 768px) {
      display: none;
    }
  }

  .ghost-btn {
    box-shadow: none;
    border: none;
    padding: 0;
    font-weight: 400;
  }

  @media(max-width: 768px) {
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
  }

  .select-wrapper {
    
    &.projects-filter-section__system-type {
      width: 15.9375rem;
      margin-right: 1.875rem;

      .choices__inner {
        padding-bottom: 0.625rem;
      }

      @media(max-width: 768px) {
        width: 100%;
      }
    }
  }

  .choices {
    margin-bottom: 0;
  }

  .choices__list {

    .choices__item {
      font-size: 1rem;
      font-family: 'Inter', sans-serif;
    }

    .choices__item[data-value="Сфера применения"] {
      color: $turquoise_2;
    }

    .choices__item[data-value="Выбрать"] {
      color: $turquoise_2;
    }

    .choices__item.choices__item--choice[data-value="Сфера применения"],
    .choices__item.choices__item--choice[data-value="Выбрать"] {
      display: none;
    }
  }
}

.projects__pagination {
  .pagination-more {
    margin-right: 2.8125rem;
  }
}

.projects__pagination-content {
  display: flex;
  align-items: center;

  @media(max-width: 568px) {
    justify-content: center;
    .pagination {
      display: none;
    }
    .pagination-more {
      margin-right: 0;
      font-weight: 600;
    }
  }
}


