.history__subtitle {
  font-family: 'Inter', sans-serif;
  font-size: 1.125rem;
  line-height: 2rem;
  color: $dark;
  margin-top: 2.5rem;

  a {
    font-size: 1.125rem;
    line-height: 2rem;
    color: $blue_1;

    &:hover {
      color: $blue_2;
      cursor: pointer;
    }
  }

  @media(max-width: 991px) {
    margin-top: 1.25rem;
  }
}

.history__title {
  font-family: 'Gilroy', sans-serif;
  font-weight: 800;
  font-size: 2.8125rem;
  line-height: 3.094rem;
  margin-top: 4.375rem;

  @media(max-width: 991px) {
    margin-top: 3.75rem;
    font-size: 1.625rem;
    line-height: 1.95rem;
  }
}

.history__text {
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;
  color: $dark;
  margin-top: 2.5rem;

  span {
    font-weight: 700;
  }

  @media(max-width: 991px) {
    margin-top: 1.25rem;
  }

  &:last-child {
    margin-top: 1.25rem;
  }
}

.history__ul {
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  line-height: 1.6rem; 
  
  li {
    color: $dark;
    margin-top: 1.25rem;

    span {
      font-weight: 700;
    }
  
    a {
      color: $blue_1;
  
      &:hover {
        color: $blue_2;
        cursor: pointer;
      }
    }

    &::marker {
      color: $turquoise;
    }
  }
}

.history-products__title {
  font-family: 'Gilroy', sans-serif;
  font-weight: 800;
  font-size: 1.625rem;
  line-height: 1.95rem;
  margin-top: 4.375rem;

  @media(max-width: 991px) {
    margin-top: 3.75rem;
    font-size: 1.25rem;
  line-height: 1.5rem;
  }
}

.history-products__items {
  display: flex;
  justify-content: space-between;
  margin-top: 0.9375rem;

  @media(max-width: 991px) {
    flex-direction: column;
  }
}

.history-products__item {
  max-width: 100%;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &:first-child {
    margin-right: 9%;
  }

  &:nth-child(2) {
    margin-right: 10%;
  }
  
  @media(max-width: 1400px) {
    &:first-child {
      margin-right: 6%;
    }
  
    &:nth-child(2) {
      margin-right: 3%;
    }
  }

  @media(max-width: 991px) {
    margin-top: 0.3125rem;

    &:first-child {
      margin-right: 0;
    }
  
    &:nth-child(2) {
      margin-right: 0;
    }
  }
}

.history-products__img {

  img {
    width: 100%;
    object-fit: cover;
  }
}

.history-products__item:not(:first-child) {

  .history-products__img { 
    padding-top: 1.25rem;
  }

  .history-products__link {
    margin-left: 16.5%;
  }

  @media(max-width: 991px) {
    margin-top: 2.5rem;

    .history-products__img { 
      padding-top: 0;
    }
  
    .history-products__link {
      margin-left: 0;
    }
  }
}

.history-products__link {
  font-family: 'Gilroy', sans-serif;
  font-weight: 800;
  font-size: 1.25rem;
  line-height: 1.5rem;
  margin-top: 1.3125rem;

  a {
    font-family: 'Gilroy', sans-serif;
    font-size: 1.25rem;
    line-height: 1.5rem;
    color: $blue_1;

    &:hover {
      color: $blue_2;
      cursor: pointer;
    }
  }

  @media(max-width: 991px) {
    font-family: 'Inter', sans-serif;
    font-weight: 700;
    font-size: 1.125rem;
    line-height: 1.35rem;

    a {
      font-family: 'Inter', sans-serif;
      font-size: 1.125rem;
      line-height: 1.35rem;
    }
  }
}

.history-cards {
  margin-top: 6.25rem;

  @media(max-width: 991px) {
    margin-top: 5rem;
  }
}

.history-cards__items {
  display: flex;
  flex-direction: column;
}

.history-cards__item {
  display: flex;
  background: $card-bg;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;
  overflow: hidden;

  @media(min-width: 992px) {
    height: 29.9375rem;
  }

  @media(max-width: 991px) {
    flex-direction: column;
  }
}

.history-cards__item:not(:first-child) {
  margin-top: 4.375rem;

  @media(max-width: 991px) {
    margin-top: 2.5rem;
  }
}

.history-cards__img {
  width: 48%;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @media(max-width: 1199px) {
    width: 50%;
  }

  @media(max-width: 991px) {
    width: 100%;
    height: 39%;
  }
}

.history-cards__desk {
  width: 52%;
  padding: 5.8125rem 19% 5.8125rem 6.5625rem;

  @media(max-width: 1399px) {
    width: 50%;
    padding: 5.8125rem 5.9375rem 5.8125rem 4.0625rem;
  }

  @media(max-width: 991px) {
    width: 100%;
    height: 61%;
  }

  @media(max-width: 767px) {
    padding: 0.9375rem 1.25rem 2.5rem;
  }
}

.history-cards__title {
  font-family: 'Gilroy', sans-serif;
  font-weight: 800;
  font-size: 1.875rem;
  line-height: 2.25rem;

  @media(max-width: 568px) {
    font-size: 1.25rem;
    line-height: 1.5rem;
  }
}

.history-cards__text {
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  line-height: 1.6rem; 
  color: $turquoise;
  margin-top: 1.875rem;

  @media(max-width: 991px) {
    margin-top: 0.625rem;
  }

  @media(max-width: 568px) {
    font-size: 0.8125rem;
    line-height: 1rem;
  }
}


.history-cards__button {
  display: inline-block;
  padding: 0.5625rem 0.9375rem;
  border: 0.0625rem solid $blue_1;
  border-radius: 2.5rem;
  transition: all 0.3s ease;
  color: $blue_1;
  font-family: 'Inter', sans-serif;
  font-weight: 600;
  font-size: 0.8125rem;
  line-height: 1rem;
  margin-top: 3.125rem;

  svg {
    stroke: $blue_1;
    transform: rotate(180deg);
    width: 0.75rem;
    height: 0.875rem;
    display: inline-block;
  }

  &:hover{
    border-color: $blue_2 !important;
    color: $blue_2;

    svg{
      stroke:$blue_2;
    }
  }

  @media(max-width: 991px) {
    margin-top: 1.25rem;
  }
}

.history-cards__link {
  display: block;
  font-family: 'Inter', sans-serif;
  font-size: 0.8125rem;
  line-height: 1rem;
  color: $blue_1;
  margin-top: 1.375rem;

  &:hover{
    color: $blue_2;
  }
}

.history-swiper__title {
  font-family: 'Gilroy', sans-serif;
  font-weight: 800;
  font-size: 2.8125rem;
  line-height: 3.0938rem;
  margin-top: 7.5rem;

  @media(max-width: 991px) {
    font-size: 1.625rem;
    line-height: 1.95rem;
    margin-top: 5rem;
  }
}

.history-swiper__subtitle {
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;
  margin-top: 2.5rem;
  max-width: 45.625rem;

  @media(max-width: 991px) {
       margin-top: 1.25rem;
  }
}
// slider

.js-history-swiper__wrap {
  position: relative;
}

.history-swiper {
  position: relative;

  &:after{
    content: "";
    position: absolute;
    bottom: 3.5625rem;
    left: 0;
    width: 100%;
    height: 0.25rem;
    border-bottom: 0.0625rem dashed $cyan;
    z-index: $lvl_-1;
  }
}

.history-swiper__slider {
  margin-top: 3.75rem;
  overflow: hidden;
  position: relative;

  .rounded-pagination {
    margin-top: 5.625rem;
    bottom: 2.5rem;
    padding: 0 10px;
  }

  .rounded-pagination .swiper-pagination-bullet {
    margin: auto;

    &:after{
      position: absolute;
      font-family: 'Inter', sans-serif;
      font-weight: 700;
      font-size: 1.125rem;
      line-height: 1.35rem;
      top: 2.5rem;      
      color: $cyan;
      left: -0.9375rem;
      width: 3.75rem;
      text-align: center;

      @media(max-width: 767px) {
        font-size: 1rem;
        line-height: 1.6rem;
      }
    }

    &:nth-child(1):after{
      content: "1994";      
    }

    &:nth-child(2):after{
      content: "1997";      
    }

    &:nth-child(3):after{
      content: "1998";      
    }

    &:nth-child(4):after{
      content: "2001";      
    }

    &:nth-child(5):after{
      content: "2003";      
    }

    &:nth-child(6):after{
      content: "2004";      
    }

    &:nth-child(7):after{
      content: "2005";      
    }

    &:nth-child(8):after{
      content: "2000";      
    }

    &:nth-child(9):after{
      content: "2000";      
    }

    &:nth-child(10):after{
      content: "2000";      
    }

    &:nth-child(11):after{
      content: "2000";      
    }

    &:nth-child(12):after{
      content: "2000";      
    }

    &:nth-child(13):after{
      content: "2000";      
    }

    &:nth-child(14):after{
      content: "2000";      
    }

    &:nth-child(15):after{
      content: "2000";      
    }

    &.swiper-pagination-bullet-active {
      background-color: $white;
    }

    &:before {
      border-color: $cyan;
      background-color: $cyan;
    }

    &.swiper-pagination-bullet-active:after {
      color: $blue_1;
    }

    &.swiper-pagination-bullet-active:before {
      border-color: $blue_1;
      background-color: $blue_1;
    }
    

    @media(max-width: 767px) {
      display: none;

      &.swiper-pagination-bullet-active,
      &._previousBullet,
      &._nextBullet,
      &._visible {
        display: block;
      }
    }
  }

  @media(max-width: 991px) {
    margin-top: 3.125rem;
  }
}

.history-swiper__item {
  width: 100%;
  height: 15.5rem;
  display: flex;
  justify-content: flex-start;

  @media(max-width: 1199px) {
    height: 100%;
  }

  @media(max-width: 767px) {
    flex-direction: column;
  }
}

.history-swiper__image {
  width: 15.9375rem;
  overflow: hidden;
  border-radius: 0.75rem;
  
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    

  }

  @media(max-width: 767px) {
    width: 100%;
  }

  @media(max-width: 420px) {
    height: 9.375rem;
  }
}

.history-swiper__content {
  padding: 0 1.75rem;
  width: 72%;

  @media(max-width: 1399px) {
    width: 60%;
  }

  @media(max-width: 767px) {
    width: 100%;
    margin-top: 1.25rem;
    padding: 0;
  }
}

.history-swiper__content-title {
  font-family: 'Gilroy', sans-serif;
  font-weight: 800;
  font-size: 1.875rem;
  line-height: 2.25rem;

  @media(max-width: 991px) {
    font-size: 1.25rem;
    line-height: 1.5rem;
  }
}

.history-swiper__content-text {
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;
  margin-top: 1.25rem;
  color: $turquoise;

  @media(max-width: 991px) {
       margin-top: 0.625rem;
  }
}


.history-swiper__button-container {
  width: 7.8125rem;
  height: 5rem;
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  justify-content: space-between;

  .swiper-button {
    position: static;
    width: 3.125rem;
    height: 5rem;
    background-color: rgba(239, 249, 255, 0.8) !important;
    border-radius: 0.375rem;
    margin: 0;
    top: 0;
    transition: all 0.3s ease;

    svg {
      fill: $blue_1;
      width: 0.7rem;
    }

    &:hover {
      background-color: transparent !important;
    }
  }

  .swiper-button-prev {
    transform: rotate(180deg);
  }

  @media(min-width: 992px) {
    height: 75%;
    background: $white;
    z-index: $lvl_1;
    top: -1px;
    right: -1px;
  }
}
