.search-form {
    width: 600px;
    margin-top: 50px;

    @media (max-width: 767px) {
        width: 100%;
    }

    &__form-group {
        position: relative;
        display: flex;
        align-items: center;
    }

    &__form-control {
        padding: 10px 110px 10px 20px;
    
        &::placeholder {
            opacity: 0;
        }

        &:focus,
        &:not(:placeholder-shown) {
            + span {
                font-size: 12px;
                top: -10px;
                width: 100%;
            }
        }
    }

    &__fake-placeholder {
        position: absolute;
        top: 50%;
        left: 20px;
        transform: translateY(-50%);
        width: 300px;
        color: $turquoise_2;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        pointer-events: none;
        transition: all 0.3s;

        @media (max-width: 420px) {
            width: 230px;
            transition: all 0.05s;
        }

        @media (max-width: 350px) {
            width: 200px;
        }
    }

    &__btn {
        position: absolute;
        right: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100px;
        height: 100%;
        border: none;
        background-color: $blue_1;
        border-radius: 40px;
        box-shadow: 0 4px 15px rgba(0, 88, 161, 0.28);
        transition: background-color 0.3s, box-shadow 0.3s;

        @media (max-width: 500px) {
            width: 65px;
        }

        &:hover {
            background-color: $blue_2;
            box-shadow: none;
        }

        svg {
            width: 16px;
            height: 16px;
            stroke: #fff;
        }
    }
}
