.calc_page_top {                              /* для какого элемента класс!? */
  margin-top: 2.5rem;
  margin-bottom: 0;
}

.calc_metodical {                              /* для какого элемента класс!? */
  display: inline-block;
  font-size: 1.125rem;
  line-height: 1.9375rem;
  font-family: "Inter", sans-serif;
}

.calculated__container {
  margin-bottom: 4.375rem;

  .lead{
    display: block;
    color: $dark;
    font-weight: 400;
  }
}

.calculated_section {                             /* для какого элемента класс!? */
  background-color: rgba(239, 249, 255, 0.8);
  border-radius: 1.25rem;
  padding: 3.75rem 5.3125rem 4.375rem 3.75rem;

}

.calculated__blocks {
  padding: 3.75rem 5.3125rem 4.375rem 3.75rem;
  background-color: rgba(239, 249, 255, 0.8);
  border-radius: 1.25rem;
  margin-top: 2.5rem;

  h4 {
    display: inline-block;
    margin-bottom: 1.875rem;
    margin-top: 0;
    font-size: 1.625rem;
    line-height: 1.95rem;

    @media(max-width: 568px) {
      font-size: 1.125rem;
      line-height: 1.35rem;
      font-weight: 700;
    }
  }

  .form-control {
    min-height: 2.375rem;
    min-width: 5rem;
    line-height: 0;
  }

  @media(max-width: 768px) {
    padding: 0.9375rem 0.9375rem 2.8125rem 0.9375rem;
  }
}

.calculated__blocks-title{
  color: $black;
  font-size: 1.625rem;
  line-height: 1.9375rem;
  font-family: "Gilroy", sans-serif;
  font-weight: 800;
  margin-bottom: 1.875rem;

  @media(max-width: 768px){
    font-size: 1.125rem;
    line-height: 1.35rem;
    font-weight: 700;
    margin-bottom: 1.25rem;
  }
}

.calculated__blocks-num{
  font-family: 'Gilroy', sans-serif;
  font-size: 1.625rem;
  line-height: 1.95rem;
  color: $turquoise;
  font-weight: 800;
  display: inline-block;
  margin-right: 1.25rem;


  @media(max-width: 768px) {
    margin-right: 0.375rem;
    font-size: 1.125rem;
    line-height: 1.35rem;
    font-weight: 700;
  }
}

.input-row {
  display: flex;
  align-items: center;

  .form-group {
    max-width: 7.5rem;
    margin-right: 3.125rem;
  }

  &:not(:last-child) {
    margin-bottom: 1.25rem;
  }

  .form-control::-moz-placeholder { color: $turquoise_2; }
  .form-control::-webkit-input-placeholder { color: $turquoise_2; }

  @media(max-width: 768px) {
    .form-group {
      max-width: 5rem;
      margin-right: 1.25rem;
    }
  }
}

.input-row__description {
  font-size: 0.8125rem;
  line-height: 0.9812rem;
  font-weight: 400;
  font-family: "Inter", sans-serif;
}

.calculated__blocks-item {
  position: relative;

  &:not(:last-child) {
    margin-bottom: 3.75rem;
  }
}

.calculated__select {
  max-width: 40.625rem;
  width: 100%;
  background-color: white;
  border-radius: 1.25rem;

  .choices{
    .choices__inner{
      min-height: 2.375rem;
    }
    &:after{
      border-color: $blue_1 transparent transparent;
    }
    &.is-open{
      &:after{
        border-color: transparent transparent $blue_1;
      }
    }
  }
}

.calculated-hint {
  padding: 1.875rem;
  border-radius: 1rem;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  background-color: white;
  max-width: 22.5rem;
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;

  @media(max-width: 1679px) {
    top: 2.5rem;
  }
  @media(max-width: 991px) {
    position: static;
    max-width: 100%;
    margin-top: 1.25rem;
  }
}

.calculated-hint__title {
  color: $dark;
  font-size: 0.75rem;
  line-height: 0.9375rem;
  font-weight: 700;
  display: inline-block;
  margin-bottom: 0.9375rem;
  font-family: 'Inter', sans-serif;
}

.calculated-hint__item {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &:not(:last-child) {
    margin-bottom: 0.625rem;
  }
}

.calculated-hint__name {
  color: $turquoise;
  font-size: 0.6875rem;
  line-height: 0.8125rem;
  font-family: 'Inter', sans-serif;
}

.calculated-hint__value {
  font-size: 0.6875rem;
  line-height: 0.8125rem;
  font-family: 'Inter', sans-serif;
  color: $dark;
  display: inline-block;
  min-width: 2.625rem;
  margin-left: 0.625rem;
}

.calculated__button {
  margin-top: 1.875rem;
  font-size: 1.125rem;
  padding: 14px 30px;
}

.alert-notification {
  margin-bottom: 2.5rem;
  background-color: #FAEAE6;
  padding: 0.9375rem;
  border-radius: 1rem;
  display: flex;
  justify-content: space-between;

  svg {
    stroke: $red;
    width: 1.5rem;
    height: 1.5rem;
    display: inline-block;
    margin-right: 0.625rem;
  }

  span {
    font-size: 1rem;
    line-height: 1.6rem;
    font-weight: 400;
    color: $red;
    font-family: 'Inter', sans-serif;
  }

  &__message {
    margin-right: 0.9375rem;
  }

  &__close {
    display: inline-block;
    width: 0.75rem;
    height: 0.75rem;
    cursor: pointer;
    transition: all 0.3s ease;

    svg {
      stroke: none;
      fill: black;
    }

    &:hover {
      svg {
        fill: $red;
        stroke: $red;
      }
    }
  }
}

.result-table {
  padding-bottom: 1.875rem;
  background: url('./images/calc_page/calc-result-background.png') no-repeat;
  background-size: cover;

  &_no-bg {
    background: none;
  }

  h2 {
    margin-top: 0;
  }
}

.result-table__container {
  padding: 3.75rem 0 4.125rem 3.75rem;
  background: $white;
  box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
  border-radius: 1.25rem;

  .h2 {
    display: block;
    margin-bottom: 1.75rem;
  }

  @media(max-width: 768px) {
    padding: 2.3125rem 1.25rem;
  }
}

.result-table__items {
  max-width: 95%;

  @media(max-width: 768px) {
    max-width: 100%;
  }
}

.result-table__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;

  &:not(:last-child) {
    margin-bottom: 0.3125rem;
  }

  &:before {
    content: "";
    width: 100%;
    height: 0.0625rem;
    border-bottom: 0.0625rem dashed $turquoise;
    position: absolute;
    bottom: 0.6875rem;
    z-index: $lvl_0;
  }

  @media(max-width: 768px) {
    &:before {
      display: none;
    }
  }
}

.result-table__name {
  color: $turquoise;
  font-size: 1rem;
  line-height: 1.6rem;
  font-family: 'Inter', sans-serif;
  display: flex;
  padding: 0.3125rem 0;
  background-color: white;
  z-index: $lvl_1;

  @media(max-width: 768px) {
    font-size: 0.8125rem;
    line-height: 0.9812rem;
    display: contents;
  }
}

.result-table__value {
  color: $dark;
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;
  font-weight: 700;
  display: inline-block;
  padding: 0.3125rem 0;
  background-color: white;
  z-index: $lvl_1;

  @media(max-width: 768px) {
    font-size: 0.8125rem;
    line-height: 0.9812rem;
    display: contents;
  }
}
