.soft-section-direction {
  display: flex;
  flex-direction: column;
  align-items: center;

  h2 {
    margin-bottom: 0 !important;
    margin-left: auto;
    margin-right: auto;
  }

  @media(max-width: 1199px) {
    align-items: flex-start;
  }

}

.index-soft {
  position: relative;
  background: url("./images/index/index-soft-background.jpg") center center no-repeat;
  background-size: cover;
  padding: 6.875rem 0 7.6875rem 0;

  .h2 {
    display: block;
    color: white;
    margin-bottom: 0.375rem;
    z-index: $lvl_1;
    margin-top: 0;

    @media(max-width: 1399px) {
      margin-bottom: 8rem;
    }

    @media(max-width: 991px) {
      margin-bottom: 1.1875rem;
      font-weight: 800;
      font-size: 1.625rem;
      line-height: 1.9375rem;
    }
  }

  &:before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), linear-gradient(180deg, rgba(0, 88, 161, 0.2) 27.17%, rgba(0, 88, 161, 0) 56.21%), linear-gradient(0deg, rgba(0, 88, 161, 0.5), rgba(0, 88, 161, 0.5));
  }

  @media(max-width: 991px) {
    padding: 2.6875rem 0 3.875rem 0;
  }
  @media(max-width: 991px) {
    margin-bottom: 2.5rem;
  }
}

.index-soft__container {
  position: relative;
  z-index: $lvl_1;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: start;
  max-width: 69.0625rem;
  flex-wrap: wrap;
  margin-top: 3rem;

  @media(max-width: 991px){
    width: 100%;
    grid-template-columns: repeat(2, 1fr);
  }

  @media(max-width: 767px) {
    display: block;
    margin-top: 1.1875rem;
  }
}

.index-soft__item {
  display: flex;
  align-items: center;
  margin: 0 3.875rem 2.6875rem 0;
  cursor: pointer;

  svg {
    stroke: $white;
    fill: $white;
    max-width: 2.5625rem;
    max-height: 2.25rem;
  }

  &:hover{
    .index_soft__text{
      text-decoration: underline;
      transition: all 0.3s ease;
    }
  }

  @media(max-width: 1199px) {
    margin-right: 0;
  }
  @media(max-width: 991px) {

    &:not(:last-child) {
      margin-bottom: 1.5625rem;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.index-soft__text {
  color: $white;
  font-size: 1rem;
  line-height: 1.2rem;
  font-family: 'Inter', sans-serif;
  margin-left: 0.875rem;
  transition: all 0.3s ease;
  max-width: 16.875rem;

  &:hover {
    text-decoration: underline;
    text-underline-offset: 0.25rem;
  }

  @media(max-width: 568px) {
    font-size: 0.8125rem;
    line-height: 0.9812rem;
  }
}