

/* wrapper */
.yalb_wrapper {
  position:fixed;
  width:100%;
  height:100%;
  top:0;
  background:rgba(0,0,0,0.5);
  z-index:10000;
  opacity:1;
  transition:opacity 0.4s;
  /* Comment in to disable yalb-closing by clicking on the wrapper */
  /* pointer-events:none; */
}

/* container */
.yalb {
  margin:0 auto 0;
  border:0.3125rem solid $white;
  border:0.3125rem solid rgba(255, 255, 255, 0.8);
  box-shadow:0 0 0.3125rem rgba(0, 0, 0, 0.8);
  width:20.3125rem;
  height:2.3125rem;
  position:relative;
  bottom:-35%;
  background:$white;
  border-radius:0.3125rem;
  overflow:hidden;
  font-family:Arial;
  transition:width 0.4s, height 0.4s, bottom 0.4s;
  max-width: 80vw;
  cursor: grab;
  /* Comment in to disable yalb-closing by clicking on the wrapper */
  /* pointer-events:auto; */
}

/* prev / next / close */
.yalb span.prev, .yalb span.next, .yalb span.close {
  text-decoration:none;
  color: $black;
  line-height:100%;
  display:block;
  cursor:pointer;
}

.yalb span.prev, .yalb span.next {
  font-size:2.1875rem;
  z-index:3;
  position:relative;
  bottom:-20%;
  margin-top:-1.0625rem;
  top:20%;
}

.yalb span.prev,.yalb span.next{
  position: fixed;
  top: 50%;
  width: 3.75rem;
  height: 3.75rem;
  border-radius:0 0.3125rem 0.3125rem 0;
  background: rgba(255,255,255,0.3);
  &:before{
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 3.125rem;
    height: 3.125rem;
    background: url("data:image/svg+xml,%3C%3Fxml version='1.0' %3F%3E%3C!DOCTYPE svg PUBLIC '-//W3C//DTD SVG 1.1//EN' 'http://www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd'%3E%3Csvg height='50px' id='Layer_1' style='enable-background:new 0 0 128 128;' version='1.1' viewBox='0 0 128 128' width='50px' xml:space='preserve' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg%3E%3Cline style='fill:none;stroke:%232F3435;stroke-width:12;stroke-linecap:square;stroke-miterlimit:10;' x1='40.5' x2='87.5' y1='17' y2='64'/%3E%3Cline style='fill:none;stroke:%232F3435;stroke-width:12;stroke-linecap:square;stroke-miterlimit:10;' x1='87.5' x2='40.5' y1='64' y2='111'/%3E%3C/g%3E%3C/svg%3E");
  }
}

.yalb span.prev {
  left: 1.25rem;
  transform: rotate(180deg);
}

.yalb span.next {
  right: 1.25rem;
}






.yalb span.close {
  background:white;
  z-index:4;
  position:absolute;
  border-radius:0 0 0 0.3125rem;
  padding:0.625rem;
  top:0;
  right:0;
  font-size:1.25rem;
  opacity:0;
}

.yalb:hover span.close, .yalb:hover span.prev, .yalb:hover span.next {
  opacity:1;
}

.yalb span.close:before {
  content:'\2716';
}

/* error-msg */
.yalb strong.error {
  position:absolute;
  width:100%;
  display:block;
  z-index:1;
  top:20%;
  text-align:center;
  margin-top:-0.125rem;
  box-sizing:border-box;
  padding:0 3rem;
  opacity:1;
  transition:opacity 0.4s;
}

.yalb strong.error:before {
  content:'Image could not be loaded';
}

/* image */
.yalb img.image {
  height:100%;
  width:100%;
  display:block;
  z-index:1;
  top:0;
  left:0;
  opacity:1;
  transition:opacity 0.4s;
}

/* loader-icon */
.yalb span.loader {
  position:absolute;
  margin:0 auto;
  z-index:2;
  bottom:50%;
  margin-bottom:-0.875rem;
  left:50%;
  margin-left:-0.625rem;
  display:block;
  border:0.1875rem solid rgba(200,200,200,0.8);
  animation: loading 1.1s infinite linear;
  height:1.25rem;
  width:1.25rem;
  border-radius:50%;
}

@keyframes loading {
  50% {
    height:0.625rem;
    width:0.625rem;
    margin-bottom:-0.4375rem;
    margin-left:-0.3125rem;
  }
}

.yalb_wrapper.fadeOut, .yalb_wrapper .fadeOut{
  opacity:0 !important;
}

.yalb .hide{
  display:none !important;
}