.guarantee__wrap {
    margin-top: 2.5rem;
    display: flex;
    justify-content: space-between;
  
    @media(max-width: 991px) {
      flex-direction: column;
    }
  
    @media(max-width: 568px) {
      margin-top: 1.25rem;
    }
}

.guarantee__subtitle {
    font-family: "Inter", sans-serif;
    font-size: 1.125rem;
    line-height: 2rem;
    color: $dark;
    width: 65%;

    @media(max-width: 991px) {
        width: 100%;
    }

    @media(max-width: 568px) {
        font-size: 1rem;
        line-height: 1.6rem;
    }
}
  
.guarantee__card {
    padding: 1.875rem;
    background: $card-bg;
    box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
    border-radius: 1.25rem;
    position: relative;
    height: fit-content;
    width: 31.5%;

    &:after {
        content: '';
        position: absolute;
        background-image: url("images/catalog/catalog-info-pattern.svg");
        border-radius: 1.25rem;
        width: 100%;
        height: 100%;
        z-index: $lvl_-1;
        bottom: -0.9375rem;
        right: -0.9375rem;

        @media (max-width: 500px) {
            bottom: -0.8rem;
            right: -0.4rem;
            background-size: cover;
            background-image: url("images/catalog/catalog-info-pattern-mobile.svg");
        }
    }

    .title {
        font-family: 'Gilroy', sans-serif;
        font-weight: 800;
        font-size: 1.625rem;
        line-height: 1.95rem;
        color: $dark;
        margin-bottom: 0.9375rem;

        @media(max-width: 991px) {
            font-weight: 700;
            font-size: 1rem;
            line-height: 1.6rem;
        }
    }

    a,
    .text {
        font-family: "Inter", sans-serif;
        font-size: 0.8125rem;
        line-height: 1rem;
        margin-top: 0.3125rem;
    }

    a {
        color: $blue_1;

        &:hover {
            border-color: $blue_2;
            cursor: pointer;
        } 
    }

    @media(min-width: 1680px) {
        width: 25%;
    }

    @media(max-width: 991px) {
        width: 100%;
        margin-top: 2.5rem;
    }

    @media(max-width: 568px) {
        padding: 1.5625rem 1.75rem 2rem 1.5625rem;
        margin-top: 1.25rem;

        .title {
            font-weight: 700;
            font-size: 1rem;
            line-height: 1.6rem;
            margin-bottom: 0.9375rem;
        }
    }
}

.guarantee__info {
    width: 65%;
    margin-bottom: 2.5rem;
    margin-top: -1.25rem;

    .title {
        font-family: 'Gilroy', sans-serif;
        font-weight: 800;
        font-size: 2.8125rem;
        line-height: 3.0938rem;
        color: $dark;

        @media(max-width: 991px) {
            font-size: 1.625rem;
            line-height: 1.95rem;
        }
    }

    .text {
        margin-top: 2.5rem;
        font-family: "Inter", sans-serif;
        font-size: 1rem;
        line-height: 1.6rem;
        color: $dark;
        
        span {
            color: $turquoise;
        }
    
        &:not(:first-child) {
            margin-top: 1.25rem;
        }
    }

    .subtitle {
        font-family: 'Gilroy', sans-serif;
        font-weight: 800;
        font-size: 1.875rem;
        line-height: 2.25rem;
        color: $dark;
        margin: 2.5rem 0 1.5625rem;

        @media(max-width: 991px) {
            font-size: 1.25rem;
            line-height: 1.625rem;
        }
    }

    @media(max-width: 1199px) {
        margin-top: -5rem;
    }

    @media(max-width: 991px) {
        width: 100%;
        margin-top: 3.125rem;
    }
}

.guarantee__info-box {
    margin-top: 1.25rem;
    display: flex;

    img {
        width: 2.1875rem;
        height: 1.75rem;
        margin-right: 15px;
    }

    .desk{

        .title {
            font-family: 'Gilroy', sans-serif;
            font-weight: 800;
            font-size: 1.25rem;
            line-height: 1.5rem;
            color: $dark;

            @media(max-width: 991px) {
                font-weight: 700;
                font-size: 1rem;
                line-height: 1.6rem;
            }
        }

        .text {
            margin-top: 0.3125rem;
        }
    }
}
