.calc__section {
  position: relative;

  .h1 {
    display: block;
    @media(max-width: 1199px) {
      font-size: 1.875rem;
      line-height: 2.25rem;
    }
  }

  @media(max-width: 1399px) {
    padding-bottom: 7.9375rem;
  }
  @media(max-width: 1199px) {
    padding-bottom: 5.75rem;
  }
}

.calc__links {
  .h3{
    display: block;
    margin-top: 0;
  }
}

.calc__links-container{
  margin-bottom: 1.875rem;
  @media(max-width: 1199px){
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  @media(max-width: 568px){
    flex-direction: column;
    align-items: flex-start;
  }
}

.calc__links-link {
  display: flex;
  align-items: center;
  &:not(:last-child) {
    margin-bottom: 2.8125rem;
    @media(max-width: 1199px){
      margin-bottom: 0;
    }
    @media(max-width: 568px){
      margin-bottom: 2.8125rem;
    }
  }

  a {
    font-size: 1.125rem;
    line-height: 1.575rem;
    color: $blue_1;
    font-family: "Inter", sans-serif;
    font-weight: 700;
    display: block;
  }

  svg {
    width: 2.4375rem;
    height: 3.5rem;
    stroke: $blue_1;
    display: inline-block;
    margin-right: 1.25rem;
  }

  &:hover {
    cursor: pointer;
    a {
      color: $blue_2;
      text-decoration: underline;
      text-underline-offset: 0.25rem;
    }
    svg {
      stroke: $blue_2;
    }
  }
}

.calc__num-bg {
  position: absolute;
  max-width: 16.125rem;
  bottom: 0;
  left: 2.0625rem;
  z-index: $lvl_-1;

  @media(max-width: 1199px) {
    display: none;
  }
}

.calc__banner {
  display: none;
  flex-direction: column;
  align-items: center;
  position: relative;

  @media(max-width: 1199px) {
    display: flex;
  }
}

.calc__image {
  max-width: 100%;
  height: auto;

  @media(max-width: 1199px) {
    display: none;
  }
}

.calc__title-container {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;

  @media(max-width: 1680px) {
    bottom: 3.3125rem;
    left: 1.625rem;
  }

  @media(max-width: 1399px) {
    bottom: 2.8125rem;
    left: 1.5625rem;
  }

  @media(max-width: 1199px) {
    position: static;
  }

  .h3 {
    display: block;
  }
}

.calc__hand {
  position: absolute;
  width: 100%;
  height: 29.3125rem;
  bottom: 0;
  right: -3rem;
  max-width: 39.875rem;

  @media(max-width: 1679px) {
    height: 24rem;
    bottom: 4.5rem;
    right: -6.6875rem;
  }

  @media(max-width: 991px) {
    bottom: -1.875rem;
    right: -6.6875rem;
  }
  @media(max-width: 767px) {
    width: auto;
    bottom: -6rem;
    right: -10.1875rem;
  }
  @media(max-width: 500px) {
    width: auto;
    bottom: -2.9375rem;
    right: -6.625rem;
    height: 18.75rem;
  }
}

.calc__banner-container {
  position: relative;

  @media(max-width: 1199px) {
    display: flex;
    flex-direction: column-reverse;
  }
}

.calc_banner__box {                    
  max-width: 32rem;
  position: relative;

}

.calc__banner-image {
  max-width: 100%;
  height: auto;
}

.calc__banner-text {
  color: $turquoise;
  margin-bottom: 0;

  @media(max-width: 1199px) {

    margin-top: 0;
    margin-bottom: 0.75rem;
    color: $dark-gray;
    font-weight: 700;
  }

  @media(max-width: 768px) {
    font-size: 1rem;
    line-height: 1.2rem;
  }
}
