.search {
    margin-top: 3.4375rem;
    font-family: 'Inter', sans-serif;
    font-size: 1rem;
    line-height: 1.6rem;
    color: $dark;
  
    .search-form {
        width: 45.625rem;

        @media(max-width: 991px) {
            width: 100%;
        }
    }
}

.search-result {
    margin: 3.4375rem 0 2.5rem;
    width: 56%;

    @media(max-width: 991px) {
        width: 100%;
    }
}

.search-result__item {
    margin-bottom: 1.875rem;

    .title {
        font-weight: 700;
        color: $blue_1;

        &:hover {
            border-color: $blue_2;
        }
    }

    .desk {
        margin-top: 0.3125rem;

        span {
            font-weight: 700;
        }
    }

    .date {
        margin-top: 0.625rem;
        font-size: 0.6875rem;
        line-height: 0.8125rem;
        color: $turquoise_2;
    }
}

.search-result__item-null {
    
    .title {
        font-family: "Gilroy", sans-serif;
        font-weight: 800;
        font-size: 1.25rem;
        line-height: 1.5rem;
    }

    .desk {
        margin-top: 0.625rem;
    }
} 

.js-search-block {
    position: relative;
}

.search-modal {   
    position: absolute;
	left: 52.4375rem;
	top: 7.1875rem;
	background: $card-bg;
	box-shadow: 0.625rem 0.625rem 1.25rem rgba(0, 0, 0, 0.1);
	padding: 1.25rem 1.25rem 1.25rem 2.5rem;
	border-radius: 0.75rem;
    width: 48.625rem;
	display: none;
	flex-direction: column;
	z-index: $lvl_3;
    max-height: 27.5rem;
    min-width: 15.625rem;

    &.active {
        display: flex;
    }

    .js-smooth-scrollbar {
        padding-right: 0.625rem;
    }

    @media(max-width: 991px) {
        padding: 1.25rem 0.9375rem 1.25rem 1.25rem;
    }

    @media(max-width: 568px) {
        left: 0.9375rem;
    }

    @media(max-height: 560px) {
        height: calc(100vh - 120px);
    }
}

header.__fixed {
    .search-modal {

        @media(min-width: 1200px) {
            top: 4.0625rem;

            @media(max-height: 560px) {
                height: calc(100vh - 70px);;
            }
        }
    }   
}


.search-modal__title {
    font-weight: 700;
    font-size: 0.8125rem;
    line-height: 1rem;

    &:not(:first-child) {
        margin-top: 2.375rem;
    }
}

.search-modal__products {
    margin-top: 0.9375rem;
}

.search-modal__products-item {
    display: flex;
    align-items: center;

    &:not(:first-child) {
        margin-top: 1.5625rem;
    }
}

.search-modal__products-img {
    width: 3.75rem;
    height: 3.75rem;
    margin-right: 2.5rem;
    

    img {
        object-fit: cover;
        max-width: 100%;
        max-height: 100%;
    }

    @media(max-width: 991px) {
        margin-right: 0.9375rem;
    }
}

.search-modal__products-desc {

}

.search-modal__products-title {
    font-weight: 700;
    font-size: 1rem;
    line-height: 1.2rem;
}

.search-modal__products-subtitle {
    font-size: 0.6875rem;
    line-height: 0.8125rem;
    margin-top: 0.125rem;
    color: $dark;
}

.search-modal__products-info {
    margin-top: 0.125rem;
    
    .text {
        color: $turquoise;
        font-size: 0.6875rem;
        line-height: 0.8125rem;
        margin-right: 1.25rem;
        display: inline-block;
    }

    @media(max-width: 991px) {
        display: none;
    }
}
 
.search-modal__products-tags {

    .text {
        background: #E9EFF2;
        border-radius: 0.375rem;
        margin-right: 0.3125rem;
        padding: 0.0938rem 0.625rem;
        font-size: 0.6875rem;
        line-height: 0.8125rem;
        display: inline-block;
        color: $dark;
    }

    @media(max-width: 991px) {
        display: none;
    }
}

.search-modal__docs {
    display: flex;
    flex-direction: column;
    max-width: 27.0625rem;
    margin-top: 0.9375rem;
}
.search-modal__doc {
    font-size: 0.8125rem;
    line-height: 1rem;
    color: $blue_1;

    &:not(:first-child) {
        margin-top: 0.9375rem;
    }
}

.search-modal__all-results {
    font-weight: 700;
    font-size: 0.8125rem;
    line-height: 1rem;
    color: $blue_1;
    margin-top: 1.875rem;
    display: block;
}