
@mixin custom-scroll {
	&::-webkit-scrollbar {
		width: 0.5rem;
	}

	&::-webkit-scrollbar-track {
		margin: 0.625rem 0;
		background: linear-gradient(to right, transparent 0.1875rem, #F1F1F1 0.1875rem, #F1F1F1 0.3125rem, transparent 0.3125rem);
		border-radius: 0.125rem;
	}

	&::-webkit-scrollbar-thumb {
		background-color: $secondary;
		border-radius: 3.125rem;
	}
}

@mixin box-shadow {
	box-shadow: 0 0.375rem 1.25rem rgba(0, 0, 0, .2);
}
